  export default "import React from 'react';\nimport type { CascaderProps } from 'antd';\nimport { Cascader } from 'antd';\n\ninterface Option {\n  value: string | number;\n  label: string;\n  children?: Option[];\n  disableCheckbox?: boolean;\n}\n\nconst options: Option[] = [\n  {\n    label: 'Light',\n    value: 'light',\n    children: new Array(20)\n      .fill(null)\n      .map((_, index) => ({ label: `Number ${index}`, value: index })),\n  },\n  {\n    label: 'Bamboo',\n    value: 'bamboo',\n    children: [\n      {\n        label: 'Little',\n        value: 'little',\n        children: [\n          {\n            label: 'Toy Fish',\n            value: 'fish',\n            disableCheckbox: true,\n          },\n          {\n            label: 'Toy Cards',\n            value: 'cards',\n          },\n          {\n            label: 'Toy Bird',\n            value: 'bird',\n          },\n        ],\n      },\n    ],\n  },\n];\n\nconst onChange: CascaderProps<Option, 'value', true>['onChange'] = (value) => {\n  console.log(value);\n};\n\nconst App: React.FC = () => (\n  <Cascader\n    style={{ width: '100%' }}\n    options={options}\n    onChange={onChange}\n    multiple\n    maxTagCount=\"responsive\"\n  />\n);\n\nexport default App;\n";;
  