
  import '/Users/vagusx/Projects/ant-design/components/table/demo/virtual-list.tsx?watch=parent';
  export default "import React from 'react';\nimport { Button, Segmented, Space, Switch, Table, Typography } from 'antd';\nimport type { TableProps } from 'antd';\n\ninterface RecordType {\n  id: number;\n  firstName: string;\n  lastName: string;\n  age: number;\n  address1: string;\n  address2: string;\n  address3: string;\n}\n\nconst fixedColumns: TableProps<RecordType>['columns'] = [\n  {\n    title: 'ID',\n    dataIndex: 'id',\n    width: 100,\n    fixed: 'left',\n  },\n  {\n    title: 'FistName',\n    dataIndex: 'firstName',\n    width: 120,\n    fixed: 'left',\n  },\n  {\n    title: 'LastName',\n    dataIndex: 'lastName',\n    width: 120,\n    fixed: 'left',\n  },\n  {\n    title: 'Group',\n    width: 120,\n    render: (_, record) => `Group ${Math.floor(record.id / 4)}`,\n    onCell: (record) => ({\n      rowSpan: record.id % 4 === 0 ? 4 : 0,\n    }),\n  },\n  {\n    title: 'Age',\n    dataIndex: 'age',\n    width: 100,\n    onCell: (record) => ({\n      colSpan: record.id % 4 === 0 ? 2 : 1,\n    }),\n  },\n  {\n    title: 'Address 1',\n    dataIndex: 'address1',\n    onCell: (record) => ({\n      colSpan: record.id % 4 === 0 ? 0 : 1,\n    }),\n  },\n  {\n    title: 'Address 2',\n    dataIndex: 'address2',\n  },\n  {\n    title: 'Address 3',\n    dataIndex: 'address3',\n  },\n  {\n    title: 'Action',\n    width: 150,\n    fixed: 'right',\n    render: () => (\n      <Space>\n        <Typography.Link>Action1</Typography.Link>\n        <Typography.Link>Action2</Typography.Link>\n      </Space>\n    ),\n  },\n];\n\nconst columns: TableProps<RecordType>['columns'] = [\n  {\n    title: 'ID',\n    dataIndex: 'id',\n    width: 100,\n  },\n  {\n    title: 'FistName',\n    dataIndex: 'firstName',\n    width: 120,\n  },\n  {\n    title: 'LastName',\n    dataIndex: 'lastName',\n    width: 120,\n  },\n];\n\nconst getData = (count: number) => {\n  const data: RecordType[] = new Array(count).fill(null).map((_, index) => ({\n    id: index,\n    firstName: `First_${index.toString(16)}`,\n    lastName: `Last_${index.toString(16)}`,\n    age: 25 + (index % 10),\n    address1: `New York No. ${index} Lake Park`,\n    address2: `London No. ${index} Lake Park`,\n    address3: `Sydney No. ${index} Lake Park`,\n  }));\n\n  return data;\n};\n\nconst App = () => {\n  const [fixed, setFixed] = React.useState(true);\n  const [bordered, setBordered] = React.useState(true);\n  const [expanded, setExpanded] = React.useState(false);\n  const [empty, setEmpty] = React.useState(false);\n  const [count, setCount] = React.useState(10000);\n\n  const tblRef: Parameters<typeof Table>[0]['ref'] = React.useRef(null);\n  const data = React.useMemo(() => getData(count), [count]);\n\n  const mergedColumns = React.useMemo<typeof fixedColumns>(() => {\n    if (!fixed) {\n      return columns;\n    }\n\n    if (!expanded) {\n      return fixedColumns;\n    }\n\n    return fixedColumns.map((col) => ({ ...col, onCell: undefined }));\n  }, [expanded, fixed]);\n\n  const expandableProps = React.useMemo<TableProps<RecordType>['expandable']>(() => {\n    if (!expanded) {\n      return undefined;\n    }\n\n    return {\n      columnWidth: 48,\n      expandedRowRender: (record) => <p style={{ margin: 0 }}> Expanded {record.address1}</p>,\n      rowExpandable: (record) => record.id % 2 === 0,\n    };\n  }, [expanded]);\n\n  return (\n    <div style={{ padding: 64 }}>\n      <Space direction=\"vertical\" style={{ width: '100%' }}>\n        <Space>\n          <Switch\n            checked={bordered}\n            onChange={() => setBordered(!bordered)}\n            checkedChildren=\"Bordered\"\n            unCheckedChildren=\"Bordered\"\n          />\n          <Switch\n            checked={fixed}\n            onChange={() => setFixed(!fixed)}\n            checkedChildren=\"Fixed\"\n            unCheckedChildren=\"Fixed\"\n          />\n          <Switch\n            checked={expanded}\n            onChange={() => setExpanded(!expanded)}\n            checkedChildren=\"Expandable\"\n            unCheckedChildren=\"Expandable\"\n          />\n          <Switch\n            checked={empty}\n            onChange={() => setEmpty(!empty)}\n            checkedChildren=\"Empty\"\n            unCheckedChildren=\"Empty\"\n          />\n          <Segmented\n            value={count}\n            onChange={(value: number) => setCount(value)}\n            options={[\n              {\n                label: 'None',\n                value: 0,\n              },\n              {\n                label: 'Less',\n                value: 4,\n              },\n              {\n                label: 'Lot',\n                value: 10000,\n              },\n            ]}\n          />\n\n          {data.length >= 999 && (\n            <Button\n              onClick={() => {\n                tblRef.current?.scrollTo({ index: 999 });\n              }}\n            >\n              Scroll To index 999\n            </Button>\n          )}\n        </Space>\n\n        <Table\n          bordered={bordered}\n          virtual\n          columns={mergedColumns}\n          scroll={{ x: 2000, y: 400 }}\n          rowKey=\"id\"\n          dataSource={empty ? [] : data}\n          pagination={false}\n          ref={tblRef}\n          rowSelection={\n            expanded\n              ? undefined\n              : {\n                  type: 'radio',\n                  columnWidth: 48,\n                }\n          }\n          expandable={expandableProps}\n        />\n      </Space>\n    </div>\n  );\n};\n\nexport default App;\n";;
  