
  import '/Users/vagusx/Projects/ant-design/components/color-picker/demo/change-completed.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { App, ColorPicker } from 'antd';\nimport type { ColorPickerProps } from 'antd';\n\nconst Demo = () => {\n  const { message } = App.useApp();\n  const [value, setValue] = useState<ColorPickerProps['value']>('#1677ff');\n  return (\n    <App>\n      <ColorPicker\n        value={value}\n        onChangeComplete={(color) => {\n          setValue(color);\n          message.success(`The selected color is ${color.toHexString()}`);\n        }}\n      />\n    </App>\n  );\n};\n\nexport default Demo;\n";;
  