  export default "import React, { useState } from 'react';\nimport { Space, Switch } from 'antd';\n\nconst style: React.CSSProperties = {\n  width: 150,\n  height: 100,\n  background: 'red',\n};\n\nconst App: React.FC = () => {\n  const [singleCol, setSingleCol] = useState(false);\n\n  return (\n    <>\n      <Switch\n        checked={singleCol}\n        onChange={() => {\n          setSingleCol(!singleCol);\n        }}\n      />\n      <div style={{ boxShadow: '0 0 5px green' }}>\n        <Space style={{ width: singleCol ? 307 : 310, background: 'blue' }} size={[8, 8]} wrap>\n          <div style={style} />\n          <div style={style} />\n          <div style={style} />\n          <div style={style} />\n        </Space>\n      </div>\n    </>\n  );\n};\n\nexport default App;\n";;
  