
  import '/Users/vagusx/Projects/ant-design/components/transfer/demo/advanced.tsx?watch=parent';
  export default "import React, { useEffect, useState } from 'react';\nimport { Button, Transfer } from 'antd';\nimport type { TransferProps } from 'antd';\n\ninterface RecordType {\n  key: string;\n  title: string;\n  description: string;\n  chosen: boolean;\n}\n\nconst App: React.FC = () => {\n  const [mockData, setMockData] = useState<RecordType[]>([]);\n  const [targetKeys, setTargetKeys] = useState<TransferProps['targetKeys']>([]);\n\n  const getMock = () => {\n    const tempTargetKeys = [];\n    const tempMockData = [];\n    for (let i = 0; i < 20; i++) {\n      const data = {\n        key: i.toString(),\n        title: `content${i + 1}`,\n        description: `description of content${i + 1}`,\n        chosen: i % 2 === 0,\n      };\n      if (data.chosen) {\n        tempTargetKeys.push(data.key);\n      }\n      tempMockData.push(data);\n    }\n    setMockData(tempMockData);\n    setTargetKeys(tempTargetKeys);\n  };\n\n  useEffect(() => {\n    getMock();\n  }, []);\n\n  const handleChange: TransferProps['onChange'] = (newTargetKeys) => {\n    setTargetKeys(newTargetKeys);\n  };\n\n  const renderFooter: TransferProps['footer'] = (_, info) => {\n    if (info?.direction === 'left') {\n      return (\n        <Button size=\"small\" style={{ float: 'left', margin: 5 }} onClick={getMock}>\n          Left button reload\n        </Button>\n      );\n    }\n    return (\n      <Button size=\"small\" style={{ float: 'right', margin: 5 }} onClick={getMock}>\n        Right button reload\n      </Button>\n    );\n  };\n\n  return (\n    <Transfer\n      dataSource={mockData}\n      showSearch\n      listStyle={{\n        width: 250,\n        height: 300,\n      }}\n      operations={['to right', 'to left']}\n      targetKeys={targetKeys}\n      onChange={handleChange}\n      render={(item) => `${item.title}-${item.description}`}\n      footer={renderFooter}\n    />\n  );\n};\n\nexport default App;\n";;
  