  export default "import React, { useState } from 'react';\nimport { Switch, Table } from 'antd';\nimport type { TableColumnsType } from 'antd';\n\ninterface DataType {\n  key: React.Key;\n  name: string;\n  age: number;\n  address: string;\n}\n\nconst columns: TableColumnsType<DataType> = [\n  {\n    title: 'Full Name',\n    width: 100,\n    dataIndex: 'name',\n    key: 'name',\n    fixed: 'left',\n  },\n  {\n    title: 'Age',\n    width: 100,\n    dataIndex: 'age',\n    key: 'age',\n    fixed: 'left',\n  },\n  {\n    title: 'Column 1',\n    dataIndex: 'address',\n    key: '1',\n    width: 150,\n  },\n  {\n    title: 'Column 2',\n    dataIndex: 'address',\n    key: '2',\n    width: 150,\n  },\n  {\n    title: 'Column 3',\n    dataIndex: 'address',\n    key: '3',\n    width: 150,\n  },\n  {\n    title: 'Column 4',\n    dataIndex: 'address',\n    key: '4',\n    width: 150,\n  },\n  {\n    title: 'Column 5',\n    dataIndex: 'address',\n    key: '5',\n    width: 150,\n  },\n  {\n    title: 'Column 6',\n    dataIndex: 'address',\n    key: '6',\n    width: 150,\n  },\n  {\n    title: 'Column 7',\n    dataIndex: 'address',\n    key: '7',\n    width: 150,\n  },\n  { title: 'Column 8', dataIndex: 'address', key: '8' },\n  {\n    title: 'Action',\n    key: 'operation',\n    fixed: 'right',\n    width: 100,\n    render: () => <a>action</a>,\n  },\n];\n\nconst data: DataType[] = [];\nfor (let i = 0; i < 100; i++) {\n  data.push({\n    key: i,\n    name: `Edward ${i}`,\n    age: 32,\n    address: `London Park no. ${i}`,\n  });\n}\n\nconst App: React.FC = () => {\n  const [fixedTop, setFixedTop] = useState(false);\n\n  return (\n    <Table\n      columns={columns}\n      dataSource={data}\n      scroll={{ x: 1500 }}\n      summary={() => (\n        <Table.Summary fixed={fixedTop ? 'top' : 'bottom'}>\n          <Table.Summary.Row>\n            <Table.Summary.Cell index={0} colSpan={2}>\n              <Switch\n                checkedChildren=\"Fixed Top\"\n                unCheckedChildren=\"Fixed Top\"\n                checked={fixedTop}\n                onChange={() => {\n                  setFixedTop(!fixedTop);\n                }}\n              />\n            </Table.Summary.Cell>\n            <Table.Summary.Cell index={2} colSpan={8}>\n              Scroll Context\n            </Table.Summary.Cell>\n            <Table.Summary.Cell index={10}>Fix Right</Table.Summary.Cell>\n          </Table.Summary.Row>\n        </Table.Summary>\n      )}\n      // antd site header height\n      sticky={{ offsetHeader: 64 }}\n    />\n  );\n};\n\nexport default App;\n";;
  