
  import '/Users/vagusx/Projects/ant-design/components/config-provider/demo/locale.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { EllipsisOutlined } from '@ant-design/icons';\nimport type {\n  ConfigProviderProps,\n  RadioChangeEvent,\n  TableProps,\n  TourProps,\n  UploadFile,\n} from 'antd';\nimport {\n  Button,\n  Calendar,\n  ConfigProvider,\n  DatePicker,\n  Divider,\n  Form,\n  Image,\n  Input,\n  InputNumber,\n  Modal,\n  Pagination,\n  Popconfirm,\n  QRCode,\n  Radio,\n  Select,\n  Space,\n  Table,\n  theme,\n  TimePicker,\n  Tour,\n  Transfer,\n  Upload,\n} from 'antd';\nimport enUS from 'antd/locale/en_US';\nimport zhCN from 'antd/locale/zh_CN';\nimport dayjs from 'dayjs';\n\nimport 'dayjs/locale/zh-cn';\n\ntype Locale = ConfigProviderProps['locale'];\n\ndayjs.locale('en');\n\nconst { Option } = Select;\nconst { RangePicker } = DatePicker;\n\nconst columns: TableProps['columns'] = [\n  {\n    title: 'Name',\n    dataIndex: 'name',\n    filters: [{ text: 'filter1', value: 'filter1' }],\n  },\n  {\n    title: 'Age',\n    dataIndex: 'age',\n  },\n];\n\nconst Page: React.FC = () => {\n  const { token } = theme.useToken();\n\n  const [open, setOpen] = useState(false);\n  const [tourOpen, setTourOpen] = useState(false);\n  const tourRefs = React.useRef<HTMLElement[]>([]);\n\n  const showModal = () => {\n    setOpen(true);\n  };\n\n  const hideModal = () => {\n    setOpen(false);\n  };\n\n  const info = () => {\n    Modal.info({\n      title: 'some info',\n      content: 'some info',\n    });\n  };\n\n  const confirm = () => {\n    Modal.confirm({\n      title: 'some info',\n      content: 'some info',\n    });\n  };\n\n  const steps: TourProps['steps'] = [\n    {\n      title: 'Upload File',\n      description: 'Put your files here.',\n      target: () => tourRefs.current[0],\n    },\n    {\n      title: 'Save',\n      description: 'Save your changes.',\n      target: () => tourRefs.current[1],\n    },\n    {\n      title: 'Other Actions',\n      description: 'Click to see other actions.',\n      target: () => tourRefs.current[2],\n    },\n  ];\n\n  const fileList: UploadFile[] = [\n    {\n      uid: '-1',\n      name: 'image.png',\n      status: 'done',\n      url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',\n    },\n    {\n      uid: '-2',\n      percent: 50,\n      name: 'image.png',\n      status: 'uploading',\n      url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',\n    },\n    {\n      uid: '-3',\n      name: 'image.png',\n      status: 'error',\n    },\n  ];\n\n  return (\n    <Space\n      direction=\"vertical\"\n      size={[0, 16]}\n      style={{ width: '100%', paddingTop: 16, borderTop: `1px solid ${token.colorBorder}` }}\n    >\n      <Pagination defaultCurrent={1} total={50} showSizeChanger />\n      <Space wrap>\n        <Select showSearch style={{ width: 200 }}>\n          <Option value=\"jack\">jack</Option>\n          <Option value=\"lucy\">lucy</Option>\n        </Select>\n        <DatePicker />\n        <TimePicker />\n        <RangePicker />\n      </Space>\n      <Space wrap>\n        <Button type=\"primary\" onClick={showModal}>\n          Show Modal\n        </Button>\n        <Button onClick={info}>Show info</Button>\n        <Button onClick={confirm}>Show confirm</Button>\n        <Popconfirm title=\"Question?\">\n          <a href=\"#\">Click to confirm</a>\n        </Popconfirm>\n      </Space>\n      <Transfer dataSource={[]} showSearch targetKeys={[]} />\n      <div style={{ width: 320, border: `1px solid ${token.colorBorder}`, borderRadius: 8 }}>\n        <Calendar fullscreen={false} value={dayjs()} />\n      </div>\n      <Form name=\"basic\" autoComplete=\"off\" labelCol={{ sm: { span: 4 } }} wrapperCol={{ span: 6 }}>\n        <Form.Item label=\"Username\" name=\"username\" rules={[{ required: true }]}>\n          <Input width={200} />\n        </Form.Item>\n        <Form.Item\n          label=\"Age\"\n          name=\"age\"\n          rules={[{ type: 'number', min: 0, max: 99 }]}\n          initialValue={100}\n        >\n          <InputNumber width={200} />\n        </Form.Item>\n        <Form.Item wrapperCol={{ offset: 2, span: 6 }}>\n          <Button type=\"primary\" htmlType=\"submit\">\n            Submit\n          </Button>\n        </Form.Item>\n      </Form>\n      <Table dataSource={[]} columns={columns} />\n      <Modal title=\"Locale Modal\" open={open} onCancel={hideModal}>\n        <p>Locale Modal</p>\n      </Modal>\n      <Space wrap size={80}>\n        <QRCode\n          value=\"https://ant.design/\"\n          status=\"expired\"\n          onRefresh={() => console.log('refresh')}\n        />\n        <Image\n          width={160}\n          src=\"https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png\"\n        />\n      </Space>\n      <Upload listType=\"picture-card\" fileList={fileList} />\n      <Divider orientation=\"left\">Tour</Divider>\n      <Button type=\"primary\" onClick={() => setTourOpen(true)}>\n        Begin Tour\n      </Button>\n      <Space>\n        <Button ref={(node) => node && tourRefs.current.splice(0, 0, node)}> Upload</Button>\n        <Button ref={(node) => node && tourRefs.current.splice(1, 0, node)} type=\"primary\">\n          Save\n        </Button>\n        <Button\n          ref={(node) => node && tourRefs.current.splice(2, 0, node)}\n          icon={<EllipsisOutlined />}\n        />\n      </Space>\n      <Tour open={tourOpen} steps={steps} onClose={() => setTourOpen(false)} />\n    </Space>\n  );\n};\n\nconst App: React.FC = () => {\n  const [locale, setLocal] = useState<Locale>(enUS);\n\n  const changeLocale = (e: RadioChangeEvent) => {\n    const localeValue = e.target.value;\n    setLocal(localeValue);\n    if (!localeValue) {\n      dayjs.locale('en');\n    } else {\n      dayjs.locale('zh-cn');\n    }\n  };\n\n  return (\n    <>\n      <div style={{ marginBottom: 16 }}>\n        <span style={{ marginInlineEnd: 16 }}>Change locale of components:</span>\n        <Radio.Group value={locale} onChange={changeLocale}>\n          <Radio.Button key=\"en\" value={enUS}>\n            English\n          </Radio.Button>\n          <Radio.Button key=\"cn\" value={zhCN}>\n            \n          </Radio.Button>\n        </Radio.Group>\n      </div>\n      <ConfigProvider locale={locale}>\n        <Page />\n      </ConfigProvider>\n    </>\n  );\n};\n\nexport default App;\n";;
  