
  import '/Users/vagusx/Projects/ant-design/components/drawer/demo/multi-level-drawer.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { Button, Drawer } from 'antd';\n\nconst App: React.FC = () => {\n  const [open, setOpen] = useState(false);\n  const [childrenDrawer, setChildrenDrawer] = useState(false);\n\n  const showDrawer = () => {\n    setOpen(true);\n  };\n\n  const onClose = () => {\n    setOpen(false);\n  };\n\n  const showChildrenDrawer = () => {\n    setChildrenDrawer(true);\n  };\n\n  const onChildrenDrawerClose = () => {\n    setChildrenDrawer(false);\n  };\n\n  return (\n    <>\n      <Button type=\"primary\" onClick={showDrawer}>\n        Open drawer\n      </Button>\n      <Drawer title=\"Multi-level drawer\" width={520} closable={false} onClose={onClose} open={open}>\n        <Button type=\"primary\" onClick={showChildrenDrawer}>\n          Two-level drawer\n        </Button>\n        <Drawer\n          title=\"Two-level Drawer\"\n          width={320}\n          closable={false}\n          onClose={onChildrenDrawerClose}\n          open={childrenDrawer}\n        >\n          This is two-level drawer\n        </Drawer>\n      </Drawer>\n    </>\n  );\n};\n\nexport default App;\n";;
  