
  import '/Users/vagusx/Projects/ant-design/components/form/demo/ref-item.tsx?watch=parent';
  export default "import React from 'react';\nimport type { InputRef } from 'antd';\nimport { Button, Form, Input } from 'antd';\n\nconst App: React.FC = () => {\n  const [form] = Form.useForm();\n  const ref = React.useRef<InputRef>(null);\n\n  return (\n    <Form form={form} initialValues={{ list: ['light'] }} style={{ maxWidth: 600 }}>\n      <Form.Item name=\"test\" label=\"test\">\n        <Input ref={ref} />\n      </Form.Item>\n\n      <Form.List name=\"list\">\n        {(fields) =>\n          fields.map((field) => (\n            <Form.Item {...field} key={field.key}>\n              <Input ref={ref} />\n            </Form.Item>\n          ))\n        }\n      </Form.List>\n\n      <Button\n        htmlType=\"button\"\n        onClick={() => {\n          form.getFieldInstance('test').focus();\n        }}\n      >\n        Focus Form.Item\n      </Button>\n      <Button\n        onClick={() => {\n          form.getFieldInstance(['list', 0]).focus();\n        }}\n      >\n        Focus Form.List\n      </Button>\n    </Form>\n  );\n};\n\nexport default App;\n";;
  