  export default "import React, { useState } from 'react';\nimport { ConfigProvider, Space, Switch, Table, Tag, Transfer } from 'antd';\nimport type { GetProp, TableColumnsType, TableProps, TransferProps } from 'antd';\nimport difference from 'lodash/difference';\n\ntype TableRowSelection<T> = TableProps<T>['rowSelection'];\n\ntype TransferItem = GetProp<TransferProps, 'dataSource'>[number];\n\ninterface RecordType {\n  key: string;\n  title: string;\n  description: string;\n  disabled: boolean;\n  tag: string;\n}\n\ninterface DataType {\n  key: string;\n  title: string;\n  description: string;\n  disabled: boolean;\n  tag: string;\n}\n\ninterface TableTransferProps extends TransferProps<TransferItem> {\n  dataSource: DataType[];\n  leftColumns: TableColumnsType<DataType>;\n  rightColumns: TableColumnsType<DataType>;\n}\n\n// Customize Table Transfer\nconst TableTransfer = ({ leftColumns, rightColumns, ...restProps }: TableTransferProps) => (\n  <Transfer {...restProps}>\n    {({\n      direction,\n      filteredItems,\n      onItemSelectAll,\n      onItemSelect,\n      selectedKeys: listSelectedKeys,\n      disabled: listDisabled,\n    }) => {\n      const columns = direction === 'left' ? leftColumns : rightColumns;\n\n      const rowSelection: TableRowSelection<TransferItem> = {\n        getCheckboxProps: (item) => ({ disabled: listDisabled || item.disabled }),\n        onSelectAll(selected, selectedRows) {\n          const treeSelectedKeys = selectedRows\n            .filter((item) => !item.disabled)\n            .map(({ key }) => key);\n          const diffKeys = selected\n            ? difference(treeSelectedKeys, listSelectedKeys)\n            : difference(listSelectedKeys, treeSelectedKeys);\n          onItemSelectAll(diffKeys as string[], selected);\n        },\n        onSelect({ key }, selected) {\n          onItemSelect(key as string, selected);\n        },\n        selectedRowKeys: listSelectedKeys,\n      };\n\n      return (\n        <Table\n          rowSelection={rowSelection}\n          columns={columns}\n          dataSource={filteredItems}\n          size=\"small\"\n          style={{ pointerEvents: listDisabled ? 'none' : undefined }}\n          onRow={({ key, disabled: itemDisabled }) => ({\n            onClick: () => {\n              if (itemDisabled || listDisabled) {\n                return;\n              }\n              onItemSelect(key as string, !listSelectedKeys.includes(key as string));\n            },\n          })}\n        />\n      );\n    }}\n  </Transfer>\n);\n\nconst mockTags = ['cat', 'dog', 'bird'];\n\nconst mockData: RecordType[] = Array.from({ length: 20 }).map((_, i) => ({\n  key: i.toString(),\n  title: `content${i + 1}`,\n  description: `description of content${i + 1}`,\n  disabled: i % 4 === 0,\n  tag: mockTags[i % 3],\n}));\n\nconst leftTableColumns: TableColumnsType<DataType> = [\n  {\n    dataIndex: 'title',\n    title: 'Name',\n  },\n  {\n    dataIndex: 'tag',\n    title: 'Tag',\n    render: (tag) => <Tag>{tag}</Tag>,\n  },\n  {\n    dataIndex: 'description',\n    title: 'Description',\n  },\n];\n\nconst rightTableColumns: TableColumnsType<DataType> = [\n  {\n    dataIndex: 'title',\n    title: 'Name',\n  },\n];\n\nconst initialTargetKeys = mockData.filter((item) => Number(item.key) > 10).map((item) => item.key);\n\nconst App: React.FC = () => {\n  const [targetKeys, setTargetKeys] = useState<React.Key[]>(initialTargetKeys);\n  const [selectedKeys, setSelectedKeys] = useState<React.Key[]>([]);\n\n  const onChange: TransferProps['onChange'] = (nextTargetKeys, direction, moveKeys) => {\n    console.log('targetKeys:', nextTargetKeys);\n    console.log('direction:', direction);\n    console.log('moveKeys:', moveKeys);\n    setTargetKeys(nextTargetKeys);\n  };\n\n  const onSelectChange: TransferProps['onSelectChange'] = (\n    sourceSelectedKeys,\n    targetSelectedKeys,\n  ) => {\n    console.log('sourceSelectedKeys:', sourceSelectedKeys);\n    console.log('targetSelectedKeys:', targetSelectedKeys);\n    setSelectedKeys([...sourceSelectedKeys, ...targetSelectedKeys]);\n  };\n\n  const onScroll: TransferProps['onScroll'] = (direction, e) => {\n    console.log('direction:', direction);\n    console.log('target:', e.target);\n  };\n\n  const [disabled, setDisabled] = useState(false);\n  const [showSearch, setShowSearch] = useState(false);\n\n  const secondOnChange: TransferProps['onChange'] = (nextTargetKeys) => {\n    setTargetKeys(nextTargetKeys);\n  };\n\n  const triggerDisable = (checked: boolean) => {\n    setDisabled(checked);\n  };\n\n  const triggerShowSearch = (checked: boolean) => {\n    setShowSearch(checked);\n  };\n\n  return (\n    <ConfigProvider\n      theme={{\n        components: {\n          Transfer: {\n            listWidth: 40,\n            listWidthLG: 50,\n            listHeight: 30,\n            itemHeight: 20,\n            itemPaddingBlock: 10,\n            headerHeight: 18,\n          },\n        },\n      }}\n    >\n      <Transfer\n        dataSource={mockData}\n        titles={['Source', 'Target']}\n        targetKeys={targetKeys}\n        selectedKeys={selectedKeys}\n        onChange={onChange}\n        onSelectChange={onSelectChange}\n        onScroll={onScroll}\n        render={(item) => item.title}\n      />\n      <Transfer status=\"error\" />\n      <Transfer status=\"warning\" showSearch />\n      <TableTransfer\n        dataSource={mockData}\n        targetKeys={targetKeys}\n        disabled={disabled}\n        showSearch={showSearch}\n        onChange={secondOnChange}\n        filterOption={(inputValue, item) =>\n          item.title!.indexOf(inputValue) !== -1 || item.tag.indexOf(inputValue) !== -1\n        }\n        leftColumns={leftTableColumns}\n        rightColumns={rightTableColumns}\n      />\n      <Space style={{ marginTop: 16 }}>\n        <Switch\n          unCheckedChildren=\"disabled\"\n          checkedChildren=\"disabled\"\n          checked={disabled}\n          onChange={triggerDisable}\n        />\n        <Switch\n          unCheckedChildren=\"showSearch\"\n          checkedChildren=\"showSearch\"\n          checked={showSearch}\n          onChange={triggerShowSearch}\n        />\n      </Space>\n    </ConfigProvider>\n  );\n};\n\nexport default App;\n";;
  