
  import '/Users/vagusx/Projects/ant-design/components/input/demo/advance-count.tsx?watch=parent';
  export default "import React from 'react';\nimport { Flex, Input, Typography } from 'antd';\nimport { runes } from 'runes2';\n\nconst App: React.FC = () => (\n  <Flex vertical gap={16}>\n    <div>\n      <Typography.Title level={5}>Exceed Max</Typography.Title>\n      <Input\n        count={{\n          show: true,\n          max: 10,\n        }}\n        defaultValue=\"Hello, antd!\"\n      />\n    </div>\n\n    <div>\n      <Typography.Title level={5}>Emoji count as length 1</Typography.Title>\n      <Input\n        count={{\n          show: true,\n          strategy: (txt) => runes(txt).length,\n        }}\n        defaultValue=\"\"\n      />\n    </div>\n\n    <div>\n      <Typography.Title level={5}>Not exceed max</Typography.Title>\n      <Input\n        count={{\n          show: true,\n          max: 6,\n          strategy: (txt) => runes(txt).length,\n          exceedFormatter: (txt, { max }) => runes(txt).slice(0, max).join(''),\n        }}\n        defaultValue=\" antd\"\n      />\n    </div>\n  </Flex>\n);\n\nexport default App;\n";;
  