  export default "import React from 'react';\nimport { Table } from 'antd';\nimport type { TableColumnsType } from 'antd';\n\ninterface DataType {\n  key: React.Key;\n  name: string;\n  age: number;\n  address: string;\n}\n\nconst columns: TableColumnsType<DataType> = [\n  {\n    title: 'Full Name',\n    width: 100,\n    dataIndex: 'name',\n    fixed: 'left',\n  },\n  {\n    title: 'Age',\n    width: 100,\n    dataIndex: 'age',\n  },\n  { title: 'Column 1', dataIndex: 'address', fixed: 'left' },\n  { title: 'Column 2', dataIndex: 'address' },\n  { title: 'Column 3', dataIndex: 'address' },\n  { title: 'Column 4', dataIndex: 'address' },\n  { title: 'Column 5', dataIndex: 'address' },\n  { title: 'Column 6', dataIndex: 'address' },\n  { title: 'Column 7', dataIndex: 'address' },\n  { title: 'Column 8', dataIndex: 'address' },\n  {\n    title: 'Action 1',\n    fixed: 'right',\n    width: 90,\n    render: () => <a>action</a>,\n  },\n  {\n    title: 'Action 2',\n    width: 90,\n    render: () => <a>action</a>,\n  },\n  {\n    title: 'Action 3',\n    fixed: 'right',\n    width: 90,\n    render: () => <a>action</a>,\n  },\n];\n\nconst data: DataType[] = [\n  {\n    key: '1',\n    name: 'John Brown',\n    age: 32,\n    address: 'New York Park',\n  },\n];\n\nconst App: React.FC = () => (\n  <Table columns={columns} dataSource={data} scroll={{ x: 1300 }} pagination={false} bordered />\n);\n\nexport default App;\n";;
  