
  import '/Users/vagusx/Projects/ant-design/components/modal/demo/custom-mouse-position.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { Button, Modal } from 'antd';\n\nconst App: React.FC = () => {\n  const [isModalOpen, setIsModalOpen] = useState(false);\n\n  const showModal = () => {\n    setIsModalOpen(true);\n  };\n\n  const handleOk = () => {\n    setIsModalOpen(false);\n  };\n\n  const handleCancel = () => {\n    setIsModalOpen(false);\n  };\n\n  return (\n    <>\n      <Button type=\"primary\" onClick={showModal}>\n        Open Modal\n      </Button>\n      <Modal\n        title=\"Basic Modal\"\n        open={isModalOpen}\n        onOk={handleOk}\n        onCancel={handleCancel}\n        mousePosition={{ x: 300, y: 300 }}\n      >\n        <p>Some contents...</p>\n        <p>Some contents...</p>\n        <p>Some contents...</p>\n      </Modal>\n    </>\n  );\n};\n\nexport default App;\n";;
  