
  import '/Users/vagusx/Projects/ant-design/components/input-number/demo/disabled.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { Button, InputNumber } from 'antd';\n\nconst App: React.FC = () => {\n  const [disabled, setDisabled] = useState(true);\n\n  const toggle = () => {\n    setDisabled(!disabled);\n  };\n\n  return (\n    <>\n      <InputNumber min={1} max={10} disabled={disabled} defaultValue={3} />\n      <div style={{ marginTop: 20 }}>\n        <Button onClick={toggle} type=\"primary\">\n          Toggle disabled\n        </Button>\n      </div>\n    </>\n  );\n};\n\nexport default App;\n";;
  