
  import '/Users/vagusx/Projects/ant-design/components/steps/demo/steps-in-steps.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport type { StepsProps } from 'antd';\nimport { Card, Radio, Steps } from 'antd';\n\nconst App: React.FC = () => {\n  const [size, setSize] = useState<StepsProps['size']>('default');\n  const description = 'This is a description.';\n  const horizontalSteps = (\n    <Card>\n      <Steps\n        size={size}\n        items={[\n          {\n            title: 'Finished',\n            description,\n          },\n          {\n            title: 'In Progress',\n            description,\n          },\n          {\n            title: 'Waiting',\n            description,\n          },\n        ]}\n      />\n    </Card>\n  );\n\n  return (\n    <>\n      <Radio.Group\n        style={{ marginBottom: 16 }}\n        value={size}\n        onChange={(e) => setSize(e.target.value)}\n      >\n        <Radio value=\"small\">Small</Radio>\n        <Radio value=\"default\">Default</Radio>\n      </Radio.Group>\n      <Steps\n        size={size}\n        direction=\"vertical\"\n        items={[\n          {\n            title: 'Finished',\n            description: horizontalSteps,\n          },\n          {\n            title: 'In Progress',\n            description,\n          },\n          {\n            title: 'Waiting',\n            description,\n          },\n        ]}\n      />\n    </>\n  );\n};\n\nexport default App;\n";;
  