  export default "import React, { useState } from 'react';\nimport type { RadioChangeEvent } from 'antd';\nimport { Radio } from 'antd';\n\nconst plainOptions = ['Apple', 'Pear', 'Orange'];\nconst options = [\n  { label: 'Apple', value: 'Apple' },\n  { label: 'Pear', value: 'Pear' },\n  { label: 'Orange', value: 'Orange', title: 'Orange' },\n];\nconst optionsWithDisabled = [\n  { label: 'Apple', value: 'Apple' },\n  { label: 'Pear', value: 'Pear' },\n  { label: 'Orange', value: 'Orange', disabled: true },\n];\n\nconst App: React.FC = () => {\n  const [value1, setValue1] = useState('Apple');\n  const [value2, setValue2] = useState('Apple');\n  const [value3, setValue3] = useState('Apple');\n  const [value4, setValue4] = useState('Apple');\n\n  const onChange1 = ({ target: { value } }: RadioChangeEvent) => {\n    console.log('radio1 checked', value);\n    setValue1(value);\n  };\n\n  const onChange2 = ({ target: { value } }: RadioChangeEvent) => {\n    console.log('radio2 checked', value);\n    setValue2(value);\n  };\n\n  const onChange3 = ({ target: { value } }: RadioChangeEvent) => {\n    console.log('radio3 checked', value);\n    setValue3(value);\n  };\n\n  const onChange4 = ({ target: { value } }: RadioChangeEvent) => {\n    console.log('radio4 checked', value);\n    setValue4(value);\n  };\n\n  return (\n    <>\n      <Radio.Group options={plainOptions} onChange={onChange1} value={value1} />\n      <br />\n      <Radio.Group options={optionsWithDisabled} onChange={onChange2} value={value2} />\n      <br />\n      <br />\n      <Radio.Group options={options} onChange={onChange3} value={value3} optionType=\"button\" />\n      <br />\n      <br />\n      <Radio.Group\n        options={optionsWithDisabled}\n        onChange={onChange4}\n        value={value4}\n        optionType=\"button\"\n        buttonStyle=\"solid\"\n      />\n    </>\n  );\n};\n\nexport default App;\n";;
  