  export default "import React from 'react';\nimport { Table, Tooltip } from 'antd';\nimport type { TableColumnsType } from 'antd';\n\ninterface DataType {\n  key: React.Key;\n  name: string;\n  age: number;\n  address: string;\n}\n\nconst columns: TableColumnsType<DataType> = [\n  {\n    title: 'Name',\n    dataIndex: 'name',\n    key: 'name',\n    render: (text) => <a>{text}</a>,\n    width: 150,\n  },\n  {\n    title: 'Age',\n    dataIndex: 'age',\n    key: 'age',\n    width: 80,\n  },\n  {\n    title: 'Address',\n    dataIndex: 'address',\n    key: 'address 1',\n    ellipsis: {\n      showTitle: false,\n    },\n    render: (address) => (\n      <Tooltip placement=\"topLeft\" title={address}>\n        {address}\n      </Tooltip>\n    ),\n  },\n  {\n    title: 'Long Column Long Column Long Column',\n    dataIndex: 'address',\n    key: 'address 2',\n    ellipsis: {\n      showTitle: false,\n    },\n    render: (address) => (\n      <Tooltip placement=\"topLeft\" title={address}>\n        {address}\n      </Tooltip>\n    ),\n  },\n  {\n    title: 'Long Column Long Column',\n    dataIndex: 'address',\n    key: 'address 3',\n    ellipsis: {\n      showTitle: false,\n    },\n    render: (address) => (\n      <Tooltip placement=\"topLeft\" title={address}>\n        {address}\n      </Tooltip>\n    ),\n  },\n  {\n    title: 'Long Column',\n    dataIndex: 'address',\n    key: 'address 4',\n    ellipsis: {\n      showTitle: false,\n    },\n    render: (address) => (\n      <Tooltip placement=\"topLeft\" title={address}>\n        {address}\n      </Tooltip>\n    ),\n  },\n];\n\nconst data: DataType[] = [\n  {\n    key: '1',\n    name: 'John Brown',\n    age: 32,\n    address: 'New York No. 1 Lake Park, New York No. 1 Lake Park',\n  },\n  {\n    key: '2',\n    name: 'Jim Green',\n    age: 42,\n    address: 'London No. 2 Lake Park, London No. 2 Lake Park',\n  },\n  {\n    key: '3',\n    name: 'Joe Black',\n    age: 32,\n    address: 'Sydney No. 1 Lake Park, Sydney No. 1 Lake Park',\n  },\n];\n\nconst App: React.FC = () => <Table columns={columns} dataSource={data} />;\n\nexport default App;\n";;
  