  export default "import React, { useState } from 'react';\nimport { AppstoreOutlined, MailOutlined } from '@ant-design/icons';\nimport type { MenuProps, MenuTheme } from 'antd';\nimport { Menu, Switch } from 'antd';\n\ntype MenuItem = Required<MenuProps>['items'][number];\n\nconst items: MenuItem[] = [\n  {\n    key: 'sub1',\n    label: 'Navigation One Long Long Long Long',\n    icon: <MailOutlined />,\n    children: [\n      { key: '1', label: 'Option 1' },\n      { key: '2', label: 'Option 2' },\n      { key: '3', label: 'Option 3' },\n      { key: '4', label: 'Option 4' },\n    ],\n  },\n  {\n    key: 'sub2',\n    label: 'Navigation Two',\n    icon: <AppstoreOutlined />,\n    children: [\n      { key: '5', label: 'Option 5' },\n      { key: '6', label: 'Option 6' },\n      {\n        key: 'sub3',\n        label: 'Submenu',\n        children: [\n          { key: '7', label: 'Option 7' },\n          { key: '8', label: 'Option 8' },\n        ],\n      },\n    ],\n  },\n  { key: '11', label: 'Option 11' },\n  { key: '12', label: 'Option 12' },\n];\n\nconst App: React.FC = () => {\n  const [menuTheme, setMenuTheme] = useState<MenuTheme>('dark');\n  const [current, setCurrent] = useState('1');\n\n  const changeTheme = (value: boolean) => {\n    setMenuTheme(value ? 'dark' : 'light');\n  };\n\n  const onClick: MenuProps['onClick'] = (e) => {\n    console.log('click ', e);\n    setCurrent(e.key);\n  };\n\n  return (\n    <>\n      <Switch\n        checked={menuTheme === 'dark'}\n        onChange={changeTheme}\n        checkedChildren=\"Dark\"\n        unCheckedChildren=\"Light\"\n      />\n      <br />\n      <br />\n      <Menu\n        theme={menuTheme}\n        onClick={onClick}\n        selectedKeys={[current]}\n        mode=\"inline\"\n        items={items}\n        inlineCollapsed\n        // Test only. Remove in future.\n        _internalRenderMenuItem={(node) =>\n          React.cloneElement(node, {\n            style: {\n              ...node.props.style,\n              textDecoration: 'underline',\n            },\n          })\n        }\n        // Test only. Remove in future.\n        _internalRenderSubMenuItem={(node) =>\n          React.cloneElement(node, {\n            style: {\n              ...node.props.style,\n              background: 'rgba(255, 255, 255, 0.3)',\n            },\n          })\n        }\n        // Test only. Remove in future.\n        _internalDisableMenuItemTitleTooltip\n      />\n    </>\n  );\n};\n\nexport default App;\n";;
  