  export default "import React from 'react';\nimport { Button, message } from 'antd';\n\nconst App: React.FC = () => {\n  const [messageApi, contextHolder] = message.useMessage();\n  const key = 'updatable';\n\n  const openMessage = () => {\n    messageApi.open({\n      key,\n      type: 'loading',\n      content: 'Loading...',\n    });\n    setTimeout(() => {\n      messageApi.open({\n        key,\n        type: 'success',\n        content: 'Loaded!',\n        duration: 2,\n      });\n    }, 1000);\n  };\n\n  return (\n    <>\n      {contextHolder}\n      <Button type=\"primary\" onClick={openMessage}>\n        Open the message box\n      </Button>\n    </>\n  );\n};\n\nexport default App;\n";;
  