
  import '/Users/vagusx/Projects/ant-design/components/select/demo/coordinate.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { Select, Space } from 'antd';\n\nconst cityData = {\n  Zhejiang: ['Hangzhou', 'Ningbo', 'Wenzhou'],\n  Jiangsu: ['Nanjing', 'Suzhou', 'Zhenjiang'],\n};\n\ntype CityName = keyof typeof cityData;\n\nconst provinceData: CityName[] = ['Zhejiang', 'Jiangsu'];\n\nconst App: React.FC = () => {\n  const [cities, setCities] = useState(cityData[provinceData[0] as CityName]);\n  const [secondCity, setSecondCity] = useState(cityData[provinceData[0]][0] as CityName);\n\n  const handleProvinceChange = (value: CityName) => {\n    setCities(cityData[value]);\n    setSecondCity(cityData[value][0] as CityName);\n  };\n\n  const onSecondCityChange = (value: CityName) => {\n    setSecondCity(value);\n  };\n\n  return (\n    <Space wrap>\n      <Select\n        defaultValue={provinceData[0]}\n        style={{ width: 120 }}\n        onChange={handleProvinceChange}\n        options={provinceData.map((province) => ({ label: province, value: province }))}\n      />\n      <Select\n        style={{ width: 120 }}\n        value={secondCity}\n        onChange={onSecondCityChange}\n        options={cities.map((city) => ({ label: city, value: city }))}\n      />\n    </Space>\n  );\n};\n\nexport default App;\n";;
  