
  import '/Users/vagusx/Projects/ant-design/components/notification/demo/show-with-progress.tsx?watch=parent';
  export default "import React from 'react';\nimport { Button, notification, Space } from 'antd';\n\nconst App: React.FC = () => {\n  const [api, contextHolder] = notification.useNotification();\n\n  const openNotification = (pauseOnHover: boolean) => () => {\n    api.open({\n      message: 'Notification Title',\n      description:\n        'This is the content of the notification. This is the content of the notification. This is the content of the notification.',\n      showProgress: true,\n      pauseOnHover,\n    });\n  };\n\n  return (\n    <>\n      {contextHolder}\n      <Space>\n        <Button type=\"primary\" onClick={openNotification(true)}>\n          Pause on hover\n        </Button>\n        <Button type=\"primary\" onClick={openNotification(false)}>\n          Don&apos;t pause on hover\n        </Button>\n      </Space>\n    </>\n  );\n};\n\nexport default App;\n";;
  