
  import '/Users/vagusx/Projects/ant-design/components/table/demo/pagination.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { Radio, Space, Table, Tag } from 'antd';\nimport type { TableProps } from 'antd';\n\ntype ColumnsType<T extends object> = TableProps<T>['columns'];\ntype TablePagination<T extends object> = NonNullable<Exclude<TableProps<T>['pagination'], boolean>>;\ntype TablePaginationPosition = NonNullable<TablePagination<any>['position']>[number];\n\ninterface DataType {\n  key: string;\n  name: string;\n  age: number;\n  address: string;\n  tags: string[];\n}\n\nconst topOptions = [\n  { label: 'topLeft', value: 'topLeft' },\n  { label: 'topCenter', value: 'topCenter' },\n  { label: 'topRight', value: 'topRight' },\n  { label: 'none', value: 'none' },\n];\n\nconst bottomOptions = [\n  { label: 'bottomLeft', value: 'bottomLeft' },\n  { label: 'bottomCenter', value: 'bottomCenter' },\n  { label: 'bottomRight', value: 'bottomRight' },\n  { label: 'none', value: 'none' },\n];\n\nconst columns: ColumnsType<DataType> = [\n  {\n    title: 'Name',\n    dataIndex: 'name',\n    key: 'name',\n    render: (text) => <a>{text}</a>,\n  },\n  {\n    title: 'Age',\n    dataIndex: 'age',\n    key: 'age',\n  },\n  {\n    title: 'Address',\n    dataIndex: 'address',\n    key: 'address',\n  },\n  {\n    title: 'Tags',\n    key: 'tags',\n    dataIndex: 'tags',\n    render: (tags: string[]) => (\n      <span>\n        {tags.map((tag) => {\n          let color = tag.length > 5 ? 'geekblue' : 'green';\n          if (tag === 'loser') {\n            color = 'volcano';\n          }\n          return (\n            <Tag color={color} key={tag}>\n              {tag.toUpperCase()}\n            </Tag>\n          );\n        })}\n      </span>\n    ),\n  },\n  {\n    title: 'Action',\n    key: 'action',\n    render: (_, record) => (\n      <Space size=\"middle\">\n        <a>Invite {record.name}</a>\n        <a>Delete</a>\n      </Space>\n    ),\n  },\n];\n\nconst data: DataType[] = [\n  {\n    key: '1',\n    name: 'John Brown',\n    age: 32,\n    address: 'New York No. 1 Lake Park',\n    tags: ['nice', 'developer'],\n  },\n  {\n    key: '2',\n    name: 'Jim Green',\n    age: 42,\n    address: 'London No. 1 Lake Park',\n    tags: ['loser'],\n  },\n  {\n    key: '3',\n    name: 'Joe Black',\n    age: 32,\n    address: 'Sydney No. 1 Lake Park',\n    tags: ['cool', 'teacher'],\n  },\n];\n\nconst App: React.FC = () => {\n  const [top, setTop] = useState<TablePaginationPosition>('topLeft');\n  const [bottom, setBottom] = useState<TablePaginationPosition>('bottomRight');\n\n  return (\n    <div>\n      <div>\n        <Radio.Group\n          style={{ marginBottom: 10 }}\n          options={topOptions}\n          value={top}\n          onChange={(e) => {\n            setTop(e.target.value);\n          }}\n        />\n      </div>\n      <Radio.Group\n        style={{ marginBottom: 10 }}\n        options={bottomOptions}\n        value={bottom}\n        onChange={(e) => {\n          setBottom(e.target.value);\n        }}\n      />\n      <Table columns={columns} pagination={{ position: [top, bottom] }} dataSource={data} />\n    </div>\n  );\n};\n\nexport default App;\n";;
  