
  import '/Users/vagusx/Projects/ant-design/components/modal/demo/footer-render.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { Button, Modal, Space } from 'antd';\n\nconst App: React.FC = () => {\n  const [open, setOpen] = useState(false);\n\n  const showModal = () => {\n    setOpen(true);\n  };\n  const handleOk = () => {\n    setOpen(false);\n  };\n\n  const handleCancel = () => {\n    setOpen(false);\n  };\n  return (\n    <>\n      <Space>\n        <Button type=\"primary\" onClick={showModal}>\n          Open Modal\n        </Button>\n        <Button\n          type=\"primary\"\n          onClick={() => {\n            Modal.confirm({\n              title: 'Confirm',\n              content: 'Bla bla ...',\n              footer: (_, { OkBtn, CancelBtn }) => (\n                <>\n                  <Button>Custom Button</Button>\n                  <CancelBtn />\n                  <OkBtn />\n                </>\n              ),\n            });\n          }}\n        >\n          Open Modal Confirm\n        </Button>\n      </Space>\n      <Modal\n        open={open}\n        title=\"Title\"\n        onOk={handleOk}\n        onCancel={handleCancel}\n        footer={(_, { OkBtn, CancelBtn }) => (\n          <>\n            <Button>Custom Button</Button>\n            <CancelBtn />\n            <OkBtn />\n          </>\n        )}\n      >\n        <p>Some contents...</p>\n        <p>Some contents...</p>\n        <p>Some contents...</p>\n        <p>Some contents...</p>\n        <p>Some contents...</p>\n      </Modal>\n    </>\n  );\n};\n\nexport default App;\n";;
  