
  import '/Users/vagusx/Projects/ant-design/components/steps/demo/clickable.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { Divider, Steps } from 'antd';\n\nconst App: React.FC = () => {\n  const [current, setCurrent] = useState(0);\n\n  const onChange = (value: number) => {\n    console.log('onChange:', value);\n    setCurrent(value);\n  };\n  const description = 'This is a description.';\n\n  return (\n    <>\n      <Steps\n        current={current}\n        onChange={onChange}\n        items={[\n          {\n            title: 'Step 1',\n            description,\n          },\n          {\n            title: 'Step 2',\n            description,\n          },\n          {\n            title: 'Step 3',\n            description,\n          },\n        ]}\n      />\n\n      <Divider />\n\n      <Steps\n        current={current}\n        onChange={onChange}\n        direction=\"vertical\"\n        items={[\n          {\n            title: 'Step 1',\n            description,\n          },\n          {\n            title: 'Step 2',\n            description,\n          },\n          {\n            title: 'Step 3',\n            description,\n          },\n        ]}\n      />\n    </>\n  );\n};\n\nexport default App;\n";;
  