
  import '/Users/vagusx/Projects/ant-design/components/tour/demo/indicator.tsx?watch=parent';
  export default "import React, { useRef, useState } from 'react';\nimport { EllipsisOutlined } from '@ant-design/icons';\nimport type { GetRef, TourProps } from 'antd';\nimport { Button, Divider, Space, Tour } from 'antd';\n\nconst App: React.FC = () => {\n  const ref1 = useRef<GetRef<typeof Button>>(null);\n  const ref2 = useRef<GetRef<typeof Button>>(null);\n  const ref3 = useRef<GetRef<typeof Button>>(null);\n\n  const [open, setOpen] = useState<boolean>(false);\n\n  const steps: TourProps['steps'] = [\n    {\n      title: 'Upload File',\n      description: 'Put your files here.',\n      target: () => ref1.current!,\n    },\n    {\n      title: 'Save',\n      description: 'Save your changes.',\n      target: () => ref2.current!,\n    },\n    {\n      title: 'Other Actions',\n      description: 'Click to see other actions.',\n      target: () => ref3.current!,\n    },\n  ];\n\n  return (\n    <>\n      <Button type=\"primary\" onClick={() => setOpen(true)}>\n        Begin Tour\n      </Button>\n      <Divider />\n      <Space>\n        <Button ref={ref1}>Upload</Button>\n        <Button ref={ref2} type=\"primary\">\n          Save\n        </Button>\n        <Button ref={ref3} icon={<EllipsisOutlined />} />\n      </Space>\n      <Tour\n        open={open}\n        onClose={() => setOpen(false)}\n        steps={steps}\n        indicatorsRender={(current, total) => (\n          <span>\n            {current + 1} / {total}\n          </span>\n        )}\n      />\n    </>\n  );\n};\n\nexport default App;\n";;
  