  export default "import React, { useState } from 'react';\nimport { Button, message, Modal, notification, Select, Space, Switch } from 'antd';\n\nconst options = [\n  {\n    label: 'Option 1',\n    value: '1',\n  },\n  {\n    label: 'Option 2',\n    value: '2',\n  },\n];\n\nconst Demo: React.FC = () => {\n  const [messageInstance, messageHolder] = message.useMessage();\n  const [notificationInstance, notificationHolder] = notification.useNotification();\n\n  const [isModalOpen, setIsModalOpen] = useState<boolean>(false);\n\n  const onShowStatic = () => {\n    Modal.confirm({\n      content: <Select open value=\"1\" options={options} />,\n    });\n  };\n\n  return (\n    <Space>\n      <Switch\n        style={{ position: 'relative', zIndex: isModalOpen ? 4000 : 0 }}\n        checkedChildren=\"Open\"\n        unCheckedChildren=\"Close\"\n        onChange={(open) => setIsModalOpen(open)}\n      />\n      <Button onClick={onShowStatic}>Static</Button>\n      <Modal\n        title=\"Basic Modal\"\n        open={isModalOpen}\n        footer={null}\n        destroyOnClose\n        onCancel={() => setIsModalOpen(false)}\n        maskClosable={false}\n        closable={false}\n        styles={{\n          content: {\n            marginBlockStart: 100,\n          },\n        }}\n      >\n        <Select open value=\"1\" options={options} />\n        <Modal\n          title=\"Nested Modal\"\n          open={isModalOpen}\n          footer={null}\n          destroyOnClose\n          mask={false}\n          onCancel={() => setIsModalOpen(false)}\n          maskClosable={false}\n          closable={false}\n          styles={{\n            content: {\n              marginBlockStart: 250,\n            },\n            body: {\n              display: 'flex',\n              justifyContent: 'center',\n            },\n          }}\n        >\n          <Select open value=\"1\" options={options} />\n\n          <Modal\n            title=\"Nested Modal\"\n            open={isModalOpen}\n            footer={null}\n            destroyOnClose\n            mask={false}\n            maskClosable={false}\n            onCancel={() => setIsModalOpen(false)}\n            closable={false}\n            styles={{\n              content: {\n                marginBlockStart: 400,\n              },\n              body: {\n                display: 'flex',\n                justifyContent: 'flex-end',\n              },\n            }}\n          >\n            <Space wrap>\n              <Button\n                onClick={() => {\n                  Modal.confirm({\n                    title: 'Are you OK?',\n                    content: 'I am OK',\n                  });\n                }}\n              >\n                Static Confirm\n              </Button>\n\n              <Button\n                onClick={() => {\n                  message.success('Hello World');\n                  notification.success({\n                    message: 'Hello World',\n                  });\n                }}\n              >\n                Static Message, Notification\n              </Button>\n\n              <Button\n                onClick={() => {\n                  messageInstance.success('Hello World');\n                  notificationInstance.success({\n                    message: 'Hello World',\n                  });\n                }}\n              >\n                Hook Message, Notification\n              </Button>\n\n              <Select open value=\"1\" options={options} />\n            </Space>\n          </Modal>\n        </Modal>\n      </Modal>\n\n      {messageHolder}\n      {notificationHolder}\n    </Space>\n  );\n};\n\nexport default Demo;\n";;
  