  export default "import React, { useEffect, useRef, useState } from 'react';\nimport { SmileOutlined, UserOutlined } from '@ant-design/icons';\nimport { Avatar, Button, Flex, Form, Input, InputNumber, Modal, Space, Typography } from 'antd';\nimport type { GetRef } from 'antd';\n\ntype FormInstance = GetRef<typeof Form>;\n\nconst layout = {\n  labelCol: { span: 8 },\n  wrapperCol: { span: 16 },\n};\n\nconst tailLayout = {\n  wrapperCol: { offset: 8, span: 16 },\n};\n\ninterface UserType {\n  name: string;\n  age: string;\n}\n\ninterface ModalFormProps {\n  open: boolean;\n  onCancel: () => void;\n}\n\n// reset form fields when modal is form, closed\nconst useResetFormOnCloseModal = ({ form, open }: { form: FormInstance; open: boolean }) => {\n  const prevOpenRef = useRef<boolean>();\n  useEffect(() => {\n    prevOpenRef.current = open;\n  }, [open]);\n  const prevOpen = prevOpenRef.current;\n\n  useEffect(() => {\n    if (!open && prevOpen) {\n      form.resetFields();\n    }\n  }, [form, prevOpen, open]);\n};\n\nconst ModalForm: React.FC<ModalFormProps> = ({ open, onCancel }) => {\n  const [form] = Form.useForm();\n\n  useResetFormOnCloseModal({\n    form,\n    open,\n  });\n\n  const onOk = () => {\n    form.submit();\n  };\n\n  return (\n    <Modal title=\"Basic Drawer\" open={open} onOk={onOk} onCancel={onCancel}>\n      <Form form={form} layout=\"vertical\" name=\"userForm\">\n        <Form.Item name=\"name\" label=\"User Name\" rules={[{ required: true }]}>\n          <Input />\n        </Form.Item>\n        <Form.Item name=\"age\" label=\"User Age\" rules={[{ required: true }]}>\n          <InputNumber />\n        </Form.Item>\n      </Form>\n    </Modal>\n  );\n};\n\nconst App: React.FC = () => {\n  const [open, setOpen] = useState(false);\n\n  const showUserModal = () => {\n    setOpen(true);\n  };\n\n  const hideUserModal = () => {\n    setOpen(false);\n  };\n\n  const onFinish = (values: any) => {\n    console.log('Finish:', values);\n  };\n\n  return (\n    <Form.Provider\n      onFormFinish={(name, { values, forms }) => {\n        if (name === 'userForm') {\n          const { basicForm } = forms;\n          const users = basicForm.getFieldValue('users') || [];\n          basicForm.setFieldsValue({ users: [...users, values] });\n          setOpen(false);\n        }\n      }}\n    >\n      <Form {...layout} name=\"basicForm\" onFinish={onFinish} style={{ maxWidth: 600 }}>\n        <Form.Item name=\"group\" label=\"Group Name\" rules={[{ required: true }]}>\n          <Input />\n        </Form.Item>\n\n        {/* Create a hidden field to make Form instance record this */}\n        <Form.Item name=\"users\" noStyle />\n\n        <Form.Item\n          label=\"User List\"\n          shouldUpdate={(prevValues, curValues) => prevValues.users !== curValues.users}\n        >\n          {({ getFieldValue }) => {\n            const users: UserType[] = getFieldValue('users') || [];\n            return users.length ? (\n              <Flex vertical gap={8}>\n                {users.map((user) => (\n                  <Space key={user.name}>\n                    <Avatar icon={<UserOutlined />} />\n                    {`${user.name} - ${user.age}`}\n                  </Space>\n                ))}\n              </Flex>\n            ) : (\n              <Typography.Text className=\"ant-form-text\" type=\"secondary\">\n                ( <SmileOutlined /> No user yet. )\n              </Typography.Text>\n            );\n          }}\n        </Form.Item>\n        <Form.Item {...tailLayout}>\n          <Button htmlType=\"submit\" type=\"primary\">\n            Submit\n          </Button>\n          <Button htmlType=\"button\" style={{ margin: '0 8px' }} onClick={showUserModal}>\n            Add User\n          </Button>\n        </Form.Item>\n      </Form>\n\n      <ModalForm open={open} onCancel={hideUserModal} />\n    </Form.Provider>\n  );\n};\n\nexport default App;\n";;
  