
  import '/Users/vagusx/Projects/ant-design/components/layout/demo/custom-trigger-debug.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport {\n  DesktopOutlined,\n  FileOutlined,\n  MenuFoldOutlined,\n  MenuUnfoldOutlined,\n  PieChartOutlined,\n  TeamOutlined,\n  UserOutlined,\n} from '@ant-design/icons';\nimport type { MenuProps } from 'antd';\nimport { Button, Layout, Menu, theme } from 'antd';\n\nconst { Header, Sider, Content } = Layout;\n\nconst items: MenuProps['items'] = [\n  {\n    key: '1',\n    icon: <PieChartOutlined />,\n    label: 'Option 1',\n  },\n  {\n    key: '2',\n    icon: <DesktopOutlined />,\n    label: 'Option 2',\n  },\n  {\n    key: 'sub1',\n    icon: <UserOutlined />,\n    label: 'User',\n    children: [\n      {\n        key: '3',\n        label: 'Tom',\n      },\n      {\n        key: '4',\n        label: 'Bill',\n      },\n      {\n        key: '5',\n        label: 'Alex',\n      },\n    ],\n  },\n  {\n    key: 'sub2',\n    icon: <TeamOutlined />,\n    label: 'Team',\n    children: [\n      {\n        key: '6',\n        label: 'Team 1',\n      },\n      {\n        key: '7',\n        label: 'Team 2',\n      },\n    ],\n  },\n  {\n    key: '9',\n    icon: <FileOutlined />,\n  },\n];\n\nconst App: React.FC = () => {\n  const [collapsed, setCollapsed] = useState(true);\n  const {\n    token: { colorBgContainer, borderRadiusLG },\n  } = theme.useToken();\n\n  return (\n    <Layout>\n      <Sider trigger={null} collapsible collapsed={collapsed}>\n        <div className=\"demo-logo-vertical\" />\n        <Menu\n          theme=\"dark\"\n          mode=\"inline\"\n          defaultSelectedKeys={['3']}\n          defaultOpenKeys={['sub1']}\n          items={items}\n        />\n      </Sider>\n      <Layout>\n        <Header style={{ padding: 0, background: colorBgContainer }}>\n          <Button\n            type=\"text\"\n            icon={collapsed ? <MenuUnfoldOutlined /> : <MenuFoldOutlined />}\n            onClick={() => setCollapsed(!collapsed)}\n            style={{\n              fontSize: '16px',\n              width: 64,\n              height: 64,\n            }}\n          />\n        </Header>\n        <Content\n          style={{\n            margin: '24px 16px',\n            padding: 24,\n            minHeight: 280,\n            background: colorBgContainer,\n            borderRadius: borderRadiusLG,\n          }}\n        >\n          Content\n        </Content>\n      </Layout>\n    </Layout>\n  );\n};\n\nexport default App;\n";;
  