
  import '/Users/vagusx/Projects/ant-design/components/config-provider/demo/direction.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport {\n  DownloadOutlined,\n  LeftOutlined,\n  MinusOutlined,\n  PlusOutlined,\n  RightOutlined,\n  SearchOutlined as SearchIcon,\n  SmileOutlined,\n} from '@ant-design/icons';\nimport type { ConfigProviderProps, RadioChangeEvent } from 'antd';\nimport {\n  Badge,\n  Button,\n  Cascader,\n  Col,\n  ConfigProvider,\n  Divider,\n  Input,\n  InputNumber,\n  Modal,\n  Pagination,\n  Radio,\n  Rate,\n  Row,\n  Select,\n  Space,\n  Steps,\n  Switch,\n  Tree,\n  TreeSelect,\n} from 'antd';\n\ntype DirectionType = ConfigProviderProps['direction'];\n\nconst InputGroup = Input.Group;\nconst ButtonGroup = Button.Group;\n\nconst { Option } = Select;\nconst { TreeNode } = Tree;\nconst { Search } = Input;\n\nconst cascaderOptions = [\n  {\n    value: 'tehran',\n    label: '',\n    children: [\n      {\n        value: 'tehran-c',\n        label: '',\n        children: [\n          {\n            value: 'saadat-abad',\n            label: ' ',\n          },\n        ],\n      },\n    ],\n  },\n  {\n    value: 'ardabil',\n    label: '',\n    children: [\n      {\n        value: 'ardabil-c',\n        label: '',\n        children: [\n          {\n            value: 'primadar',\n            label: '',\n          },\n        ],\n      },\n    ],\n  },\n  {\n    value: 'gilan',\n    label: '',\n    children: [\n      {\n        value: 'rasht',\n        label: '',\n        children: [\n          {\n            value: 'district-3',\n            label: ' ',\n          },\n        ],\n      },\n    ],\n  },\n];\n\ntype Placement = 'bottomLeft' | 'bottomRight' | 'topLeft' | 'topRight';\n\nconst Page: React.FC<{ placement: Placement }> = ({ placement }) => {\n  const [currentStep, setCurrentStep] = useState(0);\n  const [modalOpen, setModalOpen] = useState(false);\n  const [badgeCount, setBadgeCount] = useState(5);\n  const [showBadge, setShowBadge] = useState(true);\n\n  const selectBefore = (\n    <Select defaultValue=\"Http://\" style={{ width: 90 }}>\n      <Option value=\"Http://\">Http://</Option>\n      <Option value=\"Https://\">Https://</Option>\n    </Select>\n  );\n\n  const selectAfter = (\n    <Select defaultValue=\".com\" style={{ width: 80 }}>\n      <Option value=\".com\">.com</Option>\n      <Option value=\".jp\">.jp</Option>\n      <Option value=\".cn\">.cn</Option>\n      <Option value=\".org\">.org</Option>\n    </Select>\n  );\n\n  // ==== Cascader ====\n  const cascaderFilter = (inputValue: string, path: { label: string }[]) =>\n    path.some((option) => option.label.toLowerCase().includes(inputValue.toLowerCase()));\n\n  const onCascaderChange = (value: any) => {\n    console.log(value);\n  };\n  // ==== End Cascader ====\n\n  // ==== Modal ====\n  const showModal = () => {\n    setModalOpen(true);\n  };\n\n  const handleOk = (e: React.MouseEvent<HTMLElement>) => {\n    console.log(e);\n    setModalOpen(false);\n  };\n\n  const handleCancel = (e: React.MouseEvent<HTMLElement>) => {\n    console.log(e);\n    setModalOpen(false);\n  };\n\n  // ==== End Modal ====\n  const onStepsChange = (newCurrentStep: number) => {\n    console.log('onChange:', newCurrentStep);\n    setCurrentStep(newCurrentStep);\n  };\n\n  // ==== Badge ====\n  const increaseBadge = () => {\n    setBadgeCount(badgeCount + 1);\n  };\n\n  const declineBadge = () => {\n    setBadgeCount((prev) => (prev - 1 < 0 ? 0 : prev - 1));\n  };\n\n  const onChangeBadge = (checked: boolean) => {\n    setShowBadge(checked);\n  };\n  // ==== End Badge ====\n\n  return (\n    <div className=\"direction-components\">\n      <Row>\n        <Col span={24}>\n          <Divider orientation=\"left\">Cascader example</Divider>\n          <Cascader\n            suffixIcon={<SearchIcon />}\n            options={cascaderOptions}\n            onChange={onCascaderChange}\n            placeholder=\"   \"\n            placement={placement}\n          />\n          &nbsp;&nbsp;&nbsp;&nbsp;With search:&nbsp;&nbsp;\n          <Cascader\n            suffixIcon={<SmileOutlined />}\n            options={cascaderOptions}\n            onChange={onCascaderChange}\n            placeholder=\"Select an item\"\n            placement={placement}\n            showSearch={{ filter: cascaderFilter }}\n          />\n        </Col>\n      </Row>\n      <br />\n      <Row>\n        <Col span={12}>\n          <Divider orientation=\"left\">Switch example</Divider>\n          &nbsp;&nbsp;\n          <Switch defaultChecked />\n          &nbsp;&nbsp;\n          <Switch loading defaultChecked />\n          &nbsp;&nbsp;\n          <Switch size=\"small\" loading />\n        </Col>\n        <Col span={12}>\n          <Divider orientation=\"left\">Radio Group example</Divider>\n          <Radio.Group defaultValue=\"c\" buttonStyle=\"solid\">\n            <Radio.Button value=\"a\"></Radio.Button>\n            <Radio.Button value=\"b\" disabled>\n              \n            </Radio.Button>\n            <Radio.Button value=\"c\"></Radio.Button>\n            <Radio.Button value=\"d\"></Radio.Button>\n          </Radio.Group>\n        </Col>\n      </Row>\n      <br />\n      <Row>\n        <Col span={12}>\n          <Divider orientation=\"left\">Button example</Divider>\n          <div className=\"button-demo\">\n            <Button type=\"primary\" icon={<DownloadOutlined />} />\n            <Button type=\"primary\" shape=\"circle\" icon={<DownloadOutlined />} />\n            <Button type=\"primary\" shape=\"round\" icon={<DownloadOutlined />} />\n            <Button type=\"primary\" shape=\"round\" icon={<DownloadOutlined />}>\n              Download\n            </Button>\n            <Button type=\"primary\" icon={<DownloadOutlined />}>\n              Download\n            </Button>\n            <br />\n            <Button.Group>\n              <Button type=\"primary\">\n                <LeftOutlined />\n                Backward\n              </Button>\n              <Button type=\"primary\">\n                Forward\n                <RightOutlined />\n              </Button>\n            </Button.Group>\n            <Button type=\"primary\" loading>\n              Loading\n            </Button>\n            <Button type=\"primary\" size=\"small\" loading>\n              Loading\n            </Button>\n          </div>\n        </Col>\n        <Col span={12}>\n          <Divider orientation=\"left\">Tree example</Divider>\n          <Tree\n            showLine\n            checkable\n            defaultExpandedKeys={['0-0-0', '0-0-1']}\n            defaultSelectedKeys={['0-0-0', '0-0-1']}\n            defaultCheckedKeys={['0-0-0', '0-0-1']}\n          >\n            <TreeNode title=\"parent 1\" key=\"0-0\">\n              <TreeNode title=\"parent 1-0\" key=\"0-0-0\" disabled>\n                <TreeNode title=\"leaf\" key=\"0-0-0-0\" disableCheckbox />\n                <TreeNode title=\"leaf\" key=\"0-0-0-1\" />\n              </TreeNode>\n              <TreeNode title=\"parent 1-1\" key=\"0-0-1\">\n                <TreeNode title={<span style={{ color: '#1677ff' }}>sss</span>} key=\"0-0-1-0\" />\n              </TreeNode>\n            </TreeNode>\n          </Tree>\n        </Col>\n      </Row>\n      <br />\n      <Row>\n        <Col span={24}>\n          <Divider orientation=\"left\">Input (Input Group) example</Divider>\n          <InputGroup size=\"large\">\n            <Row gutter={8}>\n              <Col span={5}>\n                <Input defaultValue=\"0571\" />\n              </Col>\n              <Col span={8}>\n                <Input defaultValue=\"26888888\" />\n              </Col>\n            </Row>\n          </InputGroup>\n          <br />\n          <InputGroup compact>\n            <Input style={{ width: '20%' }} defaultValue=\"0571\" />\n            <Input style={{ width: '30%' }} defaultValue=\"26888888\" />\n          </InputGroup>\n          <br />\n          <InputGroup compact>\n            <Select defaultValue=\"Option1\">\n              <Option value=\"Option1\">Option1</Option>\n              <Option value=\"Option2\">Option2</Option>\n            </Select>\n            <Input style={{ width: '50%' }} defaultValue=\"input content\" />\n            <InputNumber />\n          </InputGroup>\n          <br />\n          <Search placeholder=\"input search text\" enterButton=\"Search\" size=\"large\" />\n          <br />\n          <br />\n          <div style={{ marginBottom: 16 }}>\n            <Input addonBefore={selectBefore} addonAfter={selectAfter} defaultValue=\"mysite\" />\n          </div>\n          <br />\n          <Row>\n            <Col span={12}>\n              <Divider orientation=\"left\">Select example</Divider>\n              <Space wrap>\n                <Select mode=\"multiple\" defaultValue=\"\" style={{ width: 120 }}>\n                  <Option value=\"jack\">Jack</Option>\n                  <Option value=\"\"></Option>\n                  <Option value=\"disabled\" disabled>\n                    Disabled\n                  </Option>\n                  <Option value=\"Yiminghe\">yiminghe</Option>\n                </Select>\n                <Select defaultValue=\"\" style={{ width: 120 }} disabled>\n                  <Option value=\"\"></Option>\n                </Select>\n                <Select defaultValue=\"\" style={{ width: 120 }} loading>\n                  <Option value=\"\"></Option>\n                </Select>\n                <Select showSearch style={{ width: 200 }} placeholder=\"Select a person\">\n                  <Option value=\"jack\">Jack</Option>\n                  <Option value=\"\"></Option>\n                  <Option value=\"tom\">Tom</Option>\n                </Select>\n              </Space>\n            </Col>\n            <Col span={12}>\n              <Divider orientation=\"left\">TreeSelect example</Divider>\n              <TreeSelect\n                showSearch\n                style={{ width: '100%' }}\n                dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}\n                placeholder=\"Please select\"\n                allowClear\n                treeDefaultExpandAll\n              >\n                <TreeNode title=\"parent 1\" key=\"0-1\">\n                  <TreeNode title=\"parent 1-0\" key=\"0-1-1\">\n                    <TreeNode title=\"my leaf\" key=\"random\" />\n                    <TreeNode title=\"your leaf\" key=\"random1\" />\n                  </TreeNode>\n                  <TreeNode title=\"parent 1-1\" key=\"random2\">\n                    <TreeNode title={<b style={{ color: '#08c' }}>sss</b>} key=\"random3\" />\n                  </TreeNode>\n                </TreeNode>\n              </TreeSelect>\n            </Col>\n          </Row>\n          <br />\n          <Row>\n            <Col span={24}>\n              <Divider orientation=\"left\">Modal example</Divider>\n              <Button type=\"primary\" onClick={showModal}>\n                Open Modal\n              </Button>\n              <Modal title=\" \" open={modalOpen} onOk={handleOk} onCancel={handleCancel}>\n                <p>    </p>\n                <p>    </p>\n                <p>    </p>\n              </Modal>\n            </Col>\n          </Row>\n          <br />\n          <Row>\n            <Col span={24}>\n              <Divider orientation=\"left\">Steps example</Divider>\n              <Steps\n                progressDot\n                current={currentStep}\n                items={[\n                  {\n                    title: 'Finished',\n                    description: 'This is a description.',\n                  },\n                  {\n                    title: 'In Progress',\n                    description: 'This is a description.',\n                  },\n                  {\n                    title: 'Waiting',\n                    description: 'This is a description.',\n                  },\n                ]}\n              />\n              <br />\n              <Steps\n                current={currentStep}\n                onChange={onStepsChange}\n                items={[\n                  {\n                    title: 'Step 1',\n                    description: 'This is a description.',\n                  },\n                  {\n                    title: 'Step 2',\n                    description: 'This is a description.',\n                  },\n                  {\n                    title: 'Step 3',\n                    description: 'This is a description.',\n                  },\n                ]}\n              />\n            </Col>\n          </Row>\n          <br />\n          <Row>\n            <Col span={12}>\n              <Divider orientation=\"left\">Rate example</Divider>\n              <Rate defaultValue={2.5} />\n              <br />\n              <strong>* Note:</strong> Half star not implemented in RTL direction, it will be\n              supported after <a href=\"https://github.com/react-component/rate\">rc-rate</a>{' '}\n              implement rtl support.\n            </Col>\n            <Col span={12}>\n              <Divider orientation=\"left\">Badge example</Divider>\n              <Badge count={badgeCount}>\n                <a href=\"#\" className=\"head-example\" />\n              </Badge>\n              <ButtonGroup>\n                <Button onClick={declineBadge}>\n                  <MinusOutlined />\n                </Button>\n                <Button onClick={increaseBadge}>\n                  <PlusOutlined />\n                </Button>\n              </ButtonGroup>\n              <div style={{ marginTop: 12 }}>\n                <Badge dot={showBadge}>\n                  <a href=\"#\" className=\"head-example\" />\n                </Badge>\n                <Switch onChange={onChangeBadge} checked={showBadge} />\n              </div>\n            </Col>\n          </Row>\n        </Col>\n      </Row>\n      <br />\n      <br />\n      <Row>\n        <Col span={24}>\n          <Divider orientation=\"left\">Pagination example</Divider>\n          <Pagination showSizeChanger defaultCurrent={3} total={500} />\n        </Col>\n      </Row>\n      <br />\n      <Row>\n        <Col span={24}>\n          <Divider orientation=\"left\">Grid System example</Divider>\n          <div className=\"grid-demo\">\n            <div className=\"code-box-demo\">\n              <p>\n                <strong>* Note:</strong> Every calculation in RTL grid system is from right side\n                (offset, push, etc.)\n              </p>\n              <Row>\n                <Col span={8}>col-8</Col>\n                <Col span={8} offset={8}>\n                  col-8\n                </Col>\n              </Row>\n              <Row>\n                <Col span={6} offset={6}>\n                  col-6 col-offset-6\n                </Col>\n                <Col span={6} offset={6}>\n                  col-6 col-offset-6\n                </Col>\n              </Row>\n              <Row>\n                <Col span={12} offset={6}>\n                  col-12 col-offset-6\n                </Col>\n              </Row>\n              <Row>\n                <Col span={18} push={6}>\n                  col-18 col-push-6\n                </Col>\n                <Col span={6} pull={18}>\n                  col-6 col-pull-18\n                </Col>\n              </Row>\n            </div>\n          </div>\n        </Col>\n      </Row>\n    </div>\n  );\n};\n\nconst App: React.FC = () => {\n  const [direction, setDirection] = useState<DirectionType>('ltr');\n  const [placement, setPlacement] = useState<Placement>('bottomLeft');\n\n  const changeDirection = (e: RadioChangeEvent) => {\n    const directionValue = e.target.value;\n    setDirection(directionValue);\n    setPlacement(directionValue === 'rtl' ? 'bottomRight' : 'bottomLeft');\n  };\n\n  return (\n    <>\n      <div style={{ marginBottom: 16 }}>\n        <span style={{ marginInlineEnd: 16 }}>Change direction of components:</span>\n        <Radio.Group defaultValue=\"ltr\" onChange={changeDirection}>\n          <Radio.Button key=\"ltr\" value=\"ltr\">\n            LTR\n          </Radio.Button>\n          <Radio.Button key=\"rtl\" value=\"rtl\">\n            RTL\n          </Radio.Button>\n        </Radio.Group>\n      </div>\n      <ConfigProvider direction={direction}>\n        <Page placement={placement} />\n      </ConfigProvider>\n    </>\n  );\n};\n\nexport default App;\n";;
  