  export default "import React, { useState } from 'react';\nimport type { CascaderProps } from 'antd';\nimport { Cascader } from 'antd';\n\ninterface Option {\n  value: string;\n  label: string;\n  children?: Option[];\n}\n\nconst options: Option[] = [\n  {\n    value: 'zhejiang',\n    label: 'Zhejiang',\n    children: [\n      {\n        value: 'hangzhou',\n        label: 'Hangzhou',\n      },\n    ],\n  },\n  {\n    value: 'jiangsu',\n    label: 'Jiangsu',\n    children: [\n      {\n        value: 'nanjing',\n        label: 'Nanjing',\n      },\n    ],\n  },\n];\n\nconst App: React.FC = () => {\n  const [text, setText] = useState('Unselect');\n\n  const onChange: CascaderProps<Option>['onChange'] = (_, selectedOptions) => {\n    setText(selectedOptions.map((o) => o.label).join(', '));\n  };\n\n  return (\n    <span>\n      {text}\n      &nbsp;\n      <Cascader options={options} onChange={onChange}>\n        <a>Change city</a>\n      </Cascader>\n    </span>\n  );\n};\n\nexport default App;\n";;
  