
  import '/Users/vagusx/Projects/ant-design/components/form/demo/global-state.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { Form, Input, Typography } from 'antd';\n\nconst { Paragraph } = Typography;\n\ninterface FieldData {\n  name: string | number | (string | number)[];\n  value?: any;\n  touched?: boolean;\n  validating?: boolean;\n  errors?: string[];\n}\n\ninterface CustomizedFormProps {\n  onChange: (fields: FieldData[]) => void;\n  fields: FieldData[];\n}\n\nconst CustomizedForm: React.FC<CustomizedFormProps> = ({ onChange, fields }) => (\n  <Form\n    name=\"global_state\"\n    layout=\"inline\"\n    fields={fields}\n    onFieldsChange={(_, allFields) => {\n      onChange(allFields);\n    }}\n  >\n    <Form.Item\n      name=\"username\"\n      label=\"Username\"\n      rules={[{ required: true, message: 'Username is required!' }]}\n    >\n      <Input />\n    </Form.Item>\n  </Form>\n);\n\nconst App: React.FC = () => {\n  const [fields, setFields] = useState<FieldData[]>([{ name: ['username'], value: 'Ant Design' }]);\n\n  return (\n    <>\n      <CustomizedForm\n        fields={fields}\n        onChange={(newFields) => {\n          setFields(newFields);\n        }}\n      />\n      <Paragraph style={{ maxWidth: 440, marginTop: 24 }}>\n        <pre style={{ border: 'none' }}>{JSON.stringify(fields, null, 2)}</pre>\n      </Paragraph>\n    </>\n  );\n};\n\nexport default App;\n";;
  