
  import '/Users/vagusx/Projects/ant-design/components/table/demo/resizable-column.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { Table } from 'antd';\nimport type { TableColumnsType } from 'antd';\nimport type { ResizeCallbackData } from 'react-resizable';\nimport { Resizable } from 'react-resizable';\n\ninterface DataType {\n  key: React.Key;\n  date: string;\n  amount: number;\n  type: string;\n  note: string;\n}\n\nconst ResizableTitle = (\n  props: React.HTMLAttributes<any> & {\n    onResize: (e: React.SyntheticEvent<Element>, data: ResizeCallbackData) => void;\n    width: number;\n  },\n) => {\n  const { onResize, width, ...restProps } = props;\n\n  if (!width) {\n    return <th {...restProps} />;\n  }\n\n  return (\n    <Resizable\n      width={width}\n      height={0}\n      handle={\n        <span\n          className=\"react-resizable-handle\"\n          onClick={(e) => {\n            e.stopPropagation();\n          }}\n        />\n      }\n      onResize={onResize}\n      draggableOpts={{ enableUserSelectHack: false }}\n    >\n      <th {...restProps} />\n    </Resizable>\n  );\n};\n\nconst App: React.FC = () => {\n  const [columns, setColumns] = useState<TableColumnsType<DataType>>([\n    {\n      title: 'Date',\n      dataIndex: 'date',\n      width: 200,\n    },\n    {\n      title: 'Amount',\n      dataIndex: 'amount',\n      width: 100,\n      sorter: (a, b) => a.amount - b.amount,\n    },\n    {\n      title: 'Type',\n      dataIndex: 'type',\n      width: 100,\n    },\n    {\n      title: 'Note',\n      dataIndex: 'note',\n      width: 100,\n    },\n    {\n      title: 'Action',\n      key: 'action',\n      render: () => <a>Delete</a>,\n    },\n  ]);\n  const data: DataType[] = [\n    {\n      key: 0,\n      date: '2018-02-11',\n      amount: 120,\n      type: 'income',\n      note: 'transfer',\n    },\n    {\n      key: 1,\n      date: '2018-03-11',\n      amount: 243,\n      type: 'income',\n      note: 'transfer',\n    },\n    {\n      key: 2,\n      date: '2018-04-11',\n      amount: 98,\n      type: 'income',\n      note: 'transfer',\n    },\n  ];\n\n  const handleResize =\n    (index: number) =>\n    (_: React.SyntheticEvent<Element>, { size }: ResizeCallbackData) => {\n      const newColumns = [...columns];\n      newColumns[index] = {\n        ...newColumns[index],\n        width: size.width,\n      };\n      setColumns(newColumns);\n    };\n\n  const mergedColumns = columns.map<TableColumnsType<DataType>[number]>((col, index) => ({\n    ...col,\n    onHeaderCell: (column: TableColumnsType<DataType>[number]) => ({\n      width: column.width,\n      onResize: handleResize(index) as React.ReactEventHandler<any>,\n    }),\n  }));\n\n  return (\n    <Table\n      bordered\n      components={{\n        header: {\n          cell: ResizableTitle,\n        },\n      }}\n      columns={mergedColumns}\n      dataSource={data}\n    />\n  );\n};\n\nexport default App;\n";;
  