
  import '/Users/vagusx/Projects/ant-design/components/modal/demo/footer.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { Button, Modal } from 'antd';\n\nconst App: React.FC = () => {\n  const [loading, setLoading] = useState(false);\n  const [open, setOpen] = useState(false);\n\n  const showModal = () => {\n    setOpen(true);\n  };\n\n  const handleOk = () => {\n    setLoading(true);\n    setTimeout(() => {\n      setLoading(false);\n      setOpen(false);\n    }, 3000);\n  };\n\n  const handleCancel = () => {\n    setOpen(false);\n  };\n\n  return (\n    <>\n      <Button type=\"primary\" onClick={showModal}>\n        Open Modal with customized footer\n      </Button>\n      <Modal\n        open={open}\n        title=\"Title\"\n        onOk={handleOk}\n        onCancel={handleCancel}\n        footer={[\n          <Button key=\"back\" onClick={handleCancel}>\n            Return\n          </Button>,\n          <Button key=\"submit\" type=\"primary\" loading={loading} onClick={handleOk}>\n            Submit\n          </Button>,\n          <Button\n            key=\"link\"\n            href=\"https://google.com\"\n            type=\"primary\"\n            loading={loading}\n            onClick={handleOk}\n          >\n            Search on Google\n          </Button>,\n        ]}\n      >\n        <p>Some contents...</p>\n        <p>Some contents...</p>\n        <p>Some contents...</p>\n        <p>Some contents...</p>\n        <p>Some contents...</p>\n      </Modal>\n    </>\n  );\n};\n\nexport default App;\n";;
  