
  import '/Users/vagusx/Projects/ant-design/components/modal/demo/modal-render.tsx?watch=parent';
  export default "import React, { useRef, useState } from 'react';\nimport { Button, Modal } from 'antd';\nimport type { DraggableData, DraggableEvent } from 'react-draggable';\nimport Draggable from 'react-draggable';\n\nconst App: React.FC = () => {\n  const [open, setOpen] = useState(false);\n  const [disabled, setDisabled] = useState(true);\n  const [bounds, setBounds] = useState({ left: 0, top: 0, bottom: 0, right: 0 });\n  const draggleRef = useRef<HTMLDivElement>(null);\n\n  const showModal = () => {\n    setOpen(true);\n  };\n\n  const handleOk = (e: React.MouseEvent<HTMLElement>) => {\n    console.log(e);\n    setOpen(false);\n  };\n\n  const handleCancel = (e: React.MouseEvent<HTMLElement>) => {\n    console.log(e);\n    setOpen(false);\n  };\n\n  const onStart = (_event: DraggableEvent, uiData: DraggableData) => {\n    const { clientWidth, clientHeight } = window.document.documentElement;\n    const targetRect = draggleRef.current?.getBoundingClientRect();\n    if (!targetRect) {\n      return;\n    }\n    setBounds({\n      left: -targetRect.left + uiData.x,\n      right: clientWidth - (targetRect.right - uiData.x),\n      top: -targetRect.top + uiData.y,\n      bottom: clientHeight - (targetRect.bottom - uiData.y),\n    });\n  };\n\n  return (\n    <>\n      <Button onClick={showModal}>Open Draggable Modal</Button>\n      <Modal\n        title={\n          <div\n            style={{ width: '100%', cursor: 'move' }}\n            onMouseOver={() => {\n              if (disabled) {\n                setDisabled(false);\n              }\n            }}\n            onMouseOut={() => {\n              setDisabled(true);\n            }}\n            // fix eslintjsx-a11y/mouse-events-have-key-events\n            // https://github.com/jsx-eslint/eslint-plugin-jsx-a11y/blob/master/docs/rules/mouse-events-have-key-events.md\n            onFocus={() => {}}\n            onBlur={() => {}}\n            // end\n          >\n            Draggable Modal\n          </div>\n        }\n        open={open}\n        onOk={handleOk}\n        onCancel={handleCancel}\n        modalRender={(modal) => (\n          <Draggable\n            disabled={disabled}\n            bounds={bounds}\n            nodeRef={draggleRef}\n            onStart={(event, uiData) => onStart(event, uiData)}\n          >\n            <div ref={draggleRef}>{modal}</div>\n          </Draggable>\n        )}\n      >\n        <p>\n          Just don&apos;t learn physics at school and your life will be full of magic and miracles.\n        </p>\n        <br />\n        <p>Day before yesterday I saw a rabbit, and yesterday a deer, and today, you.</p>\n      </Modal>\n    </>\n  );\n};\n\nexport default App;\n";;
  