  export default "import React, { useState } from 'react';\nimport { Transfer } from 'antd';\nimport type { TransferProps } from 'antd';\n\ninterface RecordType {\n  key: string;\n  title: string;\n  description: string;\n}\n\nconst mockData: RecordType[] = Array.from({ length: 10 }).map((_, i) => ({\n  key: i.toString(),\n  title: `content${i + 1}`,\n  description: `description of content${i + 1}`,\n}));\n\nconst oriTargetKeys = mockData.filter((item) => Number(item.key) % 3 > 1).map((item) => item.key);\n\nconst selectAllLabels: TransferProps['selectAllLabels'] = [\n  'Select All',\n  ({ selectedCount, totalCount }) => `${selectedCount}/${totalCount}`,\n];\n\nconst App: React.FC = () => {\n  const [targetKeys, setTargetKeys] = useState<React.Key[]>(oriTargetKeys);\n  return (\n    <Transfer\n      dataSource={mockData}\n      targetKeys={targetKeys}\n      onChange={setTargetKeys}\n      render={(item) => item.title}\n      selectAllLabels={selectAllLabels}\n    />\n  );\n};\n\nexport default App;\n";;
  