
  import '/Users/vagusx/Projects/ant-design/components/segmented/demo/dynamic.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { Button, Flex, Segmented } from 'antd';\n\nconst Demo: React.FC = () => {\n  const [options, setOptions] = useState(['Daily', 'Weekly', 'Monthly']);\n  const [moreLoaded, setMoreLoaded] = useState(false);\n\n  const handleLoadOptions = () => {\n    setOptions((prev) => [...prev, 'Quarterly', 'Yearly']);\n    setMoreLoaded(true);\n  };\n\n  return (\n    <Flex gap=\"small\" align=\"flex-start\" vertical>\n      <Segmented options={options} />\n      <Button type=\"primary\" disabled={moreLoaded} onClick={handleLoadOptions}>\n        Load more options\n      </Button>\n    </Flex>\n  );\n};\n\nexport default Demo;\n";;
  