
  import '/Users/vagusx/Projects/ant-design/components/config-provider/demo/holderRender.tsx?watch=parent';
  export default "import React, { useContext, useLayoutEffect } from 'react';\nimport { StyleProvider } from '@ant-design/cssinjs';\nimport { ExclamationCircleFilled } from '@ant-design/icons';\nimport { App, Button, ConfigProvider, message, Modal, notification, Space } from 'antd';\n\nconst Demo: React.FC = () => {\n  const { locale, theme } = useContext(ConfigProvider.ConfigContext);\n  useLayoutEffect(() => {\n    ConfigProvider.config({\n      holderRender: (children) => (\n        <StyleProvider hashPriority=\"high\">\n          <ConfigProvider prefixCls=\"static\" iconPrefixCls=\"icon\" locale={locale} theme={theme}>\n            <App message={{ maxCount: 1 }} notification={{ maxCount: 1 }}>\n              {children}\n            </App>\n          </ConfigProvider>\n        </StyleProvider>\n      ),\n    });\n  }, [locale, theme]);\n\n  return (\n    <div>\n      <Space>\n        <Button\n          type=\"primary\"\n          onClick={() => {\n            message.info('This is a normal message');\n          }}\n        >\n          message\n        </Button>\n        <Button\n          type=\"primary\"\n          onClick={() => {\n            notification.open({\n              message: 'Notification Title',\n              description:\n                'This is the content of the notification. This is the content of the notification. This is the content of the notification.',\n            });\n          }}\n        >\n          notification\n        </Button>\n        <Button\n          type=\"primary\"\n          onClick={() => {\n            Modal.confirm({\n              title: 'Do you want to delete these items?',\n              icon: <ExclamationCircleFilled />,\n              content: 'Some descriptions',\n            });\n          }}\n        >\n          Modal\n        </Button>\n      </Space>\n    </div>\n  );\n};\n\nexport default Demo;\n";;
  