
  import '/Users/vagusx/Projects/ant-design/components/table/demo/jsx.tsx?watch=parent';
  export default "import React from 'react';\nimport { Space, Table, Tag } from 'antd';\n\nconst { Column, ColumnGroup } = Table;\n\ninterface DataType {\n  key: React.Key;\n  firstName: string;\n  lastName: string;\n  age: number;\n  address: string;\n  tags: string[];\n}\n\nconst data: DataType[] = [\n  {\n    key: '1',\n    firstName: 'John',\n    lastName: 'Brown',\n    age: 32,\n    address: 'New York No. 1 Lake Park',\n    tags: ['nice', 'developer'],\n  },\n  {\n    key: '2',\n    firstName: 'Jim',\n    lastName: 'Green',\n    age: 42,\n    address: 'London No. 1 Lake Park',\n    tags: ['loser'],\n  },\n  {\n    key: '3',\n    firstName: 'Joe',\n    lastName: 'Black',\n    age: 32,\n    address: 'Sydney No. 1 Lake Park',\n    tags: ['cool', 'teacher'],\n  },\n];\n\nconst App: React.FC = () => (\n  <Table dataSource={data}>\n    <ColumnGroup title=\"Name\">\n      <Column title=\"First Name\" dataIndex=\"firstName\" key=\"firstName\" />\n      <Column title=\"Last Name\" dataIndex=\"lastName\" key=\"lastName\" />\n    </ColumnGroup>\n    <Column title=\"Age\" dataIndex=\"age\" key=\"age\" />\n    <Column title=\"Address\" dataIndex=\"address\" key=\"address\" />\n    <Column\n      title=\"Tags\"\n      dataIndex=\"tags\"\n      key=\"tags\"\n      render={(tags: string[]) => (\n        <>\n          {tags.map((tag) => {\n            let color = tag.length > 5 ? 'geekblue' : 'green';\n            if (tag === 'loser') {\n              color = 'volcano';\n            }\n            return (\n              <Tag color={color} key={tag}>\n                {tag.toUpperCase()}\n              </Tag>\n            );\n          })}\n        </>\n      )}\n    />\n    <Column\n      title=\"Action\"\n      key=\"action\"\n      render={(_: any, record: DataType) => (\n        <Space size=\"middle\">\n          <a>Invite {record.lastName}</a>\n          <a>Delete</a>\n        </Space>\n      )}\n    />\n  </Table>\n);\n\nexport default App;\n";;
  