
  import '/Users/vagusx/Projects/ant-design/components/dropdown/demo/loading.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { DownOutlined } from '@ant-design/icons';\nimport type { MenuProps } from 'antd';\nimport { Dropdown, Space } from 'antd';\n\nconst items: MenuProps['items'] = [\n  {\n    label: 'Submit and continue',\n    key: '1',\n  },\n];\n\nconst App: React.FC = () => {\n  const [loadings, setLoadings] = useState<boolean[]>([]);\n\n  const enterLoading = (index: number) => {\n    setLoadings((state) => {\n      const newLoadings = [...state];\n      newLoadings[index] = true;\n      return newLoadings;\n    });\n\n    setTimeout(() => {\n      setLoadings((state) => {\n        const newLoadings = [...state];\n        newLoadings[index] = false;\n        return newLoadings;\n      });\n    }, 6000);\n  };\n\n  return (\n    <Space direction=\"vertical\">\n      <Dropdown.Button type=\"primary\" loading menu={{ items }}>\n        Submit\n      </Dropdown.Button>\n      <Dropdown.Button type=\"primary\" size=\"small\" loading menu={{ items }}>\n        Submit\n      </Dropdown.Button>\n      <Dropdown.Button\n        type=\"primary\"\n        loading={loadings[0]}\n        menu={{ items }}\n        onClick={() => enterLoading(0)}\n      >\n        Submit\n      </Dropdown.Button>\n      <Dropdown.Button\n        icon={<DownOutlined />}\n        loading={loadings[1]}\n        menu={{ items }}\n        onClick={() => enterLoading(1)}\n      >\n        Submit\n      </Dropdown.Button>\n    </Space>\n  );\n};\n\nexport default App;\n";;
  