  export default "import React from 'react';\nimport {\n  DownOutlined,\n  FrownFilled,\n  FrownOutlined,\n  MehOutlined,\n  SmileOutlined,\n} from '@ant-design/icons';\nimport { Tree } from 'antd';\nimport type { TreeDataNode } from 'antd';\n\nconst treeData: TreeDataNode[] = [\n  {\n    title: 'parent 1',\n    key: '0-0',\n    icon: <SmileOutlined />,\n    children: [\n      {\n        title: 'leaf',\n        key: '0-0-0',\n        icon: <MehOutlined />,\n      },\n      {\n        title: 'leaf',\n        key: '0-0-1',\n        icon: ({ selected }) => (selected ? <FrownFilled /> : <FrownOutlined />),\n      },\n    ],\n  },\n];\n\nconst App: React.FC = () => (\n  <Tree\n    showIcon\n    defaultExpandAll\n    defaultSelectedKeys={['0-0-0']}\n    switcherIcon={<DownOutlined />}\n    treeData={treeData}\n  />\n);\n\nexport default App;\n";;
  