  export default "import React, { useState } from 'react';\nimport { Flex, Switch, Table, Tag, Transfer } from 'antd';\nimport type { GetProp, TableColumnsType, TableProps, TransferProps } from 'antd';\n\ntype TransferItem = GetProp<TransferProps, 'dataSource'>[number];\ntype TableRowSelection<T extends object> = TableProps<T>['rowSelection'];\n\ninterface DataType {\n  key: string;\n  title: string;\n  description: string;\n  tag: string;\n}\n\ninterface TableTransferProps extends TransferProps<TransferItem> {\n  dataSource: DataType[];\n  leftColumns: TableColumnsType<DataType>;\n  rightColumns: TableColumnsType<DataType>;\n}\n\n// Customize Table Transfer\nconst TableTransfer: React.FC<TableTransferProps> = (props) => {\n  const { leftColumns, rightColumns, ...restProps } = props;\n  return (\n    <Transfer style={{ width: '100%' }} {...restProps}>\n      {({\n        direction,\n        filteredItems,\n        onItemSelect,\n        onItemSelectAll,\n        selectedKeys: listSelectedKeys,\n        disabled: listDisabled,\n      }) => {\n        const columns = direction === 'left' ? leftColumns : rightColumns;\n        const rowSelection: TableRowSelection<TransferItem> = {\n          getCheckboxProps: () => ({ disabled: listDisabled }),\n          onChange(selectedRowKeys) {\n            onItemSelectAll(selectedRowKeys, 'replace');\n          },\n          selectedRowKeys: listSelectedKeys,\n          selections: [Table.SELECTION_ALL, Table.SELECTION_INVERT, Table.SELECTION_NONE],\n        };\n\n        return (\n          <Table\n            rowSelection={rowSelection}\n            columns={columns}\n            dataSource={filteredItems}\n            size=\"small\"\n            style={{ pointerEvents: listDisabled ? 'none' : undefined }}\n            onRow={({ key, disabled: itemDisabled }) => ({\n              onClick: () => {\n                if (itemDisabled || listDisabled) {\n                  return;\n                }\n                onItemSelect(key, !listSelectedKeys.includes(key));\n              },\n            })}\n          />\n        );\n      }}\n    </Transfer>\n  );\n};\n\nconst mockTags = ['cat', 'dog', 'bird'];\n\nconst mockData = Array.from({ length: 20 }).map<DataType>((_, i) => ({\n  key: i.toString(),\n  title: `content${i + 1}`,\n  description: `description of content${i + 1}`,\n  tag: mockTags[i % 3],\n}));\n\nconst columns: TableColumnsType<DataType> = [\n  {\n    dataIndex: 'title',\n    title: 'Name',\n  },\n  {\n    dataIndex: 'tag',\n    title: 'Tag',\n    render: (tag: string) => (\n      <Tag style={{ marginInlineEnd: 0 }} color=\"cyan\">\n        {tag.toUpperCase()}\n      </Tag>\n    ),\n  },\n  {\n    dataIndex: 'description',\n    title: 'Description',\n  },\n];\n\nconst filterOption = (input: string, item: DataType) =>\n  item.title?.includes(input) || item.tag?.includes(input);\n\nconst App: React.FC = () => {\n  const [targetKeys, setTargetKeys] = useState<TransferProps['targetKeys']>([]);\n  const [disabled, setDisabled] = useState(false);\n\n  const onChange: TableTransferProps['onChange'] = (nextTargetKeys) => {\n    setTargetKeys(nextTargetKeys);\n  };\n\n  const toggleDisabled = (checked: boolean) => {\n    setDisabled(checked);\n  };\n\n  return (\n    <Flex align=\"start\" gap=\"middle\" vertical>\n      <TableTransfer\n        dataSource={mockData}\n        targetKeys={targetKeys}\n        disabled={disabled}\n        showSearch\n        showSelectAll={false}\n        onChange={onChange}\n        filterOption={filterOption}\n        leftColumns={columns}\n        rightColumns={columns}\n      />\n      <Switch\n        unCheckedChildren=\"disabled\"\n        checkedChildren=\"disabled\"\n        checked={disabled}\n        onChange={toggleDisabled}\n      />\n    </Flex>\n  );\n};\n\nexport default App;\n";;
  