  export default "import React, { useState } from 'react';\nimport { TreeSelect } from 'antd';\nimport type { TreeSelectProps } from 'antd';\n\nconst treeData = [\n  {\n    value: 'parent 1',\n    title: 'parent 1',\n    children: [\n      {\n        value: 'parent 1-0',\n        title: 'parent 1-0',\n        children: [\n          {\n            value: 'leaf1',\n            title: 'leaf1',\n          },\n          {\n            value: 'leaf2',\n            title: 'leaf2',\n          },\n          {\n            value: 'leaf3',\n            title: 'leaf3',\n          },\n          {\n            value: 'leaf4',\n            title: 'leaf4',\n          },\n          {\n            value: 'leaf5',\n            title: 'leaf5',\n          },\n          {\n            value: 'leaf6',\n            title: 'leaf6',\n          },\n        ],\n      },\n      {\n        value: 'parent 1-1',\n        title: 'parent 1-1',\n        children: [\n          {\n            value: 'leaf11',\n            title: <b style={{ color: '#08c' }}>leaf11</b>,\n          },\n        ],\n      },\n    ],\n  },\n];\nconst App: React.FC = () => {\n  const [value, setValue] = useState<string>();\n\n  const onChange = (newValue: string) => {\n    setValue(newValue);\n  };\n\n  const onPopupScroll: TreeSelectProps['onPopupScroll'] = (e) => {\n    console.log('onPopupScroll', e);\n  };\n\n  return (\n    <TreeSelect\n      showSearch\n      style={{ width: '100%' }}\n      value={value}\n      dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}\n      placeholder=\"Please select\"\n      allowClear\n      treeDefaultExpandAll\n      onChange={onChange}\n      treeData={treeData}\n      onPopupScroll={onPopupScroll}\n    />\n  );\n};\n\nexport default App;\n";;
  