  export default "import React, { useState } from 'react';\nimport { closestCenter, DndContext, PointerSensor, useSensor, useSensors } from '@dnd-kit/core';\nimport type { DragEndEvent } from '@dnd-kit/core/dist/types/index';\nimport {\n  arrayMove,\n  horizontalListSortingStrategy,\n  SortableContext,\n  useSortable,\n} from '@dnd-kit/sortable';\nimport { Flex, Tag } from 'antd';\n\ninterface Item {\n  id: number;\n  text: string;\n}\n\ninterface DraggableTagProps {\n  tag: Item;\n}\n\nconst commonStyle: React.CSSProperties = {\n  cursor: 'move',\n  transition: 'unset', // Prevent element from shaking after drag\n};\n\nconst DraggableTag: React.FC<DraggableTagProps> = (props) => {\n  const { tag } = props;\n  const { listeners, transform, transition, isDragging, setNodeRef } = useSortable({ id: tag.id });\n\n  const style = transform\n    ? {\n        ...commonStyle,\n        transform: `translate3d(${transform.x}px, ${transform.y}px, 0)`,\n        transition: isDragging ? 'unset' : transition, // Improve performance/visual effect when dragging\n      }\n    : commonStyle;\n\n  return (\n    <Tag style={style} ref={setNodeRef} {...listeners}>\n      {tag.text}\n    </Tag>\n  );\n};\n\nconst App: React.FC = () => {\n  const [items, setItems] = useState<Item[]>([\n    { id: 1, text: 'Tag 1' },\n    { id: 2, text: 'Tag 2' },\n    { id: 3, text: 'Tag 3' },\n  ]);\n\n  const sensors = useSensors(useSensor(PointerSensor));\n\n  const handleDragEnd = (event: DragEndEvent) => {\n    const { active, over } = event;\n    if (!over) {\n      return;\n    }\n    if (active.id !== over.id) {\n      setItems((data) => {\n        const oldIndex = data.findIndex((item) => item.id === active.id);\n        const newIndex = data.findIndex((item) => item.id === over.id);\n        return arrayMove(data, oldIndex, newIndex);\n      });\n    }\n  };\n\n  return (\n    <DndContext sensors={sensors} onDragEnd={handleDragEnd} collisionDetection={closestCenter}>\n      <SortableContext items={items} strategy={horizontalListSortingStrategy}>\n        <Flex gap=\"4px 0\" wrap>\n          {items.map<React.ReactNode>((item) => (\n            <DraggableTag tag={item} key={item.id} />\n          ))}\n        </Flex>\n      </SortableContext>\n    </DndContext>\n  );\n};\n\nexport default App;\n";;
  