
  import '/Users/vagusx/Projects/ant-design/components/tooltip/demo/placement.tsx?watch=parent';
  export default "import React from 'react';\nimport { Button, ConfigProvider, Tooltip } from 'antd';\n\nconst text = <span>prompt text</span>;\n\nconst buttonWidth = 80;\n\nconst App: React.FC = () => (\n  <ConfigProvider\n    button={{\n      style: { width: buttonWidth, margin: 4 },\n    }}\n  >\n    <div className=\"demo\">\n      <div style={{ marginInlineStart: buttonWidth, whiteSpace: 'nowrap' }}>\n        <Tooltip placement=\"topLeft\" title={text}>\n          <Button>TL</Button>\n        </Tooltip>\n        <Tooltip placement=\"top\" title={text}>\n          <Button>Top</Button>\n        </Tooltip>\n        <Tooltip placement=\"topRight\" title={text}>\n          <Button>TR</Button>\n        </Tooltip>\n      </div>\n      <div style={{ width: buttonWidth, float: 'inline-start' }}>\n        <Tooltip placement=\"leftTop\" title={text}>\n          <Button>LT</Button>\n        </Tooltip>\n        <Tooltip placement=\"left\" title={text}>\n          <Button>Left</Button>\n        </Tooltip>\n        <Tooltip placement=\"leftBottom\" title={text}>\n          <Button>LB</Button>\n        </Tooltip>\n      </div>\n      <div style={{ width: buttonWidth, marginInlineStart: buttonWidth * 4 + 24 }}>\n        <Tooltip placement=\"rightTop\" title={text}>\n          <Button>RT</Button>\n        </Tooltip>\n        <Tooltip placement=\"right\" title={text}>\n          <Button>Right</Button>\n        </Tooltip>\n        <Tooltip placement=\"rightBottom\" title={text}>\n          <Button>RB</Button>\n        </Tooltip>\n      </div>\n      <div style={{ marginInlineStart: buttonWidth, clear: 'both', whiteSpace: 'nowrap' }}>\n        <Tooltip placement=\"bottomLeft\" title={text}>\n          <Button>BL</Button>\n        </Tooltip>\n        <Tooltip placement=\"bottom\" title={text}>\n          <Button>Bottom</Button>\n        </Tooltip>\n        <Tooltip placement=\"bottomRight\" title={text}>\n          <Button>BR</Button>\n        </Tooltip>\n      </div>\n    </div>\n  </ConfigProvider>\n);\n\nexport default App;\n";;
  