
  import '/Users/vagusx/Projects/ant-design/components/table/demo/hidden-columns.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { Checkbox, Divider, Table } from 'antd';\nimport type { CheckboxOptionType, TableColumnsType } from 'antd';\n\ninterface DataType {\n  key: React.Key;\n  name: string;\n  age: number;\n  address: string;\n}\n\nconst columns: TableColumnsType<DataType> = [\n  { title: 'Column 1', dataIndex: 'address', key: '1' },\n  { title: 'Column 2', dataIndex: 'address', key: '2' },\n  { title: 'Column 3', dataIndex: 'address', key: '3' },\n  { title: 'Column 4', dataIndex: 'address', key: '4' },\n  { title: 'Column 5', dataIndex: 'address', key: '5' },\n  { title: 'Column 6', dataIndex: 'address', key: '6' },\n  { title: 'Column 7', dataIndex: 'address', key: '7' },\n  { title: 'Column 8', dataIndex: 'address', key: '8' },\n];\n\nconst data: DataType[] = [\n  {\n    key: '1',\n    name: 'John Brown',\n    age: 32,\n    address: 'New York Park',\n  },\n  {\n    key: '2',\n    name: 'Jim Green',\n    age: 40,\n    address: 'London Park',\n  },\n];\n\nconst defaultCheckedList = columns.map((item) => item.key as string);\n\nconst App: React.FC = () => {\n  const [checkedList, setCheckedList] = useState(defaultCheckedList);\n\n  const options = columns.map(({ key, title }) => ({\n    label: title,\n    value: key,\n  }));\n\n  const newColumns = columns.map((item) => ({\n    ...item,\n    hidden: !checkedList.includes(item.key as string),\n  }));\n\n  return (\n    <>\n      <Divider>Columns displayed</Divider>\n      <Checkbox.Group\n        value={checkedList}\n        options={options as CheckboxOptionType[]}\n        onChange={(value) => {\n          setCheckedList(value as string[]);\n        }}\n      />\n\n      <Table columns={newColumns} dataSource={data} style={{ marginTop: 24 }} />\n    </>\n  );\n};\n\nexport default App;\n";;
  