  export default "import React, { useState } from 'react';\nimport { Button, Drawer } from 'antd';\n\nconst App: React.FC = () => {\n  const [open, setOpen] = useState(false);\n\n  const showDrawer = () => {\n    setOpen(true);\n  };\n\n  const onClose = () => {\n    setOpen(false);\n  };\n\n  return (\n    <>\n      <Button type=\"primary\" onClick={showDrawer}>\n        Open\n      </Button>\n      <Drawer title=\"Basic Drawer\" onClose={onClose} open={open}>\n        <p>Some contents...</p>\n        <p>Some contents...</p>\n        <p>Some contents...</p>\n      </Drawer>\n    </>\n  );\n};\n\nexport default App;\n";;
  