  export default "import React, { useState } from 'react';\nimport { InputNumber, Table } from 'antd';\nimport type { TableColumnsType, TableProps } from 'antd';\n\ntype TableRowSelection<T> = TableProps<T>['rowSelection'];\n\nconst RenderTimes = () => {\n  const timesRef = React.useRef(0);\n  timesRef.current += 1;\n\n  return <span>{timesRef.current}</span>;\n};\n\ninterface DataType {\n  key: React.Key;\n  name: string;\n  age: number;\n  address: string;\n}\n\nconst shouldCellUpdate = (record: any, prevRecord: any) => record !== prevRecord;\n\nconst columns: TableColumnsType<DataType> = [\n  {\n    title: 'Name',\n    dataIndex: 'name',\n    shouldCellUpdate,\n  },\n  {\n    title: 'Age',\n    dataIndex: 'age',\n    shouldCellUpdate,\n  },\n  {\n    title: 'Address',\n    dataIndex: 'address',\n    shouldCellUpdate,\n    render: (addr) => (\n      <>\n        {addr}\n        <RenderTimes />\n      </>\n    ),\n  },\n];\n\nfunction genData(count: number) {\n  const data: DataType[] = [];\n\n  for (let i = 0; i < count; i++) {\n    data.push({\n      key: i,\n      name: `Edward King ${i}`,\n      age: 32,\n      address: `London, Park Lane no. ${i}`,\n    });\n  }\n\n  return data;\n}\n\nconst App: React.FC = () => {\n  const [data, setData] = useState(genData(50));\n  const [selectedRowKeys, setSelectedRowKeys] = useState<React.Key[]>([]);\n\n  const onSelectChange = (newSelectedRowKeys: React.Key[]) => {\n    console.log('selectedRowKeys changed: ', newSelectedRowKeys);\n    setSelectedRowKeys(newSelectedRowKeys);\n  };\n\n  const rowSelection: TableRowSelection<DataType> = {\n    selectedRowKeys,\n    onChange: onSelectChange,\n  };\n\n  return (\n    <>\n      <InputNumber\n        value={data.length}\n        onChange={(cnt) => {\n          setData(genData(cnt || 0));\n        }}\n      />\n      <Table rowSelection={rowSelection} columns={columns} dataSource={data} pagination={false} />\n    </>\n  );\n};\n\nexport default App;\n";;
  