  export default "import React from 'react';\nimport { Mentions, Space } from 'antd';\nimport type { GetProp, MentionProps } from 'antd';\n\ntype MentionsOptionProps = GetProp<MentionProps, 'options'>[number];\n\nconst onChange = (value: string) => {\n  console.log('Change:', value);\n};\n\nconst onSelect = (option: MentionsOptionProps) => {\n  console.log('select', option);\n};\n\nconst App: React.FC = () => {\n  const options = [\n    {\n      value: 'afc163',\n      label: 'afc163',\n    },\n    {\n      value: 'zombieJ',\n      label: 'zombieJ',\n    },\n    {\n      value: 'yesmeck',\n      label: 'yesmeck',\n    },\n  ];\n\n  return (\n    <Space direction=\"vertical\">\n      <Mentions\n        onChange={onChange}\n        onSelect={onSelect}\n        defaultValue=\"@afc163\"\n        status=\"error\"\n        options={options}\n      />\n      <Mentions\n        onChange={onChange}\n        onSelect={onSelect}\n        defaultValue=\"@afc163\"\n        status=\"warning\"\n        options={options}\n      />\n    </Space>\n  );\n};\n\nexport default App;\n";;
  