
  import '/Users/vagusx/Projects/ant-design/components/upload/demo/file-type.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport {\n  FileExcelTwoTone,\n  FilePdfTwoTone,\n  FileWordTwoTone,\n  LoadingOutlined,\n  PaperClipOutlined,\n  PictureTwoTone,\n  PlusOutlined,\n} from '@ant-design/icons';\nimport { Image, Upload } from 'antd';\nimport type { GetProp, UploadFile, UploadProps } from 'antd';\n\ntype FileType = Parameters<GetProp<UploadProps, 'beforeUpload'>>[0];\n\nconst getBase64 = (file: FileType): Promise<string> =>\n  new Promise((resolve, reject) => {\n    const reader = new FileReader();\n    reader.readAsDataURL(file);\n    reader.onload = () => resolve(reader.result as string);\n    reader.onerror = (error) => reject(error);\n  });\n\nconst App: React.FC = () => {\n  const [previewOpen, setPreviewOpen] = useState(false);\n  const [previewImage, setPreviewImage] = useState('');\n  const [fileList, setFileList] = useState<UploadFile[]>([\n    {\n      uid: '-2',\n      name: 'pdf.pdf',\n      status: 'done',\n      url: 'http://cdn07.foxitsoftware.cn/pub/foxit/cpdf/FoxitCompanyProfile.pdf',\n    },\n    {\n      uid: '-3',\n      name: 'doc.doc',\n      status: 'done',\n      url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.doc',\n    },\n    {\n      uid: '-4',\n      name: 'image.png',\n      status: 'error',\n    },\n    {\n      uid: '-5',\n      name: 'pdf.pdf',\n      status: 'error',\n    },\n    {\n      uid: '-6',\n      name: 'doc.doc',\n      status: 'error',\n    },\n  ]);\n\n  const handlePreview = async (file: UploadFile) => {\n    if (!file.url && !file.preview) {\n      file.preview = await getBase64(file.originFileObj as FileType);\n    }\n\n    setPreviewOpen(true);\n    setPreviewImage(file.url || (file.preview as string));\n  };\n\n  const handleChange: UploadProps['onChange'] = ({ fileList: newFileList }) =>\n    setFileList(newFileList);\n\n  const handleIconRender: UploadProps['iconRender'] = (file, listType) => {\n    const fileSufIconList = [\n      { type: <FilePdfTwoTone />, suf: ['.pdf'] },\n      { type: <FileExcelTwoTone />, suf: ['.xlsx', '.xls', '.csv'] },\n      { type: <FileWordTwoTone />, suf: ['.doc', '.docx'] },\n      {\n        type: <PictureTwoTone />,\n        suf: ['.webp', '.svg', '.png', '.gif', '.jpg', '.jpeg', '.jfif', '.bmp', '.dpg'],\n      },\n    ];\n    // console.log(1, file, listType);\n    let icon = file.status === 'uploading' ? <LoadingOutlined /> : <PaperClipOutlined />;\n    if (listType === 'picture' || listType === 'picture-card' || listType === 'picture-circle') {\n      if (listType === 'picture-card' && file.status === 'uploading') {\n        icon = <LoadingOutlined />; // or icon = 'uploading...';\n      } else {\n        fileSufIconList.forEach((item) => {\n          if (item.suf.includes(file.name.slice(file.name.lastIndexOf('.')))) {\n            icon = item.type;\n          }\n        });\n      }\n    }\n    return icon;\n  };\n\n  const uploadButton = (\n    <button style={{ border: 0, background: 'none' }} type=\"button\">\n      <PlusOutlined />\n      <div style={{ marginTop: 8 }}>Upload</div>\n    </button>\n  );\n\n  return (\n    <>\n      <Upload\n        action=\"https://660d2bd96ddfa2943b33731c.mockapi.io/api/upload\"\n        listType=\"picture-card\"\n        fileList={fileList}\n        onPreview={handlePreview}\n        onChange={handleChange}\n        iconRender={handleIconRender}\n      >\n        {fileList.length >= 8 ? null : uploadButton}\n      </Upload>\n      {previewImage && (\n        <Image\n          wrapperStyle={{ display: 'none' }}\n          preview={{\n            visible: previewOpen,\n            onVisibleChange: (visible) => setPreviewOpen(visible),\n            afterOpenChange: (visible) => !visible && setPreviewImage(''),\n          }}\n          src={previewImage}\n        />\n      )}\n    </>\n  );\n};\n\nexport default App;\n";;
  