  export default "import React, { useEffect, useState } from 'react';\nimport { Anchor, Col, Row } from 'antd';\n\nconst style: React.CSSProperties = {\n  height: '30vh',\n  backgroundColor: 'rgba(0, 0, 0, 0.85)',\n  position: 'fixed',\n  top: 0,\n  insetInlineStart: 0,\n  width: '75%',\n  color: '#fff',\n};\n\nconst App: React.FC = () => {\n  const topRef = React.useRef<HTMLDivElement>(null);\n  const [targetOffset, setTargetOffset] = useState<number>();\n\n  useEffect(() => {\n    setTargetOffset(topRef.current?.clientHeight);\n  }, []);\n\n  return (\n    <div>\n      <Row>\n        <Col span={18}>\n          <div\n            id=\"part-1\"\n            style={{ height: '100vh', background: 'rgba(255,0,0,0.02)', marginTop: '30vh' }}\n          >\n            Part 1\n          </div>\n          <div id=\"part-2\" style={{ height: '100vh', background: 'rgba(0,255,0,0.02)' }}>\n            Part 2\n          </div>\n          <div id=\"part-3\" style={{ height: '100vh', background: 'rgba(0,0,255,0.02)' }}>\n            Part 3\n          </div>\n        </Col>\n        <Col span={6}>\n          <Anchor\n            targetOffset={targetOffset}\n            items={[\n              { key: 'part-1', href: '#part-1', title: 'Part 1' },\n              { key: 'part-2', href: '#part-2', title: 'Part 2' },\n              { key: 'part-3', href: '#part-3', title: 'Part 3' },\n            ]}\n          />\n        </Col>\n      </Row>\n      <div style={style} ref={topRef}>\n        <div>Fixed Top Block</div>\n      </div>\n    </div>\n  );\n};\n\nexport default App;\n";;
  