  export default "import React from 'react';\nimport { ConfigProvider, Select, Space } from 'antd';\nimport type { SelectProps } from 'antd';\n\nconst options: SelectProps['options'] = [];\n\nfor (let i = 10; i < 36; i++) {\n  options.push({\n    label: i.toString(36) + i,\n    value: i.toString(36) + i,\n  });\n}\n\nconst App: React.FC = () => (\n  <Space direction=\"vertical\">\n    <ConfigProvider\n      theme={{\n        components: {\n          Select: {\n            multipleItemBorderColor: 'rgba(0,0,0,0.06)',\n            multipleItemBorderColorDisabled: 'rgba(0,0,0,0.06)',\n            optionSelectedColor: '#1677ff',\n          },\n        },\n      }}\n    >\n      <Space style={{ width: '100%' }} direction=\"vertical\">\n        <Select\n          mode=\"multiple\"\n          allowClear\n          style={{ width: '100%' }}\n          placeholder=\"Please select\"\n          defaultValue={['a10', 'c12']}\n          options={options}\n        />\n        <Select\n          mode=\"multiple\"\n          disabled\n          style={{ width: '100%' }}\n          placeholder=\"Please select\"\n          defaultValue={['a10', 'c12']}\n          options={options}\n        />\n      </Space>\n    </ConfigProvider>\n    <ConfigProvider\n      theme={{\n        token: {\n          controlHeightSM: 28,\n        },\n      }}\n    >\n      <Space style={{ width: '100%' }} direction=\"vertical\">\n        <Select\n          mode=\"multiple\"\n          allowClear\n          size=\"small\"\n          style={{ width: '100%' }}\n          placeholder=\"Please select\"\n          defaultValue={['a10', 'c12']}\n          options={options}\n        />\n        <Select\n          mode=\"multiple\"\n          allowClear\n          style={{ width: '100%' }}\n          placeholder=\"Please select\"\n          defaultValue={['a10', 'c12']}\n          options={options}\n        />\n      </Space>\n    </ConfigProvider>\n    <ConfigProvider\n      theme={{\n        components: {\n          Select: {\n            paddingXXS: 0,\n            controlHeight: 28,\n          },\n        },\n      }}\n    >\n      <Space style={{ width: '100%' }} direction=\"vertical\">\n        <Select style={{ width: '100%' }} defaultValue=\"a10\" options={options} />\n        <Select\n          mode=\"multiple\"\n          style={{ width: '100%' }}\n          defaultValue={['a10', 'c12']}\n          options={options}\n        />\n      </Space>\n    </ConfigProvider>\n  </Space>\n);\n\nexport default App;\n";;
  