
  import '/Users/vagusx/Projects/ant-design/components/date-picker/demo/start-end.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { DatePicker, Space } from 'antd';\nimport type { Dayjs } from 'dayjs';\n\nconst App: React.FC = () => {\n  const [startValue, setStartValue] = useState<Dayjs | null>(null);\n  const [endValue, setEndValue] = useState<Dayjs | null>(null);\n  const [endOpen, setEndOpen] = useState(false);\n\n  const disabledStartDate = (startDate: Dayjs) => {\n    if (!startDate || !endValue) {\n      return false;\n    }\n    return startDate.valueOf() > endValue.valueOf();\n  };\n\n  const disabledEndDate = (endDate: Dayjs) => {\n    if (!endDate || !startValue) {\n      return false;\n    }\n    return endDate.valueOf() <= startValue.valueOf();\n  };\n\n  const handleStartOpenChange = (open: boolean) => {\n    if (!open) {\n      setEndOpen(true);\n    }\n  };\n\n  const handleEndOpenChange = (open: boolean) => {\n    setEndOpen(open);\n  };\n\n  return (\n    <Space>\n      <DatePicker\n        disabledDate={disabledStartDate}\n        showTime\n        format=\"YYYY-MM-DD HH:mm:ss\"\n        value={startValue}\n        placeholder=\"Start\"\n        onChange={setStartValue}\n        onOpenChange={handleStartOpenChange}\n      />\n      <DatePicker\n        disabledDate={disabledEndDate}\n        showTime\n        format=\"YYYY-MM-DD HH:mm:ss\"\n        value={endValue}\n        placeholder=\"End\"\n        onChange={setEndValue}\n        open={endOpen}\n        onOpenChange={handleEndOpenChange}\n      />\n    </Space>\n  );\n};\n\nexport default App;\n";;
  