
  import '/Users/vagusx/Projects/ant-design/components/tag/demo/control.tsx?watch=parent';
  export default "import React, { useEffect, useRef, useState } from 'react';\nimport { PlusOutlined } from '@ant-design/icons';\nimport type { InputRef } from 'antd';\nimport { Flex, Input, Tag, theme, Tooltip } from 'antd';\n\nconst tagInputStyle: React.CSSProperties = {\n  width: 64,\n  height: 22,\n  marginInlineEnd: 8,\n  verticalAlign: 'top',\n};\n\nconst App: React.FC = () => {\n  const { token } = theme.useToken();\n  const [tags, setTags] = useState<string[]>(['Unremovable', 'Tag 2', 'Tag 3']);\n  const [inputVisible, setInputVisible] = useState(false);\n  const [inputValue, setInputValue] = useState('');\n  const [editInputIndex, setEditInputIndex] = useState(-1);\n  const [editInputValue, setEditInputValue] = useState('');\n  const inputRef = useRef<InputRef>(null);\n  const editInputRef = useRef<InputRef>(null);\n\n  useEffect(() => {\n    if (inputVisible) {\n      inputRef.current?.focus();\n    }\n  }, [inputVisible]);\n\n  useEffect(() => {\n    editInputRef.current?.focus();\n  }, [editInputValue]);\n\n  const handleClose = (removedTag: string) => {\n    const newTags = tags.filter((tag) => tag !== removedTag);\n    console.log(newTags);\n    setTags(newTags);\n  };\n\n  const showInput = () => {\n    setInputVisible(true);\n  };\n\n  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {\n    setInputValue(e.target.value);\n  };\n\n  const handleInputConfirm = () => {\n    if (inputValue && !tags.includes(inputValue)) {\n      setTags([...tags, inputValue]);\n    }\n    setInputVisible(false);\n    setInputValue('');\n  };\n\n  const handleEditInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {\n    setEditInputValue(e.target.value);\n  };\n\n  const handleEditInputConfirm = () => {\n    const newTags = [...tags];\n    newTags[editInputIndex] = editInputValue;\n    setTags(newTags);\n    setEditInputIndex(-1);\n    setEditInputValue('');\n  };\n\n  const tagPlusStyle: React.CSSProperties = {\n    height: 22,\n    background: token.colorBgContainer,\n    borderStyle: 'dashed',\n  };\n\n  return (\n    <Flex gap=\"4px 0\" wrap>\n      {tags.map<React.ReactNode>((tag, index) => {\n        if (editInputIndex === index) {\n          return (\n            <Input\n              ref={editInputRef}\n              key={tag}\n              size=\"small\"\n              style={tagInputStyle}\n              value={editInputValue}\n              onChange={handleEditInputChange}\n              onBlur={handleEditInputConfirm}\n              onPressEnter={handleEditInputConfirm}\n            />\n          );\n        }\n        const isLongTag = tag.length > 20;\n        const tagElem = (\n          <Tag\n            key={tag}\n            closable={index !== 0}\n            style={{ userSelect: 'none' }}\n            onClose={() => handleClose(tag)}\n          >\n            <span\n              onDoubleClick={(e) => {\n                if (index !== 0) {\n                  setEditInputIndex(index);\n                  setEditInputValue(tag);\n                  e.preventDefault();\n                }\n              }}\n            >\n              {isLongTag ? `${tag.slice(0, 20)}...` : tag}\n            </span>\n          </Tag>\n        );\n        return isLongTag ? (\n          <Tooltip title={tag} key={tag}>\n            {tagElem}\n          </Tooltip>\n        ) : (\n          tagElem\n        );\n      })}\n      {inputVisible ? (\n        <Input\n          ref={inputRef}\n          type=\"text\"\n          size=\"small\"\n          style={tagInputStyle}\n          value={inputValue}\n          onChange={handleInputChange}\n          onBlur={handleInputConfirm}\n          onPressEnter={handleInputConfirm}\n        />\n      ) : (\n        <Tag style={tagPlusStyle} icon={<PlusOutlined />} onClick={showInput}>\n          New Tag\n        </Tag>\n      )}\n    </Flex>\n  );\n};\n\nexport default App;\n";;
  