
  import '/Users/vagusx/Projects/ant-design/components/image/demo/toolbarRender.tsx?watch=parent';
  export default "import React from 'react';\nimport {\n  DownloadOutlined,\n  RotateLeftOutlined,\n  RotateRightOutlined,\n  SwapOutlined,\n  UndoOutlined,\n  ZoomInOutlined,\n  ZoomOutOutlined,\n} from '@ant-design/icons';\nimport { Image, Space } from 'antd';\n\nconst src = 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png';\n\n// you can download flipped and rotated image\n// https://codesandbox.io/s/zi-ding-yi-gong-ju-lan-antd-5-7-0-forked-c9jvmp\nconst onDownload = (imgUrl: string) => {\n  fetch(imgUrl)\n    .then((response) => response.blob())\n    .then((blob) => {\n      const url = URL.createObjectURL(new Blob([blob]));\n      const link = document.createElement<'a'>('a');\n      link.href = url;\n      link.download = 'image.png';\n      document.body.appendChild(link);\n      link.click();\n      URL.revokeObjectURL(url);\n      link.remove();\n    });\n};\n\nconst App: React.FC = () => (\n  <Image\n    width={200}\n    src={src}\n    preview={{\n      toolbarRender: (\n        _,\n        {\n          image: { url },\n          transform: { scale },\n          actions: { onFlipY, onFlipX, onRotateLeft, onRotateRight, onZoomOut, onZoomIn, onReset },\n        },\n      ) => (\n        <Space size={12} className=\"toolbar-wrapper\">\n          <DownloadOutlined onClick={() => onDownload(url)} />\n          <SwapOutlined rotate={90} onClick={onFlipY} />\n          <SwapOutlined onClick={onFlipX} />\n          <RotateLeftOutlined onClick={onRotateLeft} />\n          <RotateRightOutlined onClick={onRotateRight} />\n          <ZoomOutOutlined disabled={scale === 1} onClick={onZoomOut} />\n          <ZoomInOutlined disabled={scale === 50} onClick={onZoomIn} />\n          <UndoOutlined onClick={onReset} />\n        </Space>\n      ),\n    }}\n  />\n);\n\nexport default App;\n";;
  