
  import '/Users/vagusx/Projects/ant-design/components/notification/demo/stack.tsx?watch=parent';
  export default "import React, { useMemo } from 'react';\nimport { Button, Divider, InputNumber, notification, Space, Switch } from 'antd';\n\nconst Context = React.createContext({ name: 'Default' });\n\nconst App: React.FC = () => {\n  const [enabled, setEnabled] = React.useState(true);\n  const [threshold, setThreshold] = React.useState(3);\n  const [api, contextHolder] = notification.useNotification({\n    stack: enabled\n      ? {\n          threshold,\n        }\n      : false,\n  });\n\n  const openNotification = () => {\n    api.open({\n      message: 'Notification Title',\n      description: `${Array(Math.round(Math.random() * 5) + 1)\n        .fill('This is the content of the notification.')\n        .join('\\n')}`,\n      duration: null,\n    });\n  };\n\n  const contextValue = useMemo(() => ({ name: 'Ant Design' }), []);\n\n  return (\n    <Context.Provider value={contextValue}>\n      {contextHolder}\n      <div>\n        <Space size=\"large\">\n          <Space style={{ width: '100%' }}>\n            <span>Enabled: </span>\n            <Switch checked={enabled} onChange={(v) => setEnabled(v)} />\n          </Space>\n          <Space style={{ width: '100%' }}>\n            <span>Threshold: </span>\n            <InputNumber\n              disabled={!enabled}\n              value={threshold}\n              step={1}\n              min={1}\n              max={10}\n              onChange={(v) => setThreshold(v || 0)}\n            />\n          </Space>\n        </Space>\n        <Divider />\n        <Button type=\"primary\" onClick={openNotification}>\n          Open the notification box\n        </Button>\n      </div>\n    </Context.Provider>\n  );\n};\n\nexport default App;\n";;
  