
  import '/Users/vagusx/Projects/ant-design/components/steps/demo/customized-progress-dot.tsx?watch=parent';
  export default "import React from 'react';\nimport type { StepsProps } from 'antd';\nimport { Popover, Steps } from 'antd';\n\nconst customDot: StepsProps['progressDot'] = (dot, { status, index }) => (\n  <Popover\n    content={\n      <span>\n        step {index} status: {status}\n      </span>\n    }\n  >\n    {dot}\n  </Popover>\n);\nconst description = 'You can hover on the dot.';\nconst App: React.FC = () => (\n  <Steps\n    current={1}\n    progressDot={customDot}\n    items={[\n      {\n        title: 'Finished',\n        description,\n      },\n      {\n        title: 'In Progress',\n        description,\n      },\n      {\n        title: 'Waiting',\n        description,\n      },\n      {\n        title: 'Waiting',\n        description,\n      },\n    ]}\n  />\n);\n\nexport default App;\n";;
  