  export default "import React, { useState } from 'react';\nimport { Select, Tabs } from 'antd';\n\nconst { Option } = Select;\n\nconst positionList = ['left', 'right', 'top', 'bottom'];\n\nconst App: React.FC = () => {\n  const [parentPos, setParentPos] = useState(undefined);\n  const [childPos, setChildPos] = useState(undefined);\n  const [parentType, setParentType] = useState(undefined);\n  const [childType, setChildType] = useState(undefined);\n\n  return (\n    <div>\n      <Select\n        style={{ width: 200 }}\n        onChange={(val) => {\n          setParentPos(val);\n        }}\n      >\n        {positionList.map((pos) => (\n          <Option key={pos} value={pos}>\n            Parent - {pos}\n          </Option>\n        ))}\n      </Select>\n\n      <Select\n        style={{ width: 200 }}\n        onChange={(val) => {\n          setChildPos(val);\n        }}\n      >\n        {positionList.map((pos) => (\n          <Option key={pos} value={pos}>\n            Child - {pos}\n          </Option>\n        ))}\n      </Select>\n\n      <Select\n        style={{ width: 200 }}\n        onChange={(val) => {\n          setParentType(val);\n        }}\n      >\n        <Option value=\"line\">Parent - line</Option>\n        <Option value=\"card\">Parent - card</Option>\n        <Option value=\"editable-card\">Parent - card edit</Option>\n      </Select>\n\n      <Select\n        style={{ width: 200 }}\n        onChange={(val) => {\n          setChildType(val);\n        }}\n      >\n        <Option value=\"line\">Child - line</Option>\n        <Option value=\"card\">Child - card</Option>\n        <Option value=\"editable-card\">Parent - card edit</Option>\n      </Select>\n      <Tabs\n        defaultActiveKey=\"1\"\n        tabPosition={parentPos}\n        type={parentType}\n        items={[\n          {\n            label: 'Tab 1',\n            key: '1',\n            children: (\n              <Tabs\n                defaultActiveKey=\"1\"\n                tabPosition={childPos}\n                type={childType}\n                style={{ height: 300 }}\n                items={new Array(20).fill(null).map((_, index) => {\n                  const key = String(index);\n                  return {\n                    label: `Tab ${key}`,\n                    key,\n                    children: `TTTT ${key}`,\n                  };\n                })}\n              />\n            ),\n          },\n          {\n            label: 'Tab 2',\n            key: '2',\n            children: 'Content of Tab Pane 2',\n          },\n        ]}\n      />\n    </div>\n  );\n};\n\nexport default App;\n";;
  