
  import '/Users/vagusx/Projects/ant-design/components/config-provider/demo/theme.tsx?watch=parent';
  export default "import React from 'react';\nimport {\n  Button,\n  ColorPicker,\n  ConfigProvider,\n  Divider,\n  Form,\n  Input,\n  InputNumber,\n  Space,\n  Switch,\n} from 'antd';\nimport type { ColorPickerProps, GetProp } from 'antd';\n\ntype Color = Extract<GetProp<ColorPickerProps, 'value'>, { cleared: any }>;\n\ntype ThemeData = {\n  borderRadius: number;\n  colorPrimary: string;\n  Button?: {\n    colorPrimary: string;\n    algorithm?: boolean;\n  };\n};\n\nconst defaultData: ThemeData = {\n  borderRadius: 6,\n  colorPrimary: '#1677ff',\n  Button: {\n    colorPrimary: '#00B96B',\n  },\n};\n\nexport default () => {\n  const [form] = Form.useForm();\n\n  const [data, setData] = React.useState<ThemeData>(defaultData);\n\n  return (\n    <div>\n      <ConfigProvider\n        theme={{\n          token: {\n            colorPrimary: data.colorPrimary,\n            borderRadius: data.borderRadius,\n          },\n          components: {\n            Button: {\n              colorPrimary: data.Button?.colorPrimary,\n              algorithm: data.Button?.algorithm,\n            },\n          },\n        }}\n      >\n        <Space>\n          <Input />\n          <Button type=\"primary\">Button</Button>\n        </Space>\n      </ConfigProvider>\n      <Divider />\n      <Form\n        form={form}\n        onValuesChange={(_, allValues) => {\n          setData({\n            ...allValues,\n          });\n        }}\n        name=\"theme\"\n        initialValues={defaultData}\n        labelCol={{ span: 4 }}\n        wrapperCol={{ span: 20 }}\n      >\n        <Form.Item\n          name=\"colorPrimary\"\n          label=\"Primary Color\"\n          trigger=\"onChangeComplete\"\n          getValueFromEvent={(color: Color) => color.toHexString()}\n        >\n          <ColorPicker />\n        </Form.Item>\n        <Form.Item name=\"borderRadius\" label=\"Border Radius\">\n          <InputNumber />\n        </Form.Item>\n        <Form.Item label=\"Button\">\n          <Form.Item name={['Button', 'algorithm']} valuePropName=\"checked\" label=\"algorithm\">\n            <Switch />\n          </Form.Item>\n          <Form.Item\n            name={['Button', 'colorPrimary']}\n            label=\"Primary Color\"\n            trigger=\"onChangeComplete\"\n            getValueFromEvent={(color: Color) => color.toHexString()}\n          >\n            <ColorPicker />\n          </Form.Item>\n        </Form.Item>\n        <Form.Item name=\"submit\" wrapperCol={{ offset: 4, span: 20 }}>\n          <Button type=\"primary\">Submit</Button>\n        </Form.Item>\n      </Form>\n    </div>\n  );\n};\n";;
  