
  import '/Users/vagusx/Projects/ant-design/components/checkbox/demo/controller.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { Button, Checkbox } from 'antd';\nimport type { CheckboxProps } from 'antd';\n\nconst App: React.FC = () => {\n  const [checked, setChecked] = useState(true);\n  const [disabled, setDisabled] = useState(false);\n\n  const toggleChecked = () => {\n    setChecked(!checked);\n  };\n\n  const toggleDisable = () => {\n    setDisabled(!disabled);\n  };\n\n  const onChange: CheckboxProps['onChange'] = (e) => {\n    console.log('checked = ', e.target.checked);\n    setChecked(e.target.checked);\n  };\n\n  const label = `${checked ? 'Checked' : 'Unchecked'}-${disabled ? 'Disabled' : 'Enabled'}`;\n\n  return (\n    <>\n      <p style={{ marginBottom: '20px' }}>\n        <Checkbox checked={checked} disabled={disabled} onChange={onChange}>\n          {label}\n        </Checkbox>\n      </p>\n      <p>\n        <Button type=\"primary\" size=\"small\" onClick={toggleChecked}>\n          {!checked ? 'Check' : 'Uncheck'}\n        </Button>\n        <Button style={{ margin: '0 10px' }} type=\"primary\" size=\"small\" onClick={toggleDisable}>\n          {!disabled ? 'Disable' : 'Enable'}\n        </Button>\n      </p>\n    </>\n  );\n};\n\nexport default App;\n";;
  