
  import '/Users/vagusx/Projects/ant-design/components/drawer/demo/no-mask.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { Button, Drawer } from 'antd';\n\nconst App: React.FC = () => {\n  const [open, setOpen] = useState(false);\n\n  const showDrawer = () => {\n    setOpen(true);\n  };\n\n  const onClose = () => {\n    setOpen(false);\n  };\n\n  return (\n    <>\n      <Button type=\"primary\" onClick={showDrawer}>\n        Open\n      </Button>\n      <Drawer\n        title=\"Drawer without mask\"\n        placement=\"right\"\n        mask={false}\n        onClose={onClose}\n        open={open}\n        styles={{\n          mask: {\n            width: 333,\n            background: 'red',\n            borderRadius: 20,\n            boxShadow: '-5px 0 5px green',\n            overflow: 'hidden',\n          },\n        }}\n      >\n        <p>Some contents...</p>\n        <p>Some contents...</p>\n        <p>Some contents...</p>\n      </Drawer>\n    </>\n  );\n};\n\nexport default App;\n";;
  