  export default "import React from 'react';\nimport { Table } from 'antd';\nimport type { TableColumnsType, TableProps } from 'antd';\n\ninterface DataType {\n  key: React.Key;\n  name: string;\n  chinese: number;\n  math: number;\n  english: number;\n}\n\nconst columns: TableColumnsType<DataType> = [\n  {\n    title: 'Name',\n    dataIndex: 'name',\n  },\n  {\n    title: 'Chinese Score',\n    dataIndex: 'chinese',\n    sorter: {\n      compare: (a, b) => a.chinese - b.chinese,\n      multiple: 3,\n    },\n  },\n  {\n    title: 'Math Score',\n    dataIndex: 'math',\n    sorter: {\n      compare: (a, b) => a.math - b.math,\n      multiple: 2,\n    },\n  },\n  {\n    title: 'English Score',\n    dataIndex: 'english',\n    sorter: {\n      compare: (a, b) => a.english - b.english,\n      multiple: 1,\n    },\n  },\n];\n\nconst data: DataType[] = [\n  {\n    key: '1',\n    name: 'John Brown',\n    chinese: 98,\n    math: 60,\n    english: 70,\n  },\n  {\n    key: '2',\n    name: 'Jim Green',\n    chinese: 98,\n    math: 66,\n    english: 89,\n  },\n  {\n    key: '3',\n    name: 'Joe Black',\n    chinese: 98,\n    math: 90,\n    english: 70,\n  },\n  {\n    key: '4',\n    name: 'Jim Red',\n    chinese: 88,\n    math: 99,\n    english: 89,\n  },\n];\n\nconst onChange: TableProps<DataType>['onChange'] = (pagination, filters, sorter, extra) => {\n  console.log('params', pagination, filters, sorter, extra);\n};\n\nconst App: React.FC = () => <Table columns={columns} dataSource={data} onChange={onChange} />;\n\nexport default App;\n";;
  