
  import '/Users/vagusx/Projects/ant-design/components/list/demo/pagination.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { Avatar, List, Radio, Space } from 'antd';\n\ntype PaginationPosition = 'top' | 'bottom' | 'both';\n\ntype PaginationAlign = 'start' | 'center' | 'end';\n\nconst data = [\n  {\n    title: 'Ant Design Title 1',\n  },\n  {\n    title: 'Ant Design Title 2',\n  },\n  {\n    title: 'Ant Design Title 3',\n  },\n  {\n    title: 'Ant Design Title 4',\n  },\n];\n\nconst positionOptions = ['top', 'bottom', 'both'];\n\nconst alignOptions = ['start', 'center', 'end'];\n\nconst App: React.FC = () => {\n  const [position, setPosition] = useState<PaginationPosition>('bottom');\n  const [align, setAlign] = useState<PaginationAlign>('center');\n\n  return (\n    <>\n      <Space direction=\"vertical\" style={{ marginBottom: '20px' }} size=\"middle\">\n        <Space>\n          <span>Pagination Position:</span>\n          <Radio.Group\n            optionType=\"button\"\n            value={position}\n            onChange={(e) => {\n              setPosition(e.target.value);\n            }}\n          >\n            {positionOptions.map((item) => (\n              <Radio.Button key={item} value={item}>\n                {item}\n              </Radio.Button>\n            ))}\n          </Radio.Group>\n        </Space>\n        <Space>\n          <span>Pagination Align:</span>\n          <Radio.Group\n            optionType=\"button\"\n            value={align}\n            onChange={(e) => {\n              setAlign(e.target.value);\n            }}\n          >\n            {alignOptions.map((item) => (\n              <Radio.Button key={item} value={item}>\n                {item}\n              </Radio.Button>\n            ))}\n          </Radio.Group>\n        </Space>\n      </Space>\n      <List\n        pagination={{ position, align }}\n        dataSource={data}\n        renderItem={(item, index) => (\n          <List.Item>\n            <List.Item.Meta\n              avatar={<Avatar src={`https://api.dicebear.com/7.x/miniavs/svg?seed=${index}`} />}\n              title={<a href=\"https://ant.design\">{item.title}</a>}\n              description=\"Ant Design, a design language for background applications, is refined by Ant UED Team\"\n            />\n          </List.Item>\n        )}\n      />\n    </>\n  );\n};\n\nexport default App;\n";;
  