  export default "import React, { useState } from 'react';\nimport { SmileOutlined } from '@ant-design/icons';\nimport { TreeSelect } from 'antd';\n\nconst icon = <SmileOutlined />;\nconst treeData = [\n  {\n    value: 'parent 1',\n    title: 'parent 1',\n    children: [\n      {\n        value: 'parent 1-0',\n        title: 'parent 1-0',\n        children: [\n          {\n            value: 'leaf1',\n            title: 'my leaf',\n          },\n          {\n            value: 'leaf2',\n            title: 'your leaf',\n          },\n        ],\n      },\n      {\n        value: 'parent 1-1',\n        title: 'parent 1-1',\n        children: [\n          {\n            value: 'sss',\n            title: <b style={{ color: '#08c' }}>sss</b>,\n          },\n        ],\n      },\n    ],\n  },\n];\n\nconst App: React.FC = () => {\n  const [value, setValue] = useState<string>();\n\n  const onChange = (newValue: string) => {\n    console.log(newValue);\n    setValue(newValue);\n  };\n\n  return (\n    <TreeSelect\n      showSearch\n      suffixIcon={icon}\n      style={{ width: '100%' }}\n      value={value}\n      dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}\n      placeholder=\"Please select\"\n      allowClear\n      treeDefaultExpandAll\n      onChange={onChange}\n      treeData={treeData}\n    />\n  );\n};\n\nexport default App;\n";;
  