  export default "import React, { useState } from 'react';\nimport { Button, ConfigProvider, Drawer, Space } from 'antd';\nimport { createStyles, useTheme } from 'antd-style';\nimport type { DrawerClassNames, DrawerStyles } from 'antd/es/drawer/DrawerPanel';\n\nconst useStyle = createStyles(({ token }) => ({\n  'my-drawer-body': {\n    background: token.blue1,\n  },\n  'my-drawer-mask': {\n    boxShadow: `inset 0 0 15px #fff`,\n  },\n  'my-drawer-header': {\n    background: token.green1,\n  },\n  'my-drawer-footer': {\n    color: token.colorPrimary,\n  },\n  'my-drawer-content': {\n    borderLeft: '2px dotted #333',\n  },\n}));\n\nconst App: React.FC = () => {\n  const [open, setOpen] = useState([false, false]);\n  const { styles } = useStyle();\n  const token = useTheme();\n\n  const toggleDrawer = (idx: number, target: boolean) => {\n    setOpen((p) => {\n      p[idx] = target;\n      return [...p];\n    });\n  };\n\n  const classNames: DrawerClassNames = {\n    body: styles['my-drawer-body'],\n    mask: styles['my-drawer-mask'],\n    header: styles['my-drawer-header'],\n    footer: styles['my-drawer-footer'],\n    content: styles['my-drawer-content'],\n  };\n\n  const drawerStyles: DrawerStyles = {\n    mask: {\n      backdropFilter: 'blur(10px)',\n    },\n    content: {\n      boxShadow: '-10px 0 10px #666',\n    },\n    header: {\n      borderBottom: `1px solid ${token.colorPrimary}`,\n    },\n    body: {\n      fontSize: token.fontSizeLG,\n    },\n    footer: {\n      borderTop: `1px solid ${token.colorBorder}`,\n    },\n  };\n\n  return (\n    <>\n      <Space>\n        <Button type=\"primary\" onClick={() => toggleDrawer(0, true)}>\n          Open\n        </Button>\n        <Button type=\"primary\" onClick={() => toggleDrawer(1, true)}>\n          ConfigProvider\n        </Button>\n      </Space>\n      <Drawer\n        title=\"Basic Drawer\"\n        placement=\"right\"\n        footer=\"Footer\"\n        onClose={() => toggleDrawer(0, false)}\n        open={open[0]}\n        classNames={classNames}\n        styles={drawerStyles}\n      >\n        <p>Some contents...</p>\n        <p>Some contents...</p>\n        <p>Some contents...</p>\n      </Drawer>\n      <ConfigProvider\n        drawer={{\n          classNames,\n          styles: drawerStyles,\n        }}\n      >\n        <Drawer\n          title=\"Basic Drawer\"\n          placement=\"right\"\n          footer=\"Footer\"\n          onClose={() => toggleDrawer(1, false)}\n          open={open[1]}\n        >\n          <p>Some contents...</p>\n          <p>Some contents...</p>\n          <p>Some contents...</p>\n        </Drawer>\n      </ConfigProvider>\n    </>\n  );\n};\n\nexport default App;\n";;
  