
  import '/Users/vagusx/Projects/ant-design/components/modal/demo/locale.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { ExclamationCircleOutlined } from '@ant-design/icons';\nimport { Button, Modal, Space } from 'antd';\n\nconst LocalizedModal = () => {\n  const [open, setOpen] = useState(false);\n\n  const showModal = () => {\n    setOpen(true);\n  };\n\n  const hideModal = () => {\n    setOpen(false);\n  };\n\n  return (\n    <>\n      <Button type=\"primary\" onClick={showModal}>\n        Modal\n      </Button>\n      <Modal\n        title=\"Modal\"\n        open={open}\n        onOk={hideModal}\n        onCancel={hideModal}\n        okText=\"\"\n        cancelText=\"\"\n      >\n        <p>Bla bla ...</p>\n        <p>Bla bla ...</p>\n        <p>Bla bla ...</p>\n      </Modal>\n    </>\n  );\n};\n\nconst App: React.FC = () => {\n  const [modal, contextHolder] = Modal.useModal();\n\n  const confirm = () => {\n    modal.confirm({\n      title: 'Confirm',\n      icon: <ExclamationCircleOutlined />,\n      content: 'Bla bla ...',\n      okText: '',\n      cancelText: '',\n    });\n  };\n\n  return (\n    <>\n      <Space>\n        <LocalizedModal />\n        <Button onClick={confirm}>Confirm</Button>\n      </Space>\n      {contextHolder}\n    </>\n  );\n};\n\nexport default App;\n";;
  