
  import '/Users/vagusx/Projects/ant-design/components/mentions/demo/async.tsx?watch=parent';
  export default "import React, { useCallback, useRef, useState } from 'react';\nimport { Mentions } from 'antd';\nimport debounce from 'lodash/debounce';\n\nconst App: React.FC = () => {\n  const [loading, setLoading] = useState(false);\n  const [users, setUsers] = useState<{ login: string; avatar_url: string }[]>([]);\n  const ref = useRef<string>();\n\n  const loadGithubUsers = (key: string) => {\n    if (!key) {\n      setUsers([]);\n      return;\n    }\n\n    fetch(`https://api.github.com/search/users?q=${key}`)\n      .then((res) => res.json())\n      .then(({ items = [] }) => {\n        if (ref.current !== key) return;\n\n        setLoading(false);\n        setUsers(items.slice(0, 10));\n      });\n  };\n\n  const debounceLoadGithubUsers = useCallback(debounce(loadGithubUsers, 800), []);\n\n  const onSearch = (search: string) => {\n    console.log('Search:', search);\n    ref.current = search;\n    setLoading(!!search);\n    setUsers([]);\n\n    debounceLoadGithubUsers(search);\n  };\n\n  return (\n    <Mentions\n      style={{ width: '100%' }}\n      loading={loading}\n      onSearch={onSearch}\n      options={users.map(({ login, avatar_url: avatar }) => ({\n        key: login,\n        value: login,\n        className: 'antd-demo-dynamic-option',\n        label: (\n          <>\n            <img src={avatar} alt={login} />\n            <span>{login}</span>\n          </>\n        ),\n      }))}\n    />\n  );\n};\n\nexport default App;\n";;
  