
  import '/Users/vagusx/Projects/ant-design/components/badge/demo/change.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { MinusOutlined, PlusOutlined, QuestionOutlined } from '@ant-design/icons';\nimport { Avatar, Badge, Button, Space, Switch } from 'antd';\n\nconst ButtonGroup = Button.Group;\n\nconst App: React.FC = () => {\n  const [count, setCount] = useState(5);\n  const [show, setShow] = useState(true);\n\n  const increase = () => {\n    setCount(count + 1);\n  };\n\n  const decline = () => {\n    let newCount = count - 1;\n    if (newCount < 0) {\n      newCount = 0;\n    }\n    setCount(newCount);\n  };\n\n  const random = () => {\n    const newCount = Math.floor(Math.random() * 100);\n    setCount(newCount);\n  };\n\n  const onChange = (checked: boolean) => {\n    setShow(checked);\n  };\n\n  return (\n    <Space direction=\"vertical\">\n      <Space size=\"large\">\n        <Badge count={count}>\n          <Avatar shape=\"square\" size=\"large\" />\n        </Badge>\n        <ButtonGroup>\n          <Button onClick={decline} icon={<MinusOutlined />} />\n          <Button onClick={increase} icon={<PlusOutlined />} />\n          <Button onClick={random} icon={<QuestionOutlined />} />\n        </ButtonGroup>\n      </Space>\n      <Space size=\"large\">\n        <Badge dot={show}>\n          <Avatar shape=\"square\" size=\"large\" />\n        </Badge>\n        <Switch onChange={onChange} checked={show} />\n      </Space>\n    </Space>\n  );\n};\n\nexport default App;\n";;
  