  export default "import React, { useState } from 'react';\nimport { Switch, Transfer } from 'antd';\nimport type { TransferProps } from 'antd';\n\ninterface RecordType {\n  key: string;\n  title: string;\n  description: string;\n  disabled: boolean;\n}\n\nconst mockData: RecordType[] = Array.from({ length: 20 }).map((_, i) => ({\n  key: i.toString(),\n  title: `content${i + 1}`,\n  description: `description of content${i + 1}`,\n  disabled: i % 3 < 1,\n}));\n\nconst oriTargetKeys = mockData.filter((item) => Number(item.key) % 3 > 1).map((item) => item.key);\n\nconst App: React.FC = () => {\n  const [targetKeys, setTargetKeys] = useState<React.Key[]>(oriTargetKeys);\n  const [selectedKeys, setSelectedKeys] = useState<React.Key[]>([]);\n  const [disabled, setDisabled] = useState(false);\n\n  const handleChange: TransferProps['onChange'] = (newTargetKeys, direction, moveKeys) => {\n    setTargetKeys(newTargetKeys);\n\n    console.log('targetKeys: ', newTargetKeys);\n    console.log('direction: ', direction);\n    console.log('moveKeys: ', moveKeys);\n  };\n\n  const handleSelectChange: TransferProps['onSelectChange'] = (\n    sourceSelectedKeys,\n    targetSelectedKeys,\n  ) => {\n    setSelectedKeys([...sourceSelectedKeys, ...targetSelectedKeys]);\n\n    console.log('sourceSelectedKeys: ', sourceSelectedKeys);\n    console.log('targetSelectedKeys: ', targetSelectedKeys);\n  };\n\n  const handleScroll: TransferProps['onScroll'] = (direction, e) => {\n    console.log('direction:', direction);\n    console.log('target:', e.target);\n  };\n\n  const handleDisable = (checked: boolean) => {\n    setDisabled(checked);\n  };\n\n  return (\n    <>\n      <Transfer\n        dataSource={mockData}\n        titles={['Source', 'Target']}\n        targetKeys={targetKeys}\n        selectedKeys={selectedKeys}\n        onChange={handleChange}\n        onSelectChange={handleSelectChange}\n        onScroll={handleScroll}\n        render={(item) => item.title}\n        disabled={disabled}\n        oneWay\n        style={{ marginBottom: 16 }}\n      />\n      <Switch\n        unCheckedChildren=\"disabled\"\n        checkedChildren=\"disabled\"\n        checked={disabled}\n        onChange={handleDisable}\n      />\n    </>\n  );\n};\n\nexport default App;\n";;
  