  export default "import React, { useState } from 'react';\nimport { CarryOutOutlined, CheckOutlined, FormOutlined } from '@ant-design/icons';\nimport { Select, Switch, Tree } from 'antd';\nimport type { TreeDataNode } from 'antd';\n\nconst treeData: TreeDataNode[] = [\n  {\n    title: 'parent 1',\n    key: '0-0',\n    icon: <CarryOutOutlined />,\n    children: [\n      {\n        title: 'parent 1-0',\n        key: '0-0-0',\n        icon: <CarryOutOutlined />,\n        children: [\n          { title: 'leaf', key: '0-0-0-0', icon: <CarryOutOutlined /> },\n          {\n            title: (\n              <>\n                <div>multiple line title</div>\n                <div>multiple line title</div>\n              </>\n            ),\n            key: '0-0-0-1',\n            icon: <CarryOutOutlined />,\n          },\n          { title: 'leaf', key: '0-0-0-2', icon: <CarryOutOutlined /> },\n        ],\n      },\n      {\n        title: 'parent 1-1',\n        key: '0-0-1',\n        icon: <CarryOutOutlined />,\n        children: [{ title: 'leaf', key: '0-0-1-0', icon: <CarryOutOutlined /> }],\n      },\n      {\n        title: 'parent 1-2',\n        key: '0-0-2',\n        icon: <CarryOutOutlined />,\n        children: [\n          { title: 'leaf', key: '0-0-2-0', icon: <CarryOutOutlined /> },\n          {\n            title: 'leaf',\n            key: '0-0-2-1',\n            icon: <CarryOutOutlined />,\n            switcherIcon: <FormOutlined />,\n          },\n        ],\n      },\n    ],\n  },\n  {\n    title: 'parent 2',\n    key: '0-1',\n    icon: <CarryOutOutlined />,\n    children: [\n      {\n        title: 'parent 2-0',\n        key: '0-1-0',\n        icon: <CarryOutOutlined />,\n        children: [\n          { title: 'leaf', key: '0-1-0-0', icon: <CarryOutOutlined /> },\n          { title: 'leaf', key: '0-1-0-1', icon: <CarryOutOutlined /> },\n        ],\n      },\n    ],\n  },\n];\n\nconst App: React.FC = () => {\n  const [showLine, setShowLine] = useState<boolean>(true);\n  const [showIcon, setShowIcon] = useState<boolean>(false);\n  const [showLeafIcon, setShowLeafIcon] = useState<React.ReactNode>(true);\n\n  const onSelect = (selectedKeys: React.Key[], info: any) => {\n    console.log('selected', selectedKeys, info);\n  };\n\n  const handleLeafIconChange = (value: 'true' | 'false' | 'custom') => {\n    if (value === 'custom') {\n      return setShowLeafIcon(<CheckOutlined />);\n    }\n\n    if (value === 'true') {\n      return setShowLeafIcon(true);\n    }\n\n    return setShowLeafIcon(false);\n  };\n\n  return (\n    <div>\n      <div style={{ marginBottom: 16 }}>\n        showLine: <Switch checked={!!showLine} onChange={setShowLine} />\n        <br />\n        <br />\n        showIcon: <Switch checked={showIcon} onChange={setShowIcon} />\n        <br />\n        <br />\n        showLeafIcon:{' '}\n        <Select defaultValue=\"true\" onChange={handleLeafIconChange}>\n          <Select.Option value=\"true\">True</Select.Option>\n          <Select.Option value=\"false\">False</Select.Option>\n          <Select.Option value=\"custom\">Custom icon</Select.Option>\n        </Select>\n      </div>\n      <Tree\n        showLine={showLine ? { showLeafIcon } : false}\n        showIcon={showIcon}\n        defaultExpandedKeys={['0-0-0']}\n        onSelect={onSelect}\n        treeData={treeData}\n      />\n    </div>\n  );\n};\n\nexport default App;\n";;
  