  export default "import React from 'react';\nimport { AndroidOutlined, AppleOutlined } from '@ant-design/icons';\nimport { Tabs } from 'antd';\n\nconst App: React.FC = () => (\n  <Tabs\n    defaultActiveKey=\"2\"\n    items={[AppleOutlined, AndroidOutlined].map((Icon, i) => {\n      const id = String(i + 1);\n      return {\n        key: id,\n        label: `Tab ${id}`,\n        children: `Tab ${id}`,\n        icon: <Icon />,\n      };\n    })}\n  />\n);\n\nexport default App;\n";;
  