
  import '/Users/vagusx/Projects/ant-design/components/tree/demo/draggable.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { Tree } from 'antd';\nimport type { TreeDataNode, TreeProps } from 'antd';\n\nconst x = 3;\nconst y = 2;\nconst z = 1;\nconst defaultData: TreeDataNode[] = [];\n\nconst generateData = (_level: number, _preKey?: React.Key, _tns?: TreeDataNode[]) => {\n  const preKey = _preKey || '0';\n  const tns = _tns || defaultData;\n\n  const children: React.Key[] = [];\n  for (let i = 0; i < x; i++) {\n    const key = `${preKey}-${i}`;\n    tns.push({ title: key, key });\n    if (i < y) {\n      children.push(key);\n    }\n  }\n  if (_level < 0) {\n    return tns;\n  }\n  const level = _level - 1;\n  children.forEach((key, index) => {\n    tns[index].children = [];\n    return generateData(level, key, tns[index].children);\n  });\n};\ngenerateData(z);\n\nconst App: React.FC = () => {\n  const [gData, setGData] = useState(defaultData);\n  const [expandedKeys] = useState(['0-0', '0-0-0', '0-0-0-0']);\n\n  const onDragEnter: TreeProps['onDragEnter'] = (info) => {\n    console.log(info);\n    // expandedKeys, set it when controlled is needed\n    // setExpandedKeys(info.expandedKeys)\n  };\n\n  const onDrop: TreeProps['onDrop'] = (info) => {\n    console.log(info);\n    const dropKey = info.node.key;\n    const dragKey = info.dragNode.key;\n    const dropPos = info.node.pos.split('-');\n    const dropPosition = info.dropPosition - Number(dropPos[dropPos.length - 1]); // the drop position relative to the drop node, inside 0, top -1, bottom 1\n\n    const loop = (\n      data: TreeDataNode[],\n      key: React.Key,\n      callback: (node: TreeDataNode, i: number, data: TreeDataNode[]) => void,\n    ) => {\n      for (let i = 0; i < data.length; i++) {\n        if (data[i].key === key) {\n          return callback(data[i], i, data);\n        }\n        if (data[i].children) {\n          loop(data[i].children!, key, callback);\n        }\n      }\n    };\n    const data = [...gData];\n\n    // Find dragObject\n    let dragObj: TreeDataNode;\n    loop(data, dragKey, (item, index, arr) => {\n      arr.splice(index, 1);\n      dragObj = item;\n    });\n\n    if (!info.dropToGap) {\n      // Drop on the content\n      loop(data, dropKey, (item) => {\n        item.children = item.children || [];\n        // where to insert. New item was inserted to the start of the array in this example, but can be anywhere\n        item.children.unshift(dragObj);\n      });\n    } else {\n      let ar: TreeDataNode[] = [];\n      let i: number;\n      loop(data, dropKey, (_item, index, arr) => {\n        ar = arr;\n        i = index;\n      });\n      if (dropPosition === -1) {\n        // Drop on the top of the drop node\n        ar.splice(i!, 0, dragObj!);\n      } else {\n        // Drop on the bottom of the drop node\n        ar.splice(i! + 1, 0, dragObj!);\n      }\n    }\n    setGData(data);\n  };\n\n  return (\n    <Tree\n      className=\"draggable-tree\"\n      defaultExpandedKeys={expandedKeys}\n      draggable\n      blockNode\n      onDragEnter={onDragEnter}\n      onDrop={onDrop}\n      treeData={gData}\n    />\n  );\n};\n\nexport default App;\n";;
  