
  import '/Users/vagusx/Projects/ant-design/components/qr-code/demo/customSize.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { MinusOutlined, PlusOutlined } from '@ant-design/icons';\nimport { Button, QRCode } from 'antd';\n\nconst MIN_SIZE = 48;\nconst MAX_SIZE = 300;\n\nconst App: React.FC = () => {\n  const [size, setSize] = useState<number>(160);\n\n  const increase = () => {\n    setSize((prevSize) => {\n      const newSize = prevSize + 10;\n      if (newSize >= MAX_SIZE) {\n        return MAX_SIZE;\n      }\n      return newSize;\n    });\n  };\n\n  const decline = () => {\n    setSize((prevSize) => {\n      const newSize = prevSize - 10;\n      if (newSize <= MIN_SIZE) {\n        return MIN_SIZE;\n      }\n      return newSize;\n    });\n  };\n\n  return (\n    <>\n      <Button.Group style={{ marginBottom: 16 }}>\n        <Button onClick={decline} disabled={size <= MIN_SIZE} icon={<MinusOutlined />}>\n          Smaller\n        </Button>\n        <Button onClick={increase} disabled={size >= MAX_SIZE} icon={<PlusOutlined />}>\n          Larger\n        </Button>\n      </Button.Group>\n      <QRCode\n        errorLevel=\"H\"\n        size={size}\n        iconSize={size / 4}\n        value=\"https://ant.design/\"\n        icon=\"https://gw.alipayobjects.com/zos/rmsportal/KDpgvguMpGfqaHPjicRK.svg\"\n      />\n    </>\n  );\n};\n\nexport default App;\n";;
  