
  import '/Users/vagusx/Projects/ant-design/components/table/demo/summary.tsx?watch=parent';
  export default "import React from 'react';\nimport { Table, Typography } from 'antd';\nimport type { TableColumnsType } from 'antd';\n\nconst { Text } = Typography;\n\ninterface DataType {\n  key: string;\n  name: string;\n  borrow: number;\n  repayment: number;\n}\n\ninterface FixedDataType {\n  key: React.Key;\n  name: string;\n  description: string;\n}\n\nconst columns: TableColumnsType<DataType> = [\n  {\n    title: 'Name',\n    dataIndex: 'name',\n  },\n  {\n    title: 'Borrow',\n    dataIndex: 'borrow',\n  },\n  {\n    title: 'Repayment',\n    dataIndex: 'repayment',\n  },\n];\n\nconst data: DataType[] = [\n  {\n    key: '1',\n    name: 'John Brown',\n    borrow: 10,\n    repayment: 33,\n  },\n  {\n    key: '2',\n    name: 'Jim Green',\n    borrow: 100,\n    repayment: 0,\n  },\n  {\n    key: '3',\n    name: 'Joe Black',\n    borrow: 10,\n    repayment: 10,\n  },\n  {\n    key: '4',\n    name: 'Jim Red',\n    borrow: 75,\n    repayment: 45,\n  },\n];\n\nconst fixedColumns: TableColumnsType<FixedDataType> = [\n  {\n    title: 'Name',\n    dataIndex: 'name',\n    fixed: true,\n    width: 100,\n  },\n  {\n    title: 'Description',\n    dataIndex: 'description',\n  },\n];\n\nconst fixedData: FixedDataType[] = [];\nfor (let i = 0; i < 20; i += 1) {\n  fixedData.push({\n    key: i,\n    name: ['Light', 'Bamboo', 'Little'][i % 3],\n    description: 'Everything that has a beginning, has an end.',\n  });\n}\n\nconst App: React.FC = () => (\n  <>\n    <Table\n      columns={columns}\n      dataSource={data}\n      pagination={false}\n      bordered\n      summary={(pageData) => {\n        let totalBorrow = 0;\n        let totalRepayment = 0;\n\n        pageData.forEach(({ borrow, repayment }) => {\n          totalBorrow += borrow;\n          totalRepayment += repayment;\n        });\n\n        return (\n          <>\n            <Table.Summary.Row>\n              <Table.Summary.Cell index={0}>Total</Table.Summary.Cell>\n              <Table.Summary.Cell index={1}>\n                <Text type=\"danger\">{totalBorrow}</Text>\n              </Table.Summary.Cell>\n              <Table.Summary.Cell index={2}>\n                <Text>{totalRepayment}</Text>\n              </Table.Summary.Cell>\n            </Table.Summary.Row>\n            <Table.Summary.Row>\n              <Table.Summary.Cell index={0}>Balance</Table.Summary.Cell>\n              <Table.Summary.Cell index={1} colSpan={2}>\n                <Text type=\"danger\">{totalBorrow - totalRepayment}</Text>\n              </Table.Summary.Cell>\n            </Table.Summary.Row>\n          </>\n        );\n      }}\n    />\n\n    <br />\n\n    <Table\n      columns={fixedColumns}\n      dataSource={fixedData}\n      pagination={false}\n      scroll={{ x: 2000, y: 500 }}\n      bordered\n      summary={() => (\n        <Table.Summary fixed>\n          <Table.Summary.Row>\n            <Table.Summary.Cell index={0}>Summary</Table.Summary.Cell>\n            <Table.Summary.Cell index={1}>This is a summary content</Table.Summary.Cell>\n          </Table.Summary.Row>\n        </Table.Summary>\n      )}\n    />\n  </>\n);\n\nexport default App;\n";;
  