  export default "import React from 'react';\nimport { AlertFilled, CloseSquareFilled } from '@ant-design/icons';\nimport { Button, Form, Input, Tooltip } from 'antd';\nimport { createStyles, css } from 'antd-style';\nimport uniqueId from 'lodash/uniqueId';\n\nconst useStyle = createStyles(() => ({\n  'custom-feedback-icons': css`\n    .ant-form-item-feedback-icon {\n      pointer-events: all;\n    }\n  `,\n}));\n\nconst App: React.FC = () => {\n  const [form] = Form.useForm();\n  const { styles } = useStyle();\n\n  return (\n    <Form\n      name=\"custom-feedback-icons\"\n      form={form}\n      style={{ maxWidth: 600 }}\n      feedbackIcons={({ errors }) => ({\n        error: (\n          <Tooltip\n            key=\"tooltipKey\"\n            title={errors?.map((error) => <div key={uniqueId()}>{error}</div>)}\n            color=\"red\"\n          >\n            <CloseSquareFilled />\n          </Tooltip>\n        ),\n      })}\n    >\n      <Form.Item\n        name=\"custom-feedback-test-item\"\n        label=\"Test\"\n        className={styles['custom-feedback-icons']}\n        rules={[{ required: true, type: 'email' }, { min: 10 }]}\n        help=\"\"\n        hasFeedback\n      >\n        <Input />\n      </Form.Item>\n      <Form.Item\n        name=\"custom-feedback-test-item2\"\n        label=\"Test\"\n        className={styles['custom-feedback-icons']}\n        rules={[{ required: true, type: 'email' }, { min: 10 }]}\n        help=\"\"\n        hasFeedback={{\n          icons: ({ errors }) => ({\n            error: (\n              <Tooltip\n                key=\"tooltipKey\"\n                title={errors?.map((error) => <div key={uniqueId()}>{error}</div>)}\n                color=\"pink\"\n              >\n                <AlertFilled />\n              </Tooltip>\n            ),\n            success: false,\n          }),\n        }}\n      >\n        <Input />\n      </Form.Item>\n      <Form.Item>\n        <Button htmlType=\"submit\">Submit</Button>\n      </Form.Item>\n    </Form>\n  );\n};\n\nexport default App;\n";;
  