  export default "import React from 'react';\nimport { Button, ConfigProvider, Popconfirm } from 'antd';\n\nconst text = 'Are you sure to delete this task?';\nconst description = 'Delete the task';\nconst buttonWidth = 80;\n\nconst App: React.FC = () => (\n  <ConfigProvider\n    button={{\n      style: { width: buttonWidth, margin: 4 },\n    }}\n  >\n    <div className=\"demo\">\n      <div style={{ marginInlineStart: buttonWidth, whiteSpace: 'nowrap' }}>\n        <Popconfirm\n          placement=\"topLeft\"\n          title={text}\n          description={description}\n          okText=\"Yes\"\n          cancelText=\"No\"\n        >\n          <Button>TL</Button>\n        </Popconfirm>\n        <Popconfirm\n          placement=\"top\"\n          title={text}\n          description={description}\n          okText=\"Yes\"\n          cancelText=\"No\"\n        >\n          <Button>Top</Button>\n        </Popconfirm>\n        <Popconfirm\n          placement=\"topRight\"\n          title={text}\n          description={description}\n          okText=\"Yes\"\n          cancelText=\"No\"\n        >\n          <Button>TR</Button>\n        </Popconfirm>\n      </div>\n      <div style={{ width: buttonWidth, float: 'inline-start' }}>\n        <Popconfirm\n          placement=\"leftTop\"\n          title={text}\n          description={description}\n          okText=\"Yes\"\n          cancelText=\"No\"\n        >\n          <Button>LT</Button>\n        </Popconfirm>\n        <Popconfirm\n          placement=\"left\"\n          title={text}\n          description={description}\n          okText=\"Yes\"\n          cancelText=\"No\"\n        >\n          <Button>Left</Button>\n        </Popconfirm>\n        <Popconfirm\n          placement=\"leftBottom\"\n          title={text}\n          description={description}\n          okText=\"Yes\"\n          cancelText=\"No\"\n        >\n          <Button>LB</Button>\n        </Popconfirm>\n      </div>\n      <div style={{ width: buttonWidth, marginInlineStart: buttonWidth * 4 + 24 }}>\n        <Popconfirm\n          placement=\"rightTop\"\n          title={text}\n          description={description}\n          okText=\"Yes\"\n          cancelText=\"No\"\n        >\n          <Button>RT</Button>\n        </Popconfirm>\n        <Popconfirm\n          placement=\"right\"\n          title={text}\n          description={description}\n          okText=\"Yes\"\n          cancelText=\"No\"\n        >\n          <Button>Right</Button>\n        </Popconfirm>\n        <Popconfirm\n          placement=\"rightBottom\"\n          title={text}\n          description={description}\n          okText=\"Yes\"\n          cancelText=\"No\"\n        >\n          <Button>RB</Button>\n        </Popconfirm>\n      </div>\n      <div style={{ marginInlineStart: buttonWidth, clear: 'both', whiteSpace: 'nowrap' }}>\n        <Popconfirm\n          placement=\"bottomLeft\"\n          title={text}\n          description={description}\n          okText=\"Yes\"\n          cancelText=\"No\"\n        >\n          <Button>BL</Button>\n        </Popconfirm>\n        <Popconfirm\n          placement=\"bottom\"\n          title={text}\n          description={description}\n          okText=\"Yes\"\n          cancelText=\"No\"\n        >\n          <Button>Bottom</Button>\n        </Popconfirm>\n        <Popconfirm\n          placement=\"bottomRight\"\n          title={text}\n          description={description}\n          okText=\"Yes\"\n          cancelText=\"No\"\n        >\n          <Button>BR</Button>\n        </Popconfirm>\n      </div>\n    </div>\n  </ConfigProvider>\n);\n\nexport default App;\n";;
  