  export default "import React from 'react';\nimport { Anchor } from 'antd';\n\nconst handleClick = (\n  e: React.MouseEvent<HTMLElement>,\n  link: {\n    title: React.ReactNode;\n    href: string;\n  },\n) => {\n  e.preventDefault();\n  console.log(link);\n};\n\nconst App: React.FC = () => (\n  <Anchor\n    affix={false}\n    onClick={handleClick}\n    items={[\n      {\n        key: '1',\n        href: '#anchor-demo-basic',\n        title: 'Basic demo',\n      },\n      {\n        key: '2',\n        href: '#anchor-demo-static',\n        title: 'Static demo',\n      },\n      {\n        key: '3',\n        href: '#api',\n        title: 'API',\n        children: [\n          {\n            key: '4',\n            href: '#anchor-props',\n            title: 'Anchor Props',\n          },\n          {\n            key: '5',\n            href: '#link-props',\n            title: 'Link Props',\n          },\n        ],\n      },\n    ]}\n  />\n);\n\nexport default App;\n";;
  