  export default "import React from 'react';\nimport { DownOutlined, UserOutlined } from '@ant-design/icons';\nimport type { MenuProps } from 'antd';\nimport { Button, Dropdown, message, Space, Tooltip } from 'antd';\n\nconst handleButtonClick = (e: React.MouseEvent<HTMLButtonElement>) => {\n  message.info('Click on left button.');\n  console.log('click left button', e);\n};\n\nconst handleMenuClick: MenuProps['onClick'] = (e) => {\n  message.info('Click on menu item.');\n  console.log('click', e);\n};\n\nconst items: MenuProps['items'] = [\n  {\n    label: '1st menu item',\n    key: '1',\n    icon: <UserOutlined />,\n  },\n  {\n    label: '2nd menu item',\n    key: '2',\n    icon: <UserOutlined />,\n  },\n  {\n    label: '3rd menu item',\n    key: '3',\n    icon: <UserOutlined />,\n    danger: true,\n  },\n  {\n    label: '4rd menu item',\n    key: '4',\n    icon: <UserOutlined />,\n    danger: true,\n    disabled: true,\n  },\n];\n\nconst menuProps = {\n  items,\n  onClick: handleMenuClick,\n};\n\nconst App: React.FC = () => (\n  <Space wrap>\n    <Dropdown.Button menu={menuProps} onClick={handleButtonClick}>\n      Dropdown\n    </Dropdown.Button>\n    <Dropdown.Button menu={menuProps} placement=\"bottom\" icon={<UserOutlined />}>\n      Dropdown\n    </Dropdown.Button>\n    <Dropdown.Button menu={menuProps} onClick={handleButtonClick} disabled>\n      Dropdown\n    </Dropdown.Button>\n    <Dropdown.Button\n      menu={menuProps}\n      buttonsRender={([leftButton, rightButton]) => [\n        <Tooltip title=\"tooltip\" key=\"leftButton\">\n          {leftButton}\n        </Tooltip>,\n        React.cloneElement(rightButton as React.ReactElement<any, string>, { loading: true }),\n      ]}\n    >\n      With Tooltip\n    </Dropdown.Button>\n    <Dropdown menu={menuProps}>\n      <Button>\n        <Space>\n          Button\n          <DownOutlined />\n        </Space>\n      </Button>\n    </Dropdown>\n    <Dropdown.Button menu={menuProps} onClick={handleButtonClick} danger>\n      Danger\n    </Dropdown.Button>\n  </Space>\n);\n\nexport default App;\n";;
  