  export default "import React from 'react';\nimport { Card, List } from 'antd';\n\nconst data = [\n  {\n    title: 'Title 1',\n  },\n  {\n    title: 'Title 2',\n  },\n  {\n    title: 'Title 3',\n  },\n  {\n    title: 'Title 4',\n  },\n  {\n    title: 'Title 5',\n  },\n  {\n    title: 'Title 6',\n  },\n];\n\nconst ListItem = () => (\n  <List.Item>\n    <Card title=\"title\">Card content</Card>\n  </List.Item>\n);\n\nconst App: React.FC = () => (\n  <>\n    <List\n      grid={{ gutter: 16, column: 4 }}\n      dataSource={data}\n      renderItem={(item) => (\n        <List.Item>\n          <Card title={item.title}>Card content</Card>\n        </List.Item>\n      )}\n    />\n    <List grid={{ gutter: 16, column: 4 }} dataSource={data} renderItem={() => <ListItem />} />\n    <List\n      grid={{ gutter: 16, column: 4 }}\n      dataSource={data}\n      renderItem={() => (\n        <>\n          <ListItem />\n          <div />\n        </>\n      )}\n    />\n  </>\n);\n\nexport default App;\n";;
  