  export default "import React from 'react';\nimport { Select, Space } from 'antd';\n\nconst handleChange = (value: string[]) => {\n  console.log(`selected ${value}`);\n};\n\nconst options = [\n  {\n    label: 'China',\n    value: 'china',\n    emoji: '',\n    desc: 'China ()',\n  },\n  {\n    label: 'USA',\n    value: 'usa',\n    emoji: '',\n    desc: 'USA ()',\n  },\n  {\n    label: 'Japan',\n    value: 'japan',\n    emoji: '',\n    desc: 'Japan ()',\n  },\n  {\n    label: 'Korea',\n    value: 'korea',\n    emoji: '',\n    desc: 'Korea ()',\n  },\n];\n\nconst App: React.FC = () => (\n  <Select\n    mode=\"multiple\"\n    style={{ width: '100%' }}\n    placeholder=\"select one country\"\n    defaultValue={['china']}\n    onChange={handleChange}\n    options={options}\n    optionRender={(option) => (\n      <Space>\n        <span role=\"img\" aria-label={option.data.label}>\n          {option.data.emoji}\n        </span>\n        {option.data.desc}\n      </Space>\n    )}\n  />\n);\n\nexport default App;\n";;
  