
  import '/Users/vagusx/Projects/ant-design/components/popconfirm/demo/dynamic-trigger.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { Button, message, Popconfirm, Switch } from 'antd';\n\nconst App: React.FC = () => {\n  const [open, setOpen] = useState(false);\n  const [condition, setCondition] = useState(true);\n\n  const changeCondition = (checked: boolean) => {\n    setCondition(checked);\n  };\n\n  const confirm = () => {\n    setOpen(false);\n    message.success('Next step.');\n  };\n\n  const cancel = () => {\n    setOpen(false);\n    message.error('Click on cancel.');\n  };\n\n  const handleOpenChange = (newOpen: boolean) => {\n    if (!newOpen) {\n      setOpen(newOpen);\n      return;\n    }\n    // Determining condition before show the popconfirm.\n    console.log(condition);\n    if (condition) {\n      confirm(); // next step\n    } else {\n      setOpen(newOpen);\n    }\n  };\n\n  return (\n    <div>\n      <Popconfirm\n        title=\"Delete the task\"\n        description=\"Are you sure to delete this task?\"\n        open={open}\n        onOpenChange={handleOpenChange}\n        onConfirm={confirm}\n        onCancel={cancel}\n        okText=\"Yes\"\n        cancelText=\"No\"\n      >\n        <Button danger>Delete a task</Button>\n      </Popconfirm>\n      <br />\n      <br />\n      Whether directly execute\n      <Switch defaultChecked onChange={changeCondition} />\n    </div>\n  );\n};\n\nexport default App;\n";;
  