
  import '/Users/vagusx/Projects/ant-design/components/typography/demo/ellipsis-controlled.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { Flex, Slider, Switch, Typography } from 'antd';\n\nconst App = () => {\n  const [rows, setRows] = useState(2);\n  const [expanded, setExpanded] = useState(false);\n\n  return (\n    <Flex gap={16} vertical>\n      <Flex gap={16} align=\"center\">\n        <Switch\n          checked={expanded}\n          onChange={() => setExpanded((c) => !c)}\n          style={{ flex: 'none' }}\n        />\n        <Slider min={1} max={20} value={rows} onChange={setRows} style={{ flex: 'auto' }} />\n      </Flex>\n\n      <Typography.Paragraph\n        ellipsis={{\n          rows,\n          expandable: 'collapsible',\n          expanded,\n          onExpand: (_, info) => setExpanded(info.expanded),\n        }}\n        copyable\n      >\n        {'Ant Design, a design language for background applications, is refined by Ant UED Team.'.repeat(\n          20,\n        )}\n      </Typography.Paragraph>\n    </Flex>\n  );\n};\n\nexport default App;\n";;
  