
  import '/Users/vagusx/Projects/ant-design/components/form/demo/dynamic-form-items-no-style.tsx?watch=parent';
  export default "import React from 'react';\nimport { MinusCircleOutlined, PlusOutlined } from '@ant-design/icons';\nimport { Button, Form, Input, Space } from 'antd';\n\nconst onFinish = (values: any) => {\n  console.log('Received values of form:', values);\n};\n\nconst App: React.FC = () => (\n  <Form\n    name=\"dynamic_form_no_style\"\n    onFinish={onFinish}\n    style={{ maxWidth: 600 }}\n    autoComplete=\"off\"\n  >\n    <Form.Item label=\"Users\">\n      <Form.List name=\"users\">\n        {(fields, { add, remove }) => (\n          <>\n            {fields.map((field) => (\n              <Space key={field.key} style={{ marginBottom: 16 }}>\n                <Form.Item noStyle name={[field.name, 'lastName']} rules={[{ required: true }]}>\n                  <Input placeholder=\"Last Name\" />\n                </Form.Item>\n                <Form.Item noStyle name={[field.name, 'firstName']} rules={[{ required: true }]}>\n                  <Input placeholder=\"First Name\" />\n                </Form.Item>\n                <MinusCircleOutlined\n                  onClick={() => {\n                    remove(field.name);\n                  }}\n                />\n              </Space>\n            ))}\n            <Form.Item>\n              <Button type=\"dashed\" onClick={() => add()} block icon={<PlusOutlined />}>\n                Add field\n              </Button>\n            </Form.Item>\n          </>\n        )}\n      </Form.List>\n    </Form.Item>\n    <Form.Item>\n      <Button type=\"primary\" htmlType=\"submit\">\n        Submit\n      </Button>\n    </Form.Item>\n  </Form>\n);\n\nexport default App;\n";;
  