
  import '/Users/vagusx/Projects/ant-design/components/input/demo/focus.tsx?watch=parent';
  export default "import React, { useRef, useState } from 'react';\nimport type { InputRef } from 'antd';\nimport { Button, Input, Space, Switch } from 'antd';\n\nconst App: React.FC = () => {\n  const inputRef = useRef<InputRef>(null);\n  const [input, setInput] = useState(true);\n\n  const sharedProps = {\n    style: { width: '100%' },\n    defaultValue: 'Ant Design love you!',\n    ref: inputRef,\n  };\n\n  return (\n    <Space direction=\"vertical\" style={{ width: '100%' }}>\n      <Space wrap>\n        <Button\n          onClick={() => {\n            inputRef.current!.focus({\n              cursor: 'start',\n            });\n          }}\n        >\n          Focus at first\n        </Button>\n        <Button\n          onClick={() => {\n            inputRef.current!.focus({\n              cursor: 'end',\n            });\n          }}\n        >\n          Focus at last\n        </Button>\n        <Button\n          onClick={() => {\n            inputRef.current!.focus({\n              cursor: 'all',\n            });\n          }}\n        >\n          Focus to select all\n        </Button>\n        <Button\n          onClick={() => {\n            inputRef.current!.focus({\n              preventScroll: true,\n            });\n          }}\n        >\n          Focus prevent scroll\n        </Button>\n        <Switch\n          checked={input}\n          checkedChildren=\"Input\"\n          unCheckedChildren=\"TextArea\"\n          onChange={() => {\n            setInput(!input);\n          }}\n        />\n      </Space>\n      <br />\n      {input ? <Input {...sharedProps} /> : <Input.TextArea {...sharedProps} />}\n    </Space>\n  );\n};\n\nexport default App;\n";;
  