  export default "// @ts-nocheck\nimport React, { useState } from 'react';\nimport { ClockCircleOutlined, DownOutlined } from '@ant-design/icons';\nimport {\n  Anchor,\n  Badge,\n  Button,\n  Calendar,\n  Card,\n  Collapse,\n  DatePicker,\n  Dropdown,\n  Modal,\n  Slider,\n  Switch,\n  Table,\n  Tabs,\n  Timeline,\n  Transfer,\n  Tree,\n  Typography,\n} from 'antd';\nimport dayjs from 'dayjs';\nimport customParseFormat from 'dayjs/plugin/customParseFormat';\nimport difference from 'lodash/difference';\n\ndayjs.extend(customParseFormat);\n\nconst { Panel } = Collapse;\nconst { TreeNode } = Tree;\nconst { TabPane } = Tabs;\nconst { Meta } = Card;\nconst { Link } = Anchor;\nconst { Text } = Typography;\n\nconst text = `\n  A dog is a type of domesticated animal.\n  Known for its loyalty and faithfulness,\n  it can be found as a welcome guest in many households across the world.\n`;\n\nconst mockData = [];\nfor (let i = 0; i < 20; i++) {\n  mockData.push({\n    key: i.toString(),\n    title: `content${i + 1}`,\n    description: `description of content${i + 1}`,\n    disabled: i % 3 < 1,\n  });\n}\n\nconst oriTargetKeys = mockData.filter((item) => +item.key % 3 > 1).map((item) => item.key);\n\nconst data = [\n  {\n    key: '1',\n    name: 'John Brown',\n    age: 32,\n    address: 'New York No. 1 Lake Park',\n  },\n  {\n    key: '2',\n    name: 'Jim Green',\n    age: 42,\n    address: 'London No. 1 Lake Park',\n  },\n  {\n    key: '3',\n    name: 'Joe Black',\n    age: 32,\n    address: 'Sydney No. 1 Lake Park',\n  },\n  {\n    key: '4',\n    name: 'Jim Red',\n    age: 32,\n    address: 'London No. 2 Lake Park',\n  },\n];\n\nconst columnsTable = [\n  {\n    title: 'Name',\n    dataIndex: 'name',\n  },\n  {\n    title: 'Borrow',\n    dataIndex: 'borrow',\n  },\n  {\n    title: 'Repayment',\n    dataIndex: 'repayment',\n  },\n];\n\nconst dataTable = [\n  {\n    key: '1',\n    name: 'John Brown',\n    borrow: 10,\n    repayment: 33,\n  },\n  {\n    key: '2',\n    name: 'Jim Green',\n    borrow: 100,\n    repayment: 0,\n  },\n  {\n    key: '3',\n    name: 'Joe Black',\n    borrow: 10,\n    repayment: 10,\n  },\n  {\n    key: '4',\n    name: 'Jim Red',\n    borrow: 75,\n    repayment: 45,\n  },\n];\n\nconst expandedRowRender = () => {\n  const columnsExpand = [\n    { title: 'Date', dataIndex: 'date', key: 'date' },\n    { title: 'Name', dataIndex: 'name', key: 'name' },\n    {\n      title: 'Status',\n      key: 'state',\n      render: () => (\n        <span>\n          <Badge status=\"success\" />\n          Finished\n        </span>\n      ),\n    },\n    { title: 'Upgrade Status', dataIndex: 'upgradeNum', key: 'upgradeNum' },\n    {\n      title: 'Action',\n      dataIndex: 'operation',\n      key: 'operation',\n      render: () => (\n        <span className=\"table-operation\">\n          <a>Pause</a>\n          <a>Stop</a>\n          <Dropdown>\n            <a>\n              More <DownOutlined />\n            </a>\n          </Dropdown>\n        </span>\n      ),\n    },\n  ];\n\n  const dataExpand = [];\n  for (let i = 0; i < 3; ++i) {\n    data.push({\n      key: i,\n      date: '2014-12-24 23:12:00',\n      name: 'This is production name',\n      upgradeNum: 'Upgraded: 56',\n    });\n  }\n  return <Table columns={columnsExpand} dataSource={dataExpand} pagination={false} />;\n};\n\nconst columnsNest = [\n  { title: 'Name', dataIndex: 'name', key: 'name' },\n  { title: 'Platform', dataIndex: 'platform', key: 'platform' },\n  { title: 'Version', dataIndex: 'version', key: 'version' },\n  { title: 'Upgraded', dataIndex: 'upgradeNum', key: 'upgradeNum' },\n  { title: 'Creator', dataIndex: 'creator', key: 'creator' },\n  { title: 'Date', dataIndex: 'createdAt', key: 'createdAt' },\n  { title: 'Action', key: 'operation', render: () => <a>Publish</a> },\n];\n\nconst dataNest = [];\nfor (let i = 0; i < 3; ++i) {\n  dataNest.push({\n    key: i,\n    name: 'Screem',\n    platform: 'iOS',\n    version: '10.3.4.5654',\n    upgradeNum: 500,\n    creator: 'Jack',\n    createdAt: '2014-12-24 23:12:00',\n  });\n}\n\nconst columnsFixed = [\n  {\n    title: 'Full Name',\n    width: 100,\n    dataIndex: 'name',\n    key: 'name',\n    fixed: 'left',\n  },\n  {\n    title: 'Age',\n    width: 100,\n    dataIndex: 'age',\n    key: 'age',\n    fixed: 'left',\n  },\n  { title: 'Column 1', dataIndex: 'address', key: '1' },\n  { title: 'Column 2', dataIndex: 'address', key: '2' },\n  { title: 'Column 3', dataIndex: 'address', key: '3' },\n  { title: 'Column 4', dataIndex: 'address', key: '4' },\n  { title: 'Column 5', dataIndex: 'address', key: '5' },\n  { title: 'Column 6', dataIndex: 'address', key: '6' },\n  { title: 'Column 7', dataIndex: 'address', key: '7' },\n  { title: 'Column 8', dataIndex: 'address', key: '8' },\n  {\n    title: 'Action',\n    key: 'operation',\n    fixed: 'right',\n    width: 100,\n    render: () => <a>action</a>,\n  },\n];\n\nconst dataFixed = [\n  {\n    key: '1',\n    name: 'John Brown',\n    age: 32,\n    address: 'New York Park',\n  },\n  {\n    key: '2',\n    name: 'Jim Green',\n    age: 40,\n    address: 'London Park',\n  },\n];\n\nconst TableTransfer = ({ leftColumns, rightColumns, ...restProps }) => (\n  <Transfer {...restProps} showSelectAll={false}>\n    {({\n      direction,\n      filteredItems,\n      onItemSelectAll,\n      onItemSelect,\n      selectedKeys: listSelectedKeys,\n      disabled: listDisabled,\n    }) => {\n      const columns = direction === 'left' ? leftColumns : rightColumns;\n\n      const rowSelection = {\n        getCheckboxProps: (item) => ({ disabled: listDisabled || item.disabled }),\n        onSelectAll(selected, selectedRows) {\n          const treeSelectedKeys = selectedRows\n            .filter((item) => !item.disabled)\n            .map(({ key }) => key);\n          const diffKeys = selected\n            ? difference(treeSelectedKeys, listSelectedKeys)\n            : difference(listSelectedKeys, treeSelectedKeys);\n          onItemSelectAll(diffKeys, selected);\n        },\n        onSelect({ key }, selected) {\n          onItemSelect(key, selected);\n        },\n        selectedRowKeys: listSelectedKeys,\n      };\n\n      return (\n        <Table\n          id=\"components-transfer-table\"\n          rowSelection={rowSelection}\n          columns={columns}\n          dataSource={filteredItems}\n          size=\"small\"\n          style={{ pointerEvents: listDisabled ? 'none' : null }}\n          onRow={({ key, disabled: itemDisabled }) => ({\n            onClick: () => {\n              if (itemDisabled || listDisabled) return;\n              onItemSelect(key, !listSelectedKeys.includes(key));\n            },\n          })}\n        />\n      );\n    }}\n  </Transfer>\n);\n\nexport default () => {\n  const [open, setOpen] = useState(false);\n  const [targetKeys, setTargetKeys] = useState(oriTargetKeys);\n  const [selectedKeys, setSelectedKeys] = useState([]);\n  const [disabled, setDisabled] = useState(false);\n  const [showSearch, setShowSearch] = useState(false);\n\n  const handleDisable = (isDisabled) => {\n    setDisabled(isDisabled);\n  };\n\n  const handleTableTransferChange = (nextTargetKeys) => {\n    setTargetKeys(nextTargetKeys);\n  };\n\n  const triggerDisable = (isDisabled) => {\n    setDisabled(isDisabled);\n  };\n\n  const triggerShowSearch = (isShowSearch) => {\n    setShowSearch(isShowSearch);\n  };\n\n  const handleTransferChange = (nextTargetKeys) => {\n    setTargetKeys(nextTargetKeys);\n  };\n\n  const handleTransferSelectChange = (sourceSelectedKeys, targetSelectedKeys) => {\n    setSelectedKeys([...sourceSelectedKeys, ...targetSelectedKeys]);\n  };\n\n  const showModal = () => {\n    setOpen(true);\n  };\n\n  const handleOk = (e) => {\n    console.log(e);\n    setOpen(false);\n  };\n\n  const handleCancel = (e) => {\n    console.log(e);\n    setOpen(false);\n  };\n\n  const columns = [\n    {\n      title: 'Name',\n      dataIndex: 'name',\n      key: 'name',\n      filters: [\n        { text: 'Joe', value: 'Joe' },\n        { text: 'Jim', value: 'Jim' },\n      ],\n      filteredValue: null,\n      onFilter: (value, record) => record.name.includes(value),\n      sorter: (a, b) => a.name.length - b.name.length,\n      sortOrder: true,\n      ellipsis: true,\n    },\n    {\n      title: 'Age',\n      dataIndex: 'age',\n      key: 'age',\n      sorter: false,\n      sortOrder: true,\n      ellipsis: true,\n    },\n    {\n      title: 'Address',\n      dataIndex: 'address',\n      key: 'address',\n      filters: [\n        { text: 'London', value: 'London' },\n        { text: 'New York', value: 'New York' },\n      ],\n      filteredValue: null,\n      onFilter: (value, record) => record.address.includes(value),\n      sorter: false,\n      sortOrder: true,\n      ellipsis: true,\n    },\n  ];\n  return (\n    <>\n      <Button type=\"primary\" onClick={showModal}>\n        Open Modal\n      </Button>\n      <Modal title=\"Basic Modal\" open={open} onOk={handleOk} onCancel={handleCancel}>\n        <Switch\n          unCheckedChildren=\"disabled\"\n          checkedChildren=\"disabled\"\n          checked={disabled}\n          onChange={handleDisable}\n          style={{ marginBottom: 16 }}\n        />\n        <Card title=\"Card Title\">\n          <Card.Grid>Content</Card.Grid>\n          <Card.Grid hoverable={false}>Content</Card.Grid>\n          <Card.Grid>Content</Card.Grid>\n          <Card.Grid>Content</Card.Grid>\n          <Card.Grid>Content</Card.Grid>\n          <Card.Grid>Content</Card.Grid>\n          <Card.Grid>Content</Card.Grid>\n        </Card>\n        <Collapse>\n          <Panel header=\"This is panel header 1\" key=\"1\">\n            <Collapse defaultActiveKey=\"1\">\n              <Panel header=\"This is panel nest panel\" key=\"1\">\n                <p>{text}</p>\n              </Panel>\n            </Collapse>\n          </Panel>\n          <Panel header=\"This is panel header 2\" key=\"2\">\n            <p>{text}</p>\n          </Panel>\n          <Panel header=\"This is panel header 3\" key=\"3\">\n            <p>{text}</p>\n          </Panel>\n        </Collapse>\n        <Transfer\n          dataSource={mockData}\n          titles={['Source', 'Target']}\n          targetKeys={targetKeys}\n          selectedKeys={selectedKeys}\n          onChange={handleTransferChange}\n          onSelectChange={handleTransferSelectChange}\n          render={(item) => item.title}\n          disabled={disabled}\n        />\n        <TableTransfer\n          dataSource={mockData}\n          targetKeys={targetKeys}\n          disabled={disabled}\n          showSearch={showSearch}\n          onChange={handleTableTransferChange}\n          filterOption={(inputValue, item) =>\n            item.title.indexOf(inputValue) !== -1 || item.tag?.indexOf(inputValue) !== -1\n          }\n          leftColumns={[\n            {\n              dataIndex: 'title',\n              title: 'Name',\n            },\n            {\n              dataIndex: 'description',\n              title: 'Description',\n            },\n          ]}\n          rightColumns={[\n            {\n              dataIndex: 'title',\n              title: 'Name',\n            },\n          ]}\n        />\n        <Switch\n          unCheckedChildren=\"disabled\"\n          checkedChildren=\"disabled\"\n          checked={disabled}\n          onChange={triggerDisable}\n          style={{ marginTop: 16 }}\n        />\n        <Switch\n          unCheckedChildren=\"showSearch\"\n          checkedChildren=\"showSearch\"\n          checked={showSearch}\n          onChange={triggerShowSearch}\n          style={{ marginTop: 16 }}\n        />\n        <Anchor>\n          <Link href=\"#anchor-demo-basic\" title=\"Basic demo\" />\n          <Link href=\"#anchor-demo-static\" title=\"Static demo\" />\n          <Link href=\"#anchor-demo-basic\" title=\"Basic demo with Target\" target=\"_blank\" />\n          <Link href=\"#API\" title=\"API\">\n            <Link href=\"#Anchor-Props\" title=\"Anchor Props\" />\n            <Link href=\"#Link-Props\" title=\"Link Props\" />\n          </Link>\n        </Anchor>\n        <Tabs type=\"card\">\n          <TabPane tab=\"Tab 1\" key=\"1\">\n            Content of Tab Pane 1\n          </TabPane>\n          <TabPane tab=\"Tab 2\" key=\"2\">\n            Content of Tab Pane 2\n          </TabPane>\n          <TabPane tab=\"Tab 3\" key=\"3\">\n            Content of Tab Pane 3\n          </TabPane>\n        </Tabs>\n        <Timeline>\n          <Timeline.Item>Create a services site 2015-09-01</Timeline.Item>\n          <Timeline.Item>Solve initial network problems 2015-09-01</Timeline.Item>\n          <Timeline.Item dot={<ClockCircleOutlined style={{ fontSize: '16px' }} />} color=\"red\">\n            Technical testing 2015-09-01\n          </Timeline.Item>\n          <Timeline.Item>Network problems being solved 2015-09-01</Timeline.Item>\n        </Timeline>\n        <Calendar />\n        <Tree showLine switcherIcon={<DownOutlined />} defaultExpandedKeys={['0-0-0']}>\n          <TreeNode title=\"parent 1\" key=\"0-0\">\n            <TreeNode title=\"parent 1-0\" key=\"0-0-0\">\n              <TreeNode title=\"leaf\" key=\"0-0-0-0\" />\n              <TreeNode title=\"leaf\" key=\"0-0-0-1\" />\n              <TreeNode title=\"leaf\" key=\"0-0-0-2\" />\n            </TreeNode>\n            <TreeNode title=\"parent 1-1\" key=\"0-0-1\">\n              <TreeNode title=\"leaf\" key=\"0-0-1-0\" />\n            </TreeNode>\n            <TreeNode title=\"parent 1-2\" key=\"0-0-2\">\n              <TreeNode title=\"leaf\" key=\"0-0-2-0\" />\n              <TreeNode title=\"leaf\" key=\"0-0-2-1\" />\n            </TreeNode>\n          </TreeNode>\n        </Tree>\n        <Table columns={columns} dataSource={data} footer={() => 'Footer'} />\n        <Table\n          columns={columnsTable}\n          dataSource={dataTable}\n          pagination={false}\n          id=\"table-demo-summary\"\n          bordered\n          summary={(pageData) => {\n            let totalBorrow = 0;\n            let totalRepayment = 0;\n\n            pageData.forEach(({ borrow, repayment }) => {\n              totalBorrow += borrow;\n              totalRepayment += repayment;\n            });\n\n            return (\n              <>\n                <tr>\n                  <th>Total</th>\n                  <td>\n                    <Text type=\"danger\">{totalBorrow}</Text>\n                  </td>\n                  <td>\n                    <Text>{totalRepayment}</Text>\n                  </td>\n                </tr>\n                <tr>\n                  <th>Balance</th>\n                  <td colSpan={2}>\n                    <Text type=\"danger\">{totalBorrow - totalRepayment}</Text>\n                  </td>\n                </tr>\n              </>\n            );\n          }}\n        />\n        <br />\n        <Table columns={columnsNest} expandable={{ expandedRowRender }} dataSource={dataNest} />\n        <Table columns={columnsFixed} dataSource={dataFixed} scroll={{ x: 1300, y: 100 }} />\n        <Card\n          hoverable\n          style={{ width: 240 }}\n          cover={\n            <img alt=\"example\" src=\"https://os.alipayobjects.com/rmsportal/QBnOOoLaAfKPirc.png\" />\n          }\n        >\n          <Meta title=\"Europe Street beat\" description=\"www.instagram.com\" />\n        </Card>\n        <Slider defaultValue={30} />\n        <DatePicker defaultValue={dayjs('2015/01/01', 'YYYY/MM/DD')} format=\"YYYY/MM/DD\" />\n        <Badge count={5}>\n          <a href=\"#\" className=\"head-example\" />\n        </Badge>\n      </Modal>\n    </>\n  );\n};\n";;
  