
  import '/Users/vagusx/Projects/ant-design/components/table/demo/dynamic-settings.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { DownOutlined } from '@ant-design/icons';\nimport type { GetProp, RadioChangeEvent, TableProps } from 'antd';\nimport { Form, Radio, Space, Switch, Table } from 'antd';\n\ntype SizeType = TableProps['size'];\ntype ColumnsType<T extends object> = GetProp<TableProps<T>, 'columns'>;\ntype TablePagination<T extends object> = NonNullable<Exclude<TableProps<T>['pagination'], boolean>>;\ntype TablePaginationPosition = NonNullable<TablePagination<any>['position']>[number];\ntype ExpandableConfig<T extends object> = TableProps<T>['expandable'];\ntype TableRowSelection<T extends object> = TableProps<T>['rowSelection'];\n\ninterface DataType {\n  key: number;\n  name: string;\n  age: number;\n  address: string;\n  description: string;\n}\n\nconst columns: ColumnsType<DataType> = [\n  {\n    title: 'Name',\n    dataIndex: 'name',\n  },\n  {\n    title: 'Age',\n    dataIndex: 'age',\n    sorter: (a, b) => a.age - b.age,\n  },\n  {\n    title: 'Address',\n    dataIndex: 'address',\n    filters: [\n      {\n        text: 'London',\n        value: 'London',\n      },\n      {\n        text: 'New York',\n        value: 'New York',\n      },\n    ],\n    onFilter: (value, record) => record.address.indexOf(value as string) === 0,\n  },\n  {\n    title: 'Action',\n    key: 'action',\n    sorter: true,\n    render: () => (\n      <Space size=\"middle\">\n        <a>Delete</a>\n        <a>\n          <Space>\n            More actions\n            <DownOutlined />\n          </Space>\n        </a>\n      </Space>\n    ),\n  },\n];\n\nconst data: DataType[] = [];\nfor (let i = 1; i <= 10; i++) {\n  data.push({\n    key: i,\n    name: 'John Brown',\n    age: Number(`${i}2`),\n    address: `New York No. ${i} Lake Park`,\n    description: `My name is John Brown, I am ${i}2 years old, living in New York No. ${i} Lake Park.`,\n  });\n}\n\nconst defaultExpandable = { expandedRowRender: (record: DataType) => <p>{record.description}</p> };\nconst defaultTitle = () => 'Here is title';\nconst defaultFooter = () => 'Here is footer';\n\nconst App: React.FC = () => {\n  const [bordered, setBordered] = useState(false);\n  const [loading, setLoading] = useState(false);\n  const [size, setSize] = useState<SizeType>('large');\n  const [expandable, setExpandable] = useState<ExpandableConfig<DataType> | undefined>(\n    defaultExpandable,\n  );\n  const [showTitle, setShowTitle] = useState(false);\n  const [showHeader, setShowHeader] = useState(true);\n  const [showFooter, setShowFooter] = useState(true);\n  const [rowSelection, setRowSelection] = useState<TableRowSelection<DataType> | undefined>({});\n  const [hasData, setHasData] = useState(true);\n  const [tableLayout, setTableLayout] = useState();\n  const [top, setTop] = useState<TablePaginationPosition>('none');\n  const [bottom, setBottom] = useState<TablePaginationPosition>('bottomRight');\n  const [ellipsis, setEllipsis] = useState(false);\n  const [yScroll, setYScroll] = useState(false);\n  const [xScroll, setXScroll] = useState<string>();\n\n  const handleBorderChange = (enable: boolean) => {\n    setBordered(enable);\n  };\n\n  const handleLoadingChange = (enable: boolean) => {\n    setLoading(enable);\n  };\n\n  const handleSizeChange = (e: RadioChangeEvent) => {\n    setSize(e.target.value);\n  };\n\n  const handleTableLayoutChange = (e: RadioChangeEvent) => {\n    setTableLayout(e.target.value);\n  };\n\n  const handleExpandChange = (enable: boolean) => {\n    setExpandable(enable ? defaultExpandable : undefined);\n  };\n\n  const handleEllipsisChange = (enable: boolean) => {\n    setEllipsis(enable);\n  };\n\n  const handleTitleChange = (enable: boolean) => {\n    setShowTitle(enable);\n  };\n\n  const handleHeaderChange = (enable: boolean) => {\n    setShowHeader(enable);\n  };\n\n  const handleFooterChange = (enable: boolean) => {\n    setShowFooter(enable);\n  };\n\n  const handleRowSelectionChange = (enable: boolean) => {\n    setRowSelection(enable ? {} : undefined);\n  };\n\n  const handleYScrollChange = (enable: boolean) => {\n    setYScroll(enable);\n  };\n\n  const handleXScrollChange = (e: RadioChangeEvent) => {\n    setXScroll(e.target.value);\n  };\n\n  const handleDataChange = (newHasData: boolean) => {\n    setHasData(newHasData);\n  };\n\n  const scroll: { x?: number | string; y?: number | string } = {};\n  if (yScroll) {\n    scroll.y = 240;\n  }\n  if (xScroll) {\n    scroll.x = '100vw';\n  }\n\n  const tableColumns = columns.map((item) => ({ ...item, ellipsis }));\n  if (xScroll === 'fixed') {\n    tableColumns[0].fixed = true;\n    tableColumns[tableColumns.length - 1].fixed = 'right';\n  }\n\n  const tableProps: TableProps<DataType> = {\n    bordered,\n    loading,\n    size,\n    expandable,\n    title: showTitle ? defaultTitle : undefined,\n    showHeader,\n    footer: showFooter ? defaultFooter : undefined,\n    rowSelection,\n    scroll,\n    tableLayout,\n  };\n\n  return (\n    <>\n      <Form layout=\"inline\" className=\"table-demo-control-bar\" style={{ marginBottom: 16 }}>\n        <Form.Item label=\"Bordered\">\n          <Switch checked={bordered} onChange={handleBorderChange} />\n        </Form.Item>\n        <Form.Item label=\"loading\">\n          <Switch checked={loading} onChange={handleLoadingChange} />\n        </Form.Item>\n        <Form.Item label=\"Title\">\n          <Switch checked={showTitle} onChange={handleTitleChange} />\n        </Form.Item>\n        <Form.Item label=\"Column Header\">\n          <Switch checked={showHeader} onChange={handleHeaderChange} />\n        </Form.Item>\n        <Form.Item label=\"Footer\">\n          <Switch checked={showFooter} onChange={handleFooterChange} />\n        </Form.Item>\n        <Form.Item label=\"Expandable\">\n          <Switch checked={!!expandable} onChange={handleExpandChange} />\n        </Form.Item>\n        <Form.Item label=\"Checkbox\">\n          <Switch checked={!!rowSelection} onChange={handleRowSelectionChange} />\n        </Form.Item>\n        <Form.Item label=\"Fixed Header\">\n          <Switch checked={!!yScroll} onChange={handleYScrollChange} />\n        </Form.Item>\n        <Form.Item label=\"Has Data\">\n          <Switch checked={!!hasData} onChange={handleDataChange} />\n        </Form.Item>\n        <Form.Item label=\"Ellipsis\">\n          <Switch checked={!!ellipsis} onChange={handleEllipsisChange} />\n        </Form.Item>\n        <Form.Item label=\"Size\">\n          <Radio.Group value={size} onChange={handleSizeChange}>\n            <Radio.Button value=\"large\">Large</Radio.Button>\n            <Radio.Button value=\"middle\">Middle</Radio.Button>\n            <Radio.Button value=\"small\">Small</Radio.Button>\n          </Radio.Group>\n        </Form.Item>\n        <Form.Item label=\"Table Scroll\">\n          <Radio.Group value={xScroll} onChange={handleXScrollChange}>\n            <Radio.Button value={undefined}>Unset</Radio.Button>\n            <Radio.Button value=\"scroll\">Scroll</Radio.Button>\n            <Radio.Button value=\"fixed\">Fixed Columns</Radio.Button>\n          </Radio.Group>\n        </Form.Item>\n        <Form.Item label=\"Table Layout\">\n          <Radio.Group value={tableLayout} onChange={handleTableLayoutChange}>\n            <Radio.Button value={undefined}>Unset</Radio.Button>\n            <Radio.Button value=\"fixed\">Fixed</Radio.Button>\n          </Radio.Group>\n        </Form.Item>\n        <Form.Item label=\"Pagination Top\">\n          <Radio.Group\n            value={top}\n            onChange={(e) => {\n              setTop(e.target.value);\n            }}\n          >\n            <Radio.Button value=\"topLeft\">TopLeft</Radio.Button>\n            <Radio.Button value=\"topCenter\">TopCenter</Radio.Button>\n            <Radio.Button value=\"topRight\">TopRight</Radio.Button>\n            <Radio.Button value=\"none\">None</Radio.Button>\n          </Radio.Group>\n        </Form.Item>\n        <Form.Item label=\"Pagination Bottom\">\n          <Radio.Group\n            value={bottom}\n            onChange={(e) => {\n              setBottom(e.target.value);\n            }}\n          >\n            <Radio.Button value=\"bottomLeft\">BottomLeft</Radio.Button>\n            <Radio.Button value=\"bottomCenter\">BottomCenter</Radio.Button>\n            <Radio.Button value=\"bottomRight\">BottomRight</Radio.Button>\n            <Radio.Button value=\"none\">None</Radio.Button>\n          </Radio.Group>\n        </Form.Item>\n      </Form>\n      <Table\n        {...tableProps}\n        pagination={{ position: [top, bottom] }}\n        columns={tableColumns}\n        dataSource={hasData ? data : []}\n        scroll={scroll}\n      />\n    </>\n  );\n};\n\nexport default App;\n";;
  