  export default "import React from 'react';\nimport { Cascader, Select, Space, TreeSelect, Typography } from 'antd';\n\nconst options = [\n  { value: 'long', label: <Typography>long, long, long piece of text</Typography> },\n  { value: 'short', label: <Typography>short</Typography> },\n  { value: 'normal', label: <div>normal</div> },\n];\n\nconst App: React.FC = () => (\n  <Space wrap>\n    <Select\n      defaultValue=\"long, long, long piece of text\"\n      style={{ width: 120 }}\n      allowClear\n      options={options}\n    />\n\n    <Select\n      placeholder=\"Select a option\"\n      style={{ width: 120, height: 60 }}\n      allowClear\n      options={options}\n    />\n\n    <Select\n      defaultValue=\"normal\"\n      placeholder=\"Select a option\"\n      style={{ width: 120 }}\n      allowClear\n      options={options}\n    />\n\n    <Select\n      defaultValue={['normal']}\n      mode=\"multiple\"\n      placeholder=\"Select a option\"\n      style={{ width: 120 }}\n      allowClear\n      options={options}\n    />\n\n    <Select\n      mode=\"multiple\"\n      placeholder=\"Select a option\"\n      style={{ width: 120, height: 60 }}\n      allowClear\n      options={options}\n    />\n\n    <Cascader\n      placeholder=\"Select a option\"\n      style={{ width: 120, height: 60 }}\n      allowClear\n      options={options}\n    />\n\n    <TreeSelect\n      showSearch\n      style={{ width: 120, height: 60 }}\n      placeholder=\"Please select\"\n      allowClear\n      popupMatchSelectWidth={false}\n      treeDefaultExpandAll\n      treeData={[\n        {\n          value: 'parent 1',\n          title: 'parent 1',\n          children: options,\n        },\n      ]}\n    />\n  </Space>\n);\n\nexport default App;\n";;
  