  export default "import React from 'react';\nimport { Table } from 'antd';\nimport type { TableProps } from 'antd';\n\ninterface DataType {\n  key: string;\n  name: string;\n  money: string;\n  address: string;\n}\n\nconst columns: TableProps<DataType>['columns'] = [\n  {\n    title: 'Name',\n    dataIndex: 'name',\n    render: (text) => <a>{text}</a>,\n  },\n  {\n    title: 'Cash Assets',\n    className: 'column-money',\n    dataIndex: 'money',\n    align: 'right',\n  },\n  {\n    title: 'Address',\n    dataIndex: 'address',\n  },\n];\n\nconst data: DataType[] = [\n  {\n    key: '1',\n    name: 'John Brown',\n    money: '300,000.00',\n    address: 'New York No. 1 Lake Park',\n  },\n  {\n    key: '2',\n    name: 'Jim Green',\n    money: '1,256,000.00',\n    address: 'London No. 1 Lake Park',\n  },\n  {\n    key: '3',\n    name: 'Joe Black',\n    money: '120,000.00',\n    address: 'Sydney No. 1 Lake Park',\n  },\n];\n\nconst App: React.FC = () => (\n  <Table\n    columns={columns}\n    dataSource={data}\n    bordered\n    title={() => 'Header'}\n    footer={() => 'Footer'}\n  />\n);\n\nexport default App;\n";;
  