
  import '/Users/vagusx/Projects/ant-design/components/form/demo/form-dependencies.tsx?watch=parent';
  export default "import React from 'react';\nimport { Alert, Form, Input, Typography } from 'antd';\n\nconst App: React.FC = () => {\n  const [form] = Form.useForm();\n  return (\n    <Form\n      form={form}\n      name=\"dependencies\"\n      autoComplete=\"off\"\n      style={{ maxWidth: 600 }}\n      layout=\"vertical\"\n    >\n      <Alert message=\" Try modify `Password2` and then modify `Password`\" type=\"info\" showIcon />\n\n      <Form.Item label=\"Password\" name=\"password\" rules={[{ required: true }]}>\n        <Input />\n      </Form.Item>\n\n      {/* Field */}\n      <Form.Item\n        label=\"Confirm Password\"\n        name=\"password2\"\n        dependencies={['password']}\n        rules={[\n          {\n            required: true,\n          },\n          ({ getFieldValue }) => ({\n            validator(_, value) {\n              if (!value || getFieldValue('password') === value) {\n                return Promise.resolve();\n              }\n              return Promise.reject(new Error('The new password that you entered do not match!'));\n            },\n          }),\n        ]}\n      >\n        <Input />\n      </Form.Item>\n\n      {/* Render Props */}\n      <Form.Item noStyle dependencies={['password2']}>\n        {() => (\n          <Typography>\n            <p>\n              Only Update when <code>password2</code> updated:\n            </p>\n            <pre>{JSON.stringify(form.getFieldsValue(), null, 2)}</pre>\n          </Typography>\n        )}\n      </Form.Item>\n    </Form>\n  );\n};\n\nexport default App;\n";;
  