  export default "import React from 'react';\nimport { Table } from 'antd';\nimport type { TableProps } from 'antd';\n\ninterface DataType {\n  key: string;\n  name: string;\n  age: number;\n  tel: string;\n  phone: number;\n  address: string;\n}\n\n// In the fifth row, other columns are merged into first column\n// by setting it's colSpan to be 0\nconst sharedOnCell = (_: DataType, index?: number) => {\n  if (index === 1) {\n    return { colSpan: 0 };\n  }\n\n  return {};\n};\n\nconst columns: TableProps<DataType>['columns'] = [\n  {\n    title: 'RowHead',\n    dataIndex: 'key',\n    rowScope: 'row',\n  },\n  {\n    title: 'Name',\n    dataIndex: 'name',\n    render: (text) => <a>{text}</a>,\n    onCell: (_, index) => ({\n      colSpan: index === 1 ? 5 : 1,\n    }),\n  },\n  {\n    title: 'Age',\n    dataIndex: 'age',\n    onCell: sharedOnCell,\n  },\n  {\n    title: 'Home phone',\n    colSpan: 2,\n    dataIndex: 'tel',\n    onCell: (_, index) => {\n      if (index === 3) {\n        return { rowSpan: 2 };\n      }\n      // These two are merged into above cell\n      if (index === 4) {\n        return { rowSpan: 0 };\n      }\n      if (index === 1) {\n        return { colSpan: 0 };\n      }\n\n      return {};\n    },\n  },\n  {\n    title: 'Phone',\n    colSpan: 0,\n    dataIndex: 'phone',\n    onCell: sharedOnCell,\n  },\n  {\n    title: 'Address',\n    dataIndex: 'address',\n    onCell: sharedOnCell,\n  },\n];\n\nconst data: DataType[] = [\n  {\n    key: '1',\n    name: 'John Brown',\n    age: 32,\n    tel: '0571-22098909',\n    phone: 18889898989,\n    address: 'New York No. 1 Lake Park',\n  },\n  {\n    key: '2',\n    name: 'Jim Green',\n    tel: '0571-22098333',\n    phone: 18889898888,\n    age: 42,\n    address: 'London No. 1 Lake Park',\n  },\n  {\n    key: '3',\n    name: 'Joe Black',\n    age: 32,\n    tel: '0575-22098909',\n    phone: 18900010002,\n    address: 'Sydney No. 1 Lake Park',\n  },\n  {\n    key: '4',\n    name: 'Jim Red',\n    age: 18,\n    tel: '0575-22098909',\n    phone: 18900010002,\n    address: 'London No. 2 Lake Park',\n  },\n  {\n    key: '5',\n    name: 'Jake White',\n    age: 18,\n    tel: '0575-22098909',\n    phone: 18900010002,\n    address: 'Dublin No. 2 Lake Park',\n  },\n];\n\nconst App: React.FC = () => <Table columns={columns} dataSource={data} bordered />;\n\nexport default App;\n";;
  