
  import '/Users/vagusx/Projects/ant-design/components/input-number/demo/keyboard.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { Checkbox, InputNumber, Space } from 'antd';\n\nconst App: React.FC = () => {\n  const [keyboard, setKeyboard] = useState(true);\n\n  return (\n    <Space>\n      <InputNumber min={1} max={10} keyboard={keyboard} defaultValue={3} />\n      <Checkbox\n        onChange={() => {\n          setKeyboard(!keyboard);\n        }}\n        checked={keyboard}\n      >\n        Toggle keyboard\n      </Checkbox>\n    </Space>\n  );\n};\n\nexport default App;\n";;
  