
  import '/Users/vagusx/Projects/ant-design/components/auto-complete/demo/basic.tsx?watch=parent';
  export default "import React, { useState } from 'react';\nimport { AutoComplete } from 'antd';\nimport type { AutoCompleteProps } from 'antd';\n\nconst mockVal = (str: string, repeat = 1) => ({\n  value: str.repeat(repeat),\n});\n\nconst App: React.FC = () => {\n  const [value, setValue] = useState('');\n  const [options, setOptions] = useState<AutoCompleteProps['options']>([]);\n  const [anotherOptions, setAnotherOptions] = useState<AutoCompleteProps['options']>([]);\n\n  const getPanelValue = (searchText: string) =>\n    !searchText ? [] : [mockVal(searchText), mockVal(searchText, 2), mockVal(searchText, 3)];\n\n  const onSelect = (data: string) => {\n    console.log('onSelect', data);\n  };\n\n  const onChange = (data: string) => {\n    setValue(data);\n  };\n\n  return (\n    <>\n      <AutoComplete\n        options={options}\n        style={{ width: 200 }}\n        onSelect={onSelect}\n        onSearch={(text) => setOptions(getPanelValue(text))}\n        placeholder=\"input here\"\n      />\n      <br />\n      <br />\n      <AutoComplete\n        value={value}\n        options={anotherOptions}\n        style={{ width: 200 }}\n        onSelect={onSelect}\n        onSearch={(text) => setAnotherOptions(getPanelValue(text))}\n        onChange={onChange}\n        placeholder=\"control mode\"\n      />\n    </>\n  );\n};\n\nexport default App;\n";;
  