
  import '/Users/vagusx/Projects/ant-design/components/form/demo/useWatch.tsx?watch=parent';
  export default "import React from 'react';\nimport { Form, Input, InputNumber, Typography } from 'antd';\n\nconst Demo: React.FC = () => {\n  const [form] = Form.useForm<{ name: string; age: number }>();\n  const nameValue = Form.useWatch('name', form);\n  // The selector is static and does not support closures.\n  const customValue = Form.useWatch((values) => `name: ${values.name || ''}`, form);\n\n  return (\n    <>\n      <Form form={form} layout=\"vertical\" autoComplete=\"off\">\n        <Form.Item name=\"name\" label=\"Name (Watch to trigger rerender)\">\n          <Input />\n        </Form.Item>\n        <Form.Item name=\"age\" label=\"Age (Not Watch)\">\n          <InputNumber />\n        </Form.Item>\n      </Form>\n\n      <Typography>\n        <pre>Name Value: {nameValue}</pre>\n        <pre>Custom Value: {customValue}</pre>\n      </Typography>\n    </>\n  );\n};\n\nexport default Demo;\n";;
  