  export default "import React from 'react';\nimport type { CascaderProps } from 'antd';\nimport { Cascader } from 'antd';\n\nconst { SHOW_CHILD } = Cascader;\n\ninterface Option {\n  value: string | number;\n  label: string;\n  children?: Option[];\n}\nconst options: Option[] = [\n  {\n    label: 'Light',\n    value: 'light',\n    children: new Array(20)\n      .fill(null)\n      .map((_, index) => ({ label: `Number ${index}`, value: index })),\n  },\n  {\n    label: 'Bamboo',\n    value: 'bamboo',\n    children: [\n      {\n        label: 'Little',\n        value: 'little',\n        children: [\n          {\n            label: 'Toy Fish',\n            value: 'fish',\n          },\n          {\n            label: 'Toy Cards',\n            value: 'cards',\n          },\n          {\n            label: 'Toy Bird',\n            value: 'bird',\n          },\n        ],\n      },\n    ],\n  },\n];\n\nconst App: React.FC = () => {\n  const onChange: CascaderProps<Option, 'value', true>['onChange'] = (value) => {\n    console.log(value);\n  };\n  return (\n    <>\n      <Cascader\n        style={{ width: '100%' }}\n        options={options}\n        onChange={onChange}\n        multiple\n        maxTagCount=\"responsive\"\n        showCheckedStrategy={SHOW_CHILD}\n        defaultValue={[\n          ['bamboo', 'little', 'fish'],\n          ['bamboo', 'little', 'cards'],\n          ['bamboo', 'little', 'bird'],\n        ]}\n      />\n      <br />\n      <br />\n      <Cascader\n        style={{ width: '100%' }}\n        options={options}\n        onChange={onChange}\n        multiple\n        maxTagCount=\"responsive\"\n        defaultValue={[['bamboo']]}\n      />\n    </>\n  );\n};\n\nexport default App;\n";;
  