  export default "import React from 'react';\nimport { SmileOutlined } from '@ant-design/icons';\nimport {\n  Cascader,\n  DatePicker,\n  Form,\n  Input,\n  InputNumber,\n  Mentions,\n  Select,\n  TimePicker,\n  TreeSelect,\n} from 'antd';\n\nconst { Option } = Select;\n\nconst formItemLayout = {\n  labelCol: {\n    xs: { span: 24 },\n    sm: { span: 6 },\n  },\n  wrapperCol: {\n    xs: { span: 24 },\n    sm: { span: 14 },\n  },\n};\n\nconst App: React.FC = () => (\n  <Form {...formItemLayout} style={{ maxWidth: 600 }}>\n    <Form.Item\n      label=\"Fail\"\n      validateStatus=\"error\"\n      help=\"Should be combination of numbers & alphabets\"\n    >\n      <Input placeholder=\"unavailable choice\" id=\"error\" />\n    </Form.Item>\n\n    <Form.Item label=\"Warning\" validateStatus=\"warning\">\n      <Input placeholder=\"Warning\" id=\"warning\" prefix={<SmileOutlined />} />\n    </Form.Item>\n\n    <Form.Item\n      label=\"Validating\"\n      hasFeedback\n      validateStatus=\"validating\"\n      help=\"The information is being validated...\"\n    >\n      <Input placeholder=\"I'm the content is being validated\" id=\"validating\" />\n    </Form.Item>\n\n    <Form.Item label=\"Success\" hasFeedback validateStatus=\"success\">\n      <Input placeholder=\"I'm the content\" id=\"success\" />\n    </Form.Item>\n\n    <Form.Item label=\"Warning\" hasFeedback validateStatus=\"warning\">\n      <Input placeholder=\"Warning\" id=\"warning2\" />\n    </Form.Item>\n\n    <Form.Item\n      label=\"Fail\"\n      hasFeedback\n      validateStatus=\"error\"\n      help=\"Should be combination of numbers & alphabets\"\n    >\n      <Input placeholder=\"unavailable choice\" id=\"error2\" />\n    </Form.Item>\n\n    <Form.Item label=\"Success\" hasFeedback validateStatus=\"success\">\n      <DatePicker style={{ width: '100%' }} />\n    </Form.Item>\n\n    <Form.Item label=\"Warning\" hasFeedback validateStatus=\"warning\">\n      <TimePicker style={{ width: '100%' }} />\n    </Form.Item>\n\n    <Form.Item label=\"Error\" hasFeedback validateStatus=\"error\">\n      <DatePicker.RangePicker style={{ width: '100%' }} />\n    </Form.Item>\n\n    <Form.Item label=\"Error\" hasFeedback validateStatus=\"error\">\n      <Select placeholder=\"I'm Select\" allowClear>\n        <Option value=\"1\">Option 1</Option>\n        <Option value=\"2\">Option 2</Option>\n        <Option value=\"3\">Option 3</Option>\n      </Select>\n    </Form.Item>\n\n    <Form.Item\n      label=\"Validating\"\n      hasFeedback\n      validateStatus=\"error\"\n      help=\"Something breaks the rule.\"\n    >\n      <Cascader placeholder=\"I'm Cascader\" options={[{ value: 'xx', label: 'xx' }]} allowClear />\n    </Form.Item>\n\n    <Form.Item label=\"Warning\" hasFeedback validateStatus=\"warning\" help=\"Need to be checked\">\n      <TreeSelect\n        placeholder=\"I'm TreeSelect\"\n        treeData={[{ value: 'xx', label: 'xx' }]}\n        allowClear\n      />\n    </Form.Item>\n\n    <Form.Item label=\"inline\" style={{ marginBottom: 0 }}>\n      <Form.Item\n        validateStatus=\"error\"\n        help=\"Please select right date\"\n        style={{ display: 'inline-block', width: 'calc(50% - 12px)' }}\n      >\n        <DatePicker />\n      </Form.Item>\n      <span\n        style={{ display: 'inline-block', width: '24px', lineHeight: '32px', textAlign: 'center' }}\n      >\n        -\n      </span>\n      <Form.Item style={{ display: 'inline-block', width: 'calc(50% - 12px)' }}>\n        <DatePicker />\n      </Form.Item>\n    </Form.Item>\n\n    <Form.Item label=\"Success\" hasFeedback validateStatus=\"success\">\n      <InputNumber style={{ width: '100%' }} />\n    </Form.Item>\n\n    <Form.Item label=\"Success\" hasFeedback validateStatus=\"success\">\n      <Input allowClear placeholder=\"with allowClear\" />\n    </Form.Item>\n\n    <Form.Item label=\"Warning\" hasFeedback validateStatus=\"warning\">\n      <Input.Password placeholder=\"with input password\" />\n    </Form.Item>\n\n    <Form.Item label=\"Error\" hasFeedback validateStatus=\"error\">\n      <Input.Password allowClear placeholder=\"with input password and allowClear\" />\n    </Form.Item>\n\n    <Form.Item label=\"Success\" hasFeedback validateStatus=\"success\">\n      <Input.OTP />\n    </Form.Item>\n    <Form.Item label=\"Warning\" hasFeedback validateStatus=\"warning\">\n      <Input.OTP />\n    </Form.Item>\n\n    <Form.Item label=\"Error\" hasFeedback validateStatus=\"error\">\n      <Input.OTP />\n    </Form.Item>\n\n    <Form.Item label=\"Fail\" validateStatus=\"error\" hasFeedback>\n      <Mentions />\n    </Form.Item>\n\n    <Form.Item label=\"Fail\" validateStatus=\"error\" hasFeedback help=\"Should have something\">\n      <Input.TextArea allowClear showCount />\n    </Form.Item>\n  </Form>\n);\n\nexport default App;\n";;
  